<template>
  <div class="wrapper">
    <div class="mid">
      <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
        <van-list v-model="loading" :finished="finished" :finished-text="list.length?'没有更多了':''" @load="getdata">
          <div class="no" v-if="list.length == 0">
            <img src="./../../assets/images/icon_gwcwsp.svg" alt="">
            <div>暂无数据</div>
          </div>
          <div class="content" v-for="(item,index) in list" :key="index">
            <van-row class="box" @click.native="godetail(item.is_putaway,item.id)">
              <van-row class="goodlist" type="flex">
                <van-row class="img">
                  <img :src="item.logo_image" alt="">
                  <van-row class="" v-if="item.is_putaway == 1">已失效</van-row>
                </van-row>
                <van-row class="goodinfo">
                  <van-row class="name more-h">{{item.name}}</van-row>
                  <van-row type="flex" justify="space-between">
                    <van-col>
                      <span>￥{{item.goods_price}}</span>
                    </van-col>
                    <van-col @click.native.stop="del(index)"><img src="./../../assets/images/icon_delete.svg" alt="">
                    </van-col>
                  </van-row>
                </van-row>
              </van-row>
            </van-row>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        page: 1,
        list: "",
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      /**
       * 去详情
       * @param index
       */
      godetail(is_putaway, id) {
        if (is_putaway == 1) {
          return
        }
        this.$router.push({path: 'listdetail', query: {goods_id: id}});
      },
      del(index) {
        this.$post('/api/index/goods_collected', {id: this.userInfo.id, goods_id: this.list[index].id}).then(res => {
          console.log(res);
          this.$toast(res.msg);
          if (res.code == 3) {
            this.list.splice(index, 1);
          }
        })
      },
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.list = "";
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/my/my_collect', {id: this.userInfo.id, page: this.page, status: this.active}).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = res.data.data;
            } else {
              this.list = this.list.concat(res.data.data);
            }
            if (res.data.total_row == this.list.length || res.data.data.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    created() {
    },
    mounted() {
      document.title = '收藏';
      // this.getdata()
    }
  }
</script>
<style scoped lang='stylus'>
  .mid >>> .van-pull-refresh__track {
    min-height: 100vh;
  }

  .mid >>> .van-list__finished-text {
    background: #FFFFFF;
  }

  .mid >>> .no {
    margin: 0 auto 0;
    padding-top: 2rem;
    text-align: center;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
  }

  .mid {
    box-sizing: border-box;
    .content {
      background: #FFFFFF;
      padding: 0.28rem 0.28rem 0;
    }
    .box {
      box-sizing: border-box;
      .goodlist {
        margin-bottom: 0.28rem;
        .img {
          position: relative;
          img {
            width: 2.28rem;
            height: 2.28rem;
            border-radius: 0.12rem;
          }
          .van-row {
            width: 100%;
            text-align: center;
            line-height: 0.54rem;
            height: 0.54rem;
            position: absolute;
            border-radius: 0 0 0.12rem 0.12rem;
            bottom: 0;
            opacity: 0.7;
            font-size: 0.24rem;
            color: #BBBBBB;
            background-image: linear-gradient(-180deg, #000000 0%, #000000 100%);
          }
        }
        .goodinfo {
          flex: 1;
          margin-left: 0.24rem;
          font-size: 0.24rem;
          color: #999999;
          line-height: 0.34rem;
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          .name {
            color: #333333;
            font-size: 0.28rem;
          }
          img {
            height: 0.32rem;
            width: 0.32rem;
          }
          span {
            color: #EF3342;
            font-size: 0.32rem;
          }
        }
        .goodinfo:after {
          display: none;
        }
      }
    }
  }
</style>


