<template>
  <div class="wrapper">
    <van-cell-group class="content">
      <van-field clearable label="收货人姓名" placeholder="请输入姓名" v-model="name"/>
      <van-field type="number"  label="手机号"  placeholder="请输入手机号" v-model="telephone" maxlength="11"/>
      <van-cell title="选择地区" is-link :value="province?province+' '+city+' '+area:'请选择地区'" @click="show =true"/>
      <van-field type="textarea" placeholder="详细地址" rows="5" class="textarea" v-model="detail"/>
    </van-cell-group>
    <van-row class="select" @click.native="is_default = !is_default">
      <img :src="is_default?require('./../../assets/images/icon_xuanzhong.svg'):require('./../../assets/images/icon_weixuan.svg')" alt="">默认地址
    </van-row>
    <van-row class="bottom">
      <van-button type="danger" round class="btn" @click.native="save">保存</van-button>
    </van-row>
    <!--三级联动-->
    <van-popup v-model="show" position="bottom">
      <van-area :area-list="areaList" @confirm="confirm" @cancel="show=false"/>
    </van-popup>
  </div>
</template>

<script>
  import areaList from './../../assets/js/area'
  import {checkempty} from  './../../assets/js/tool'
  import {checkphone} from  './../../assets/js/tool'
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        s:0,
        show:false,
        areaList:areaList,
        id:0,//修改地址就会有id;
        name:"",
        telephone:"",
        is_default:0,
        province:"",
        city:"",
        area:"",
        detail:"",

      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      confirm(e){
        this. province=e[0].name;
        this. city=e[1].name;
        this. area=e[2].name;
        this.show=false;
      },
      save(){
        const that = this;
        const arr = [
          {val: that.name, toast: "请输入名字"},
          {val: that.telephone},
          {val: that.province, toast: "请选择地区"},
          {val: that.detail, toast: "请输入详细地址"},
        ];
        let result = true;
        for (var index = 0; index < arr.length; index++) {
          if (index === 1 && !checkphone(arr[index].val)) {
            result = false;
            break
          } else if (!checkempty(arr[index].val, arr[index].toast)) {
            result = false;
            break
          }
        }
        if (!result) {
          return
        }

        let data = {
          user_id:this.userInfo.id,
          id: that.id,
          name :that.name,
          telephone :that.telephone,
          province :that.province,
          city :that.city,
          area :that.area,
          detail :that.detail,
          is_default :that.is_default?1:0,
        };
        this.$fetch('/api/address/add_address',data).then(res=>{
          console.log(res);
          that.$toast(res.msg);
          if(res.code == 1 ||res.code == 3){
            that.$router.go(-1);
          }
        })
      }
    },
    created() {
      console.log(this.$route.query);
      if(!this.$route.query.id){
        document.title='新增地址';
      }else{
        let query = this.$route.query;
        document.title='修改地址';
        this.id = query.id;
        this.name = query.name;
        this.telephone = query.telephone;
        this.province = query.province;
        this.city = query.city;
        this.area = query.area;
        this.detail = query.detail;
        this.is_default = query.is_default;
      }
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .content >>>  .van-field .van-cell__title{
    max-width: 1.8rem;
  }
  .content >>>  .van-icon, .van-icon::before{
    font-size: 0.32rem;
    line-height: 0.56rem;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
    overflow: hidden;
  }
  .content{
    .van-cell{
      padding: 0.2rem 0.3rem;
      font-size: 0.28rem;
      line-height: 0.56rem;
      height: 0.96rem;
      box-sizing: border-box;
    }
    .textarea{
      height: auto;
    }
  }
  .select {
    margin:0.28rem;
    font-size: 0.24rem;
    color: #666666;
    img{
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.08rem;
    }
  }
  .bottom{
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    padding: 0.1rem 0.28rem;
    background: #ffffff;
    .btn{
      width: 100%;
      height: 0.88rem;
      font-size: 0.32rem;
    }
    .van-button--danger {
      color: #fff;
      background: #EF3342;
      border: 1px solid #EF3342;
    }
  }
</style>


