<template>
    <div class="wrapper">
        <div class="search">
            <div class="search-t">
                <img src="../../assets/images/icon_searchhui.svg" alt="" @click="gosearchlist"><input type="text" placeholder="搜索商品名称/关键词" v-model="searchinp">
            </div>
            <div class="search-c">
               <div class="search-ct">
                 热门搜索
               </div>
               <div class="search-cm" v-if='hotsearchshow'>
                  <div class="search-cml" v-for="(item,index) in hotsearch" :key="index" @click="golist(item.search)">
                     {{item.search}}
                  </div>
               </div>
               <div class="search-cm" v-else>
                  {{hotsearchinp}}
               </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      hotsearch:[],//热门搜索
      hotsearchshow:true,//有无热门搜索
      searchinp:'',//搜索关键字
      hotsearchinp:"暂无内容",//无搜索显示
    };
  },
  watch: {},
  computed: {},
  methods: {
    gosearchlist(){
        var  searchinp =this.searchinp
        this.$router.push({path:"/searchlist",query:{searchinp:searchinp}})
    },
    golist(searchinp){
        var  searchinp =searchinp
        this.$router.push({path:"/searchlist",query:{searchinp:searchinp}})
    }
  },
  created() {},
  mounted() {
      document.title="搜索"
      this.$post("/api/index/hot_search").then(response => {
        console.log(response);
        this.hotsearch = response.data
        if(response.code==2){
          this.hotsearch = response.msg
          this.hotsearchshow=false
        }
      });
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.wrapper {
  width: 100%;
}
.search-t {
  margin-left: 0.24rem;
  margin-top: 0.16rem;
  width: 6.94rem;
  height: 0.76rem;
  background-color: #f7f7f7;
  border-radius: 0.5rem;
}
.search-t img {
  margin-left: 0.24rem;
  vertical-align: middle;
  width: 0.32rem;
  height: 0.32rem;
}
.search-t input {
  display: inline-block;
  margin-left: 0.25rem;
  background: none;
  border: none;
  // color: #f7f7f7;
  line-height: 0.76rem;
  font-size: 0.3rem;
}
.search-t input::-webkit-input-placeholder {
  color: #bbb;
}
.search-t input::-ms-input-placeholder {
  //由于我的IE刚好是IE9，支持不了placeholder，所以也测试不了(⊙﹏⊙)，有IE10以上的娃可以帮我试试
  color: #bbb;
}
.search-ct{
  margin-top: 0.52rem;
  font-size: 0.28rem;
  margin-left: 0.24rem;
}
.search-cm{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin:0 0.24rem;
}
.search-cml{
    padding: 0.06rem 0.25rem;
    line-height: 0.6rem;
    text-align: center;
    border: 1px solid #ebebeb;
    border-radius: 0.4rem;
    margin-top: 0.24rem;
    margin-left: 0.24rem;
}
</style>


