<template>
  <!-- 去付款弹窗-->
  <van-popup v-model="showup" position="bottom" :overlay="true"
             @click-overlay="showup = false ;xuanzhong1 = false;xuanzhong2 = false;">
    <div class="paypopup">
      <div class="paypopup-close">
        <div class="paypopup-closel">
        </div>
        <div class="paypopup-closel" @click="showup = false ;xuanzhong1 = false;xuanzhong2 = false;">
          <img src="../../assets/images/icon_close.svg" alt="">
        </div>

      </div>
      <div class="paypopup-order">
        支付订单
      </div>
      <div class="paypopup-money">
        ¥{{paymoney}}
      </div>
      <div class="paypopup-balance" :class="xuanzhong1?'bg':''" @click="xuanzhong1 = true ;xuanzhong2 = false;">
        <div class="paypopup-balancel">
          <img src="../../assets/images/icon_yezf.svg" alt="">
          <span>余额支付</span>
        </div>
        <div class="paypopup-balancer">
          <img
            :src="xuanzhong1? require('../../assets/images/icon_xuanzhong.svg') :require('../../assets/images/icon_weixuan.svg')"
            alt="">
        </div>
      </div>
      <div class="paypopup-weixin" :class="xuanzhong2?'bg':''" @click="xuanzhong2 = true;xuanzhong1 = false;">
        <div class="paypopup-weixinl">
          <img src="../../assets/images/icon_wxzf.svg" alt="">
          <span>微信支付</span>
        </div>
        <div class="paypopup-weixinr">
          <img
            :src="xuanzhong2? require('../../assets/images/icon_xuanzhong.svg') :require('../../assets/images/icon_weixuan.svg')"
            alt="">
        </div>
      </div>
      <div class="paypopup-pay" @click="gopay">
        立即支付
      </div>
    </div>
  </van-popup>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    components: {},
    props: {
      paymoney: {
        default: "0.00",
      },
      order_number: {
        type: String,
        default: "",
      }
    },
    data() {
      return {
        showup: false,//去付款弹窗
        xuanzhong1: false, //
        xuanzhong2: false, //
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      showpay() {
        this.showup = true;
      },
      xuanz1() {
        this.xuanzhong1 = false;
        this.xuanzhong2 = true;
      },
      xuanz2() {
        this.xuanzhong1 = true;
        this.xuanzhong2 = false;
      },
      gopay() {
        if (!this.xuanzhong1 && !this.xuanzhong2) {
          return this.$toast('请选择支付方式！')
        }
        if (this.xuanzhong1) {//余额支付
          this.$post('/api/orders/ordersinfo', {id: this.userInfo.id, orders_num: this.order_number,}).then(res => {
            this.$toast(res.msg);
            if (res.code == 1) {
              this.showup = false;
              this.xuanzhong1 = false;
              this.xuanzhong2 = false;
              this.$emit("paysuccess");
            }
          })
        } else {
          const that = this;
          that.$post("/api/pay/pay", {
            orders_num: that.order_number
          }).then(res => {
            console.log("res:", res);
            callpay();

            //调用微信JS api 支付
            function jsApiCall() {
              console.log(JSON.parse(res));
              WeixinJSBridge.invoke("getBrandWCPayRequest", JSON.parse(res), function (res) {
                console.log(res);
                if (res.err_msg == "get_brand_wcpay_request:ok") {
                  that.$emit("paysuccess");
                  that.showup = false;
                  that.xuanzhong1 = false;
                  that.xuanzhong2 = false;
                } else if (res.err_msg == "get_brand_wcpay_request:cancel" || res.err_msg == "get_brand_wcpay_request:fail") {
                  alert("支付失败!");
                }
              });
            }

            function callpay() {
              if (typeof WeixinJSBridge == "undefined") {
                if (document.addEventListener) {
                  document.addEventListener(
                    "WeixinJSBridgeReady",
                    jsApiCall,
                    false
                  );
                } else if (document.attachEvent) {
                  document.attachEvent("WeixinJSBridgeReady", jsApiCall);
                  document.attachEvent("onWeixinJSBridgeReady", jsApiCall);
                }
              } else {
                jsApiCall();
              }
            }
          });
        }
      },
    },
    created() {
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  /*.paypopup-wrap{*/
  .van-popup {
    border-radius: 0 !important;
    border-top-left-radius: 0.3rem !important;
    border-top-right-radius: 0.3rem !important;
  }

  .paypopup {
    width: 100%;
    height: 6.96rem;
  }

  .paypopup-close {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0.2rem 0.24rem 0;
  }

  .paypopup-close img {
    width: 0.44rem;
    height: 0.44rem;
  }

  .paypopup-order {
    width: 100%;
    text-align: center;
    color: #999;
    font-size: 0.32rem;
    margin-top: 0.36rem;
  }

  .paypopup-money {
    width: 100%;
    font-size: 0.46rem;
    text-align: center;
    font-weight: 500;
    line-height: 0.9rem;
    margin-top: 0.05rem;
  }

  .paypopup-balance {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.6rem;

    line-height: 1.08rem;
    margin-top: 0.72rem;
  }

  .bg {
    background: #f5f5f5;
  }

  .paypopup-balancel span {
    margin-left: 0.2rem;
    font-size: 0.28rem;
    line-height: 1.08rem;
  }

  .paypopup-weixin {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.6rem;
    line-height: 1.08rem;
  }

  .paypopup-weixinl span {
    margin-left: 0.2rem;
    font-size: 0.28rem;
    line-height: 1.08rem;
  }

  .paypopup-pay {
    width: 6.94rem;
    height: 0.88rem;
    background: #ef3342;
    line-height: 0.88rem;
    text-align: center;
    color: #fff;
    border-radius: 0.5rem;
    margin: 0.9rem 0 0 0.28rem;
  }

  /*}*/
</style>


