<template>
  <keep-alive>
    <div id="tabbar">
      <router-view/>
      <div class="bottomBar">
        <ul>
          <router-link :to="{name:'index'}">
            <li @click="toColor">
              <p><img :src="isColor?require('../assets/images/icon_home1@2x.png'):require('../assets/images/icon_home2@2x.png')" alt=""></p>
              <p :class="{bottomBarColorActive:isColor}">首页</p>
            </li>
          </router-link>
          <router-link :to="{name:'cart'}">
            <li @click="toColor2">
              <p><img :src="isColor2?require('../assets/images/icon_gwc1@2x.png'):require('../assets/images/icon_gwc2@2x.png')" alt=""></p>
              <p :class="{bottomBarColorActive:isColor2}">购物车</p>
            </li>
          </router-link>
          <router-link :to="{name:'information'}">
            <li @click="toColor3">
              <p><img :src="isColor3?require('../assets/images/icon_wd1@2x.png'):require('../assets/images/icon_wd2@2x.png')" alt=""></p>
              <p :class="{bottomBarColorActive:isColor3}">我的</p>
            </li>
          </router-link>
        </ul>
      </div>
      <!-- tabbar -->
    </div>
  </keep-alive>

</template>

<script>
  import {mapGetters} from "vuex"

export default {
  name: "Tabbar",
  data() {
    return {
      isColor: false,
      isColor2: false,
      isColor3: false,
      current_tab: ""
    };
  },
  watch:{
    $route(to,form){
      if (to.name == "index") {
        document.title= '首页';
        this.isColor = true;
        this.isColor2 = false;
        this.isColor3 = false;
      } else if (to.name == "cart") {
        document.title= '购物车';
        this.isColor2 = true;
        this.isColor = false;
        this.isColor3 = false;
      } else if (to.name == "information") {
        document.title= '个人中心';
        this.isColor3 = true;
        this.isColor2 = false;
        this.isColor = false;
      }
    }
  },
  computed: {
    ...mapGetters(["token","userinfo"])
  },
  methods: {
    toColor: function() {
      this.isColor = true;
      this.isColor2 = false;
      this.isColor3 = false;
    },
    toColor2: function() {
      this.isColor = false;
      this.isColor2 = true;
      this.isColor3 = false;
    },
    toColor3: function() {
      this.isColor = false;
      this.isColor2 = false;
      this.isColor3 = true;
    }
  },
  mounted() {
    this.current_tab = this.$router.history.current.name;
    var name = this.current_tab;
    if (name == "index") {
      this.isColor = true;
    } else if (name == "cart") {
      this.isColor2 = true;
    } else if (name == "information") {
      this.isColor3 = true;
    } else {
      this.isColor = true;
      this.$router.push("/index");
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}
.bottomBar {
  height: 1rem;
  width: 100%;
  bottom: 0;
  background: #fff;
  position: fixed;
  bottom: 0;
}
.bottomBar ul {
  display: flex;
  width: 100%;
  justify-content: space-around;
}
.bottomBar ul li {
  width: 2.5rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bottomBar ul li img {
  display: inline-block;
  width: 0.56rem;
  height: 0.56rem;
  margin: 0.08rem 0;
}
.bottomBar ul li p {
  font-family: "PingFangSC-Regular";
  font-size: 0.24rem;
  color: #d2d7e8;
  letter-spacing: 0;
  line-height: 0.24rem;
}
.bottomBarColorActive {
  color: #E54040 !important;
}
</style>



