<template>
  <div class="wrapper">
    <div class="search">
      <div class="search-t">
        <img src="../../assets/images/icon_searchhui.svg" alt="" @click="search"><input type="text" placeholder="搜索商品名称/关键词" v-model="searchinp">
      </div>
      <div class="searchlist">
        <div class="searchlist-t">
          <div class="searchlist-tl" @click="sort(1)" :class="currentindex==1?'active':''">
            默认
          </div>
          <div class="searchlist-tl" @click="sort(2)" :class="currentindex==2?'active':''">
            新品
          </div>
          <div class="searchlist-tl" @click="sort(3)" :class="currentindex==3?'active':''">
            销量
            <img src="../../assets/images/icon_paix1.svg" alt="" v-if="salesshowfalse">
            <img src="../../assets/images/icon_paix3.svg" alt="" v-if="salesshow1">
            <img src="../../assets/images/icon_paix2.svg" alt="" v-if="salesshow2">
          </div>
          <div class="searchlist-tl" @click="sort(4)" :class="currentindex==4?'active':''">
            价格
            <img src="../../assets/images/icon_paix1.svg" alt="" v-if="priceshowfalse">
            <img src="../../assets/images/icon_paix3.svg" alt="" v-if="priceshow1">
            <img src="../../assets/images/icon_paix2.svg" alt="" v-if="priceshow2">
          </div>
        </div>
        <van-list v-model="loading" :finished="finished" @load="getdata" :offset="20">
        <div class="tabbox" v-show="list.length!=0">
            <div class="tabboxm" v-for="(item,index) in list" :key="index" @click="golistdetail(item.id)">
              <div class="tabboxmt">
                <img :src="item.logo_image" alt="">
              </div>
              <div class="tabboxmm1 sigle-h">
                {{item.name}}
              </div>
              <div class="tabboxmb">
                <span>
                  <i>￥</i>{{item.goods_price}}</span>
                <span>
                  <i>￥</i>{{item.original_price}}</span>
              </div>
              <div class="tanboxmbr">
                已售{{item.yesterday_count}}件
              </div>
            </div>
        </div>
         <div class="nomore" v-show="finished==true && list.length != 0">没有更多了^v^</div>
          </van-list>
        <div class="tabbox1" v-if="list.length==0">
           <img src="../../assets/images/icon_searchwu.svg" alt="">
           <p>没有找到相关商品</p>
           <p>试试搜索别的关键词吧</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  components: {},
  props: {},
  data() {
    return { 
      searchinp: " ", //
      list: "", //商品列表
      currentindex: "1", //当前下标
      salesshowfalse: true, //销量消失
      salesshow1: false, //销量显示高
      salesshow2: false, //销量显示低
      priceshowfalse: true, //销量消失
      priceshow1: false, //销量显示高
      priceshow2: false ,//销量显示低
      loading: false, //上拉加载
      finished: false, //加载完成
      page:1,//页面
    };
  },
  watch: {},
  computed: {
     ...mapGetters(['userInfo']),
  },
  methods: {
    golistdetail(id){
      var goods_id = id
       this.$router.push({path:"/listdetail",query:{goods_id:goods_id}})
    },
    search() {
      var user_id = this.userInfo.id;
      var keywords = this.searchinp;
      this.$fetch("/api/index/goods_search", {
        user_id: user_id,
        keywords: keywords
      }).then(response => {
        console.log(response);
        this.list = response.data.data;
      });
    },
    sort(index) {
       this.finished = false;
       this.page = 1
      if (this.searchinp == "") {
        return this.$toast({
          message: "搜索内容不能为空",
          duration: 1000
        });
      }
      this.currentindex = index;
      var user_id = this.userInfo.id;
      var keywords = this.searchinp;
      if (index == 1) {
        this.salesshowfalse = true;
        this.salesshow1 = false;
        this.salesshow2 = false;
        this.priceshowfalse = true;
        this.priceshow1 = false;
        this.priceshow2 = false;
        this.$fetch("/api/index/goods_search", {
          user_id: user_id,
          keywords: keywords
        }).then(response => {
          console.log(response);
          this.list = response.data.data;
        });
      } else if (index == 2) {
        this.salesshowfalse = true;
        this.salesshow1 = false;
        this.salesshow2 = false;
        this.priceshowfalse = true;
        this.priceshow1 = false;
        this.priceshow2 = false;
        var type = 1;
        this.$fetch("/api/index/goods_search", {
          user_id: user_id,
          keywords: keywords,
          type: type
        }).then(response => {
          console.log(response);
          this.list = response.data.data;
        });
      } else if (index == 3) {
        this.priceshowfalse = true;
        this.priceshow1 = false;
        this.priceshow2 = false;
        this.salesshowfalse = false;
        if (this.salesshow1 == false) {
          var type = 2;
          this.salesshow1 = true;
          this.salesshow2 = false;
          this.$fetch("/api/index/goods_search", {
            user_id: user_id,
            keywords: keywords,
            type: type
          }).then(response => {
            console.log(response);
            this.list = response.data.data;
          });
        } else {
          var type = 3;
          this.salesshow1 = false;
          this.salesshow2 = true;
          this.$fetch("/api/index/goods_search", {
            user_id: user_id,
            keywords: keywords,
            type: type
          }).then(response => {
            console.log(response);
            this.list = response.data.data;
          });
        }
      } else if (index == 4) {
        this.salesshowfalse = true;
        this.salesshow1 = false;
        this.salesshow2 = false;
        this.priceshowfalse = false;
        if (this.priceshow1 == false) {
          var type = 4;
          this.priceshow1 = true;
          this.priceshow2 = false;
          this.$fetch("/api/index/goods_search", {
            user_id: user_id,
            keywords: keywords,
            type: type
          }).then(response => {
            console.log(response);
            this.list = response.data.data;
          });
        } else {
          var type = 5;
          this.priceshow1 = false;
          this.priceshow2 = true;
          this.$fetch("/api/index/goods_search", {
            user_id: user_id,
            keywords: keywords,
            type: type
          }).then(response => {
            console.log(response);
            this.list = response.data.data;
          });
        }
      }
    },
    getdata() {
      var user_id = this.userInfo.id;
      var keywords = this.searchinp;
       var page = this.page
        this.$post("/api/index/goods_search", {
            user_id,
            page,
            keywords
        }).then(response => {
             if (this.page == 1) {
            this.list = response.data.data;
            console.log("this.list1",this.list)
          } else {
            this.list = this.list.concat(response.data.data);
            console.log("this.list2",this.list)
          }
          this.page++;
          this.loading = false;
          console.log("response.data.count",response.data.count,"this.list.length",this.list.length)
          if (response.data.count == this.list.length) {
            this.finished = true;
          }
        });
    }
  },
  created() {},
  mounted() {
    document.title = "搜索结果";
    this.searchinp = this.$route.query.searchinp;
    var user_id = this.userInfo.id;
    var keywords = this.searchinp;
    console.log(user_id, keywords);
    this.$fetch("/api/index/goods_search", {
      user_id: user_id,
      keywords: keywords
    }).then(response => {
      console.log(response);
      this.list = response.data.data;
    });
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.tabbox1{
  margin-top: 2.52rem;
  text-align: center;
}
.tabbox1 img{
  width: 2.8rem;
  height: 2.8rem;
}
.tabbox1 p:nth-of-type(1){
  font-size: 0.26rem;
  color: #333;
  line-height: 0.3rem;
}
.tabbox1 p:nth-of-type(2){
  font-size: 0.32rem;
  color: #999;
  line-height: 0.5rem;
}
.active {
  color: #5045d8;
}
.wrapper {
  width: 100%;
}
.search-t {
  margin-left: 0.24rem;
  margin-top: 0.16rem;
  width: 6.94rem;
  height: 0.76rem;
  background-color: #f7f7f7;
  border-radius: 0.5rem;
}
.search-t img {
  margin-left: 0.24rem;
  vertical-align: middle;
  width: 0.32rem;
  height: 0.32rem;
}
.search-t input {
  display: inline-block;
  margin-left: 0.25rem;
  background: none;
  border: none;
  //   color: #f7f7f7;
  line-height: 0.76rem;
  font-size: 0.3rem;
}
.search-t input::-webkit-input-placeholder {
  color: #bbb;
}
.search-t input::-ms-input-placeholder {
  //由于我的IE刚好是IE9，支持不了placeholder，所以也测试不了(⊙﹏⊙)，有IE10以上的娃可以帮我试试
  color: #bbb;
}
.searchlist-t {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0.1rem 0.34rem;
  margin-top: 0.4rem;
  font-size: 0.28rem;
}
.tabbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.tabboxm {
  margin-top: 0.24rem;
  width: 3.36rem;
  height: 4.68rem;
  background: #fff;
  border-radius: 0.2rem;
}
.tabboxm:nth-of-type(2n) {
  margin-left: 0.4rem;
}
.tabboxm:nth-of-type(2n + 1) {
  margin-left: 0.22rem;
}
.tabboxmt img {
  width: 3.36rem;
  height: 3.36rem;
  border-radius: 0.2rem;
}
.tabboxmm1 {
  line-height: 0.32rem;
  margin: 0 0.24rem;
  font-size: 0.24rem;
  margin-top: 0.15rem;
  font-weight: bold;
  width: 2.88rem;
}
.tabboxmb {
  margin-top: 0.15rem;
  margin-right: 0.24rem;
}
.tabboxmb span:nth-of-type(1) {
  color: #ef3544;
  font-size: 0.32rem;
  margin-left: 0.24rem;
}
.tabboxmb span:nth-of-type(2) {
  color: #333;
  font-size: 0.24rem;
  text-decoration: line-through;
}
.tabboxmb span i {
  font-size: 0.2rem;
}
.tanboxmbr {
  margin-top: 0.14rem;
  margin-left: 0.24rem;
  font-size: 0.2rem;
  color: #bbb;
}
.nomore {
    width: 100%;
    text-align: center;
    color: #999999;
    line-height: 30px;
  }
</style>


