<template>
  <div class="wrapper">
    <div class="mid">
      <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
        <van-list v-model="loading" :finished="finished" :finished-text="list.length?'没有更多了':''" @load="getdata" :immediate-check=true :offset="20">
          <div class="no" v-if="list.length == 0">
            <img src="./../../assets/images/icon_gwcwsp.svg" alt="">
            <div>暂无数据</div>
          </div>
          <van-row class="box" v-for="(item,index) in list" :key="index">
            <van-row class="goodlist" type="flex">
              <img :src="item.goods_image" alt="">
              <van-row class="goodinfo">
                <van-row class="name more-h">{{item.goods_name}}</van-row>
                <van-row type="flex" justify="space-between">
                  <van-col class="type more-h">{{item.goods_specification}}</van-col>
                  <van-col>x{{item.goods_count}}</van-col>
                </van-row>
              </van-row>
            </van-row>
            <van-row class="btn" type="flex" justify="space-between" align="center">
              <div>{{item.type}} &nbsp;&nbsp;&nbsp;&nbsp; <span :class="item.refund_status == '退款中'? 'span' :''">{{item.refund_status}}</span></div>
              <!--<div>仅退款 退款失败 <span>退款中</span>退款成功</div>-->
              <van-button round type="default" size="small" class="default" @click.native="$router.push({path:'/replacementdetail',query:{id:item.id}})">查看详情</van-button>
            </van-row>
          </van-row>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        list:"",
        page:1,

      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/orders/refund_order', {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = "";
              this.list = res.data.refund_data;
            } else {
              this.list = this.list.concat(res.data.refund_data);
            }
            if (res.data.total_row == this.list.length || res.data.refund_data.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    created() {
    },
    mounted() {
      document.title = '退货/退款';
      // this.$post('/api/orders/refund_order',{id:1}).then(res=>{
      //   console.log(res);
      //   if(res.code == 1){
      //
      //   }
      // })
    }
  }
</script>
<style scoped lang='stylus'>
  .mid >>> .van-pull-refresh__track {
    min-height: calc(100vh - 0.28rem);
  }
  .mid >>> .no{
    margin:0 auto 0;
    padding-top 2rem;
    text-align: center;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
  }

  .mid {
    padding: 0.28rem 0.28rem 0;
    box-sizing: border-box;
    .box {
      padding: 0.28rem 0.24rem ;
      box-sizing: border-box;
      background: #FFFFFF;
      margin-bottom: 0.2rem;
      box-shadow: 0 0.12rem 0.24rem 0 rgba(242, 242, 242, 0.50);
      border-radius: 0.2rem;
      .goodlist {
        margin-bottom: 0.28rem;
        img {
          width: 1.8rem;
          height: 1.8rem;
          border-radius: 0.12rem;
        }
        .goodinfo {
          flex: 1;
          margin-left: 0.24rem;
          font-size: 0.24rem;
          color: #999999;
          line-height: 0.34rem;
          display: flex;
          flex-direction: column;
          .name {
            color: #333333;
            margin-bottom: 0.1rem;
          }
          .type{
            word-wrap:break-word ;
            width: 3.8rem;
          }
        }
        .goodinfo:after {
          display: none;
        }
      }
      .btn {
        text-align: right;
        .span{
          color: #5045D8;
        }
        .default {
          width: 1.68rem;
          color: #666666;
          height: 0.6rem;
          font-size: 0.28rem;
        }
        .purple {
          width: 1.68rem;
          background: #5045D8;
          height: 0.6rem;
          color: #FFFFFF;
          font-size: 0.28rem;
        }
      }
    }
  }
</style>


