  width: 100%;
  min-height: 100vh;
  background: #f7f7f7;
  padding-bottom: 1.08rem;
}
.order-address {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0.3rem 0.28rem;
  background: #fff;
}
.order-address-l {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.order-address-l p {
  line-height: 0.4rem;
  margin-left: 0.34rem;
}
.prder-good {
  width: 100%;
  background: #fff;
  margin-top: 0.2rem;
  overflow: hidden;
}
.prder-goodm {
  display: flex;
  flex-direction: row;
  margin: 0 0.24rem;
  margin-top: 0.24rem;
  padding-bottom: 0.4rem;
}
.prder-goodml img {
  width: 2.28rem;
  height: 2.28rem;
}
.prder-goodmr {
  margin-left: 0.28rem;
}
.prder-goodmrb {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 0.6rem;
}
.prder-goodmrt {
  line-height: 0.4rem;
  font-size: 0.28rem;
  font-weight: bold;
}
.prder-goodmrm {
  margin-top: 0.12rem;
  color: #999;
}
.prder-goodmrb {
  width: 4.5rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.prder-goodmrbl {
  font-size: 0.24rem;
  color: #ef3342;
  line-height: 0.44rem;
}
.prder-goodmrbr span {
  display: inline-block;
  width: 0.44rem;
  height: 0.44rem;
  text-align: center;
  line-height: 0.44rem;
  background: #f8f8f8;
  vertical-align: middle;
}
.prder-goodmrbr input {
  width: 1.36rem;
  height: 0.44rem;
  background: #f8f8f8;
  text-align: center;
  border: none;
  outline: 0;
  margin: 0 0.05rem;
  padding-top: 0.01rem;
  vertical-align: middle;
}
.discount {
  padding: 0 0.28rem;
  display: flex;
  flex-direction: row;
  line-height: 0.96rem;
  justify-content: space-between;
  border-bottom: 1px solid #ebebeb;
  background: #fff;
}
.discount1 {
  padding: 0 0.28rem;
  display: flex;
  flex-direction: row;
  line-height: 0.96rem;
  border-bottom: 1px solid #ebebeb;
  background: #fff;
}
.discount1-r input {
  border: none;
  outline: 0;
  line-height: 0.9rem;
  padding-left: 0.5rem;
  margin-top: -0.08rem;
}
.dibu {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 1.08rem;
  line-height: 1.08rem;
  background: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
}
.dibu-l {
  margin-left: 0.28rem;
}
.dibu-m span {
  color: #ef3342;
}
.dibu-r {
  width: 2.14rem;
  background: #ef3342;
  color: #fff;
  font-size: 0.32rem;
  text-align: center;
}
.vanbox {
  width: 5.18rem;
  height: 2.96rem;
  border-radius: 0.2rem;
  overflow: hidden;
}
.vanboxt {
  margin: 0 auto;
  width: 3.08rem;
  height: 0.8rem;
  text-align: center;
  margin-top: 0.6rem;
  font-size: 0.28rem;
  color: #999;
  line-height: 0.32rem;
}
.vanboxb {
  display: flex;
  flex-direction: row;
  margin-top: 0.6rem;
}
.vanboxbl {
  width: 2.6rem;
  height: 0.96rem;
  background: #f5f5f5;
  line-height: 0.96rem;
  text-align: center;
}
.vanboxbr {
  width: 2.6rem;
  height: 0.96rem;
  background: #f5f5f5;
  color: #ef3342;
  line-height: 0.96rem;
  text-align: center;
}
.addpopup .van-popup {
  border-radius: 0.3rem !important;
}
.paypopup-wrap .van-popup {
  border-top-left-radius: 0.3rem !important;
  border-top-right-radius: 0.3rem !important;
  z-index: 1
}
.paypopup {
  width: 100%;
  height: 6.96rem;
}
.paypopup-close {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0.2rem 0.24rem 0;
}
.paypopup-close img {
  width: 0.44rem;
  height: 0.44rem;
}
.paypopup-order {
  width: 100%;
  text-align: center;
  color: #999;
  font-size: 0.32rem;
  margin-top: 0.36rem;
}
.paypopup-money {
  width: 100%;
  font-size: 0.46rem;
  text-align: center;
  font-weight: 500;
  line-height: 0.9rem;
  margin-top: 0.05rem;
}
.paypopup-balance {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.6rem;
  background: #f5f5f5;
  line-height: 1.08rem;
  margin-top: 0.72rem;
}
.paypopup-balancel span {
  margin-left: 0.2rem;
  font-size: 0.28rem;
  line-height: 1.08rem;
}
.paypopup-weixin {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.6rem;
  line-height: 1.08rem;
}
.paypopup-weixinl span {
