  font-size: 0.28rem;
  line-height: 1.08rem;
}
.paypopup-pay {
  width: 6.94rem;
  height: 0.88rem;
  background: #ef3342;
  line-height: 0.88rem;
  text-align: center;
  color: #fff;
  border-radius: 0.5rem;
  margin: 0.9rem 0 0 0.28rem;
}
</style>
<style lang="stylus" scoped>
</style>



