<template>
  <div class="wrapper" v-if="info">
    <van-row class="top" type="flex" align="center">
      <van-col class="circle"></van-col>
      <van-row class="detail">
        <van-row>
          <span>【{{info.type}}-{{info.refund_status|refund_status}}】</span>
          {{info.refund_reject}}
        </van-row>
        <van-row>{{info.update_time}}</van-row>
      </van-row>
    </van-row>
    <van-row class="number">
      <van-col>服务编号：{{info.refund_num}}</van-col>
      <van-col>申请时间：{{info.create_time}}</van-col>
    </van-row>
    <van-row class="mid">
      <van-row class="goodlist" type="flex">
        <img :src="info.goods_image" alt="">
        <van-row class="goodinfo">
          <van-row class="name ">{{info.goods_name}}</van-row>
          <van-row class="">{{info.goods_specification}}</van-row>
          <van-row type="flex" justify="space-between">
            <span>￥{{info.goods_price}}</span>
            <van-col>x{{info.goods_count}}</van-col>
          </van-row>
        </van-row>
      </van-row>
      <van-row class="rinfo">
        <van-row>退款金额：¥{{info.refund_money}}</van-row>
        <van-row>服务类型：{{info.type}}</van-row>
        <van-row>退款原因：{{info.refund_reason}}</van-row>
        <van-row>退款说明：{{info.refund_intro}}</van-row>
      </van-row>
      <van-row class="btn">
        <van-button round type="default" size="small" class="default" v-if="info.refund_status == 3"
                    @click.native="show = true">输入物流单号
        </van-button>
        <!--<van-button round type="default" size="small" class="default" v-if="">重新申请</van-button>-->
      </van-row>
    </van-row>
    <!--提交成功-->
    <van-popup v-model="show" :close-on-click-overlay=false>
      <van-row class="popup">
        <van-row class="title" type="flex" justify="center" align="center">输入物流单号</van-row>
        <van-row class="content" type="flex" justify="center" align="center">
          <input type="text" placeholder="请输入" v-model="tracking_num">
        </van-row>
        <van-row class="bottom">
          <van-col class="cancel" @click.native="show=false">取消</van-col>
          <van-col class="confirm" @click.native="confirm">确定</van-col>
        </van-row>
      </van-row>
    </van-popup>
  </div>
</template>

<script>
  import {checkempty} from "./../../assets/js/tool"
  import {mapGetters} from 'vuex'

  export default {
    components: {},
    props: {},
    data() {
      return {
        show: false,
        id: this.$route.query.id,
        info: "",
        tracking_num: "",
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      confirm() {
        if (!checkempty(this.tracking_num, '物流单号不能为空！')) {
          return
        }
        this.$post('/api/orders/tracking_num', {id: this.info.refund_id, tracking_num: this.tracking_num}).then(res => {
          this.$toast(res.msg);
          if (res.code == 1) {
            this.getdata();
            this.show = false;
          }
        })
      },
      getdata() {
        this.$post('/api/orders/refund_detail', {id: this.id}).then(res => {
          console.log(res);
          if (res.code == 1) {
            this.info = res.data;
          } else {
            this.$toast(res.msg);
          }
        })
      }
    },
    created() {
      document.title = '退款详情';
      this.getdata();
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
  }

  .top {
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    padding: 0.28rem;
    .circle {
      margin-left: 0.24rem;
      margin-right: 0.68rem;
      padding: 0.1rem;
      border-radius: 50%;
      border: 0.06rem solid #ffffff;
      box-sizing: content-box;
    }
    .detail {
      font-size: 0.28rem;
      color: #FFFFFF;
      line-height: 0.4rem;
      .van-row:nth-child(2) {
        margin-top: 0.2rem;
      }
    }
  }

  .number:after {
    display: none;
  }

  .number {
    height: 1.28rem;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0.2rem 0.28rem;
    box-sizing: border-box;
    font-size: 0.24rem;
    color: #999999;
  }

  .mid {
    margin-top: 0.2rem;
    padding: 0.28rem 0.28rem;
    box-sizing: border-box;
    background: #ffffff;
    .goodlist {
      width: 100%;

      margin-bottom: 0.52rem;
      img {
        width: 2.28rem !important;
        height: 2.28rem !important;
        border-radius: 0.12rem;
      }
      .goodinfo {
        flex: 1;
        width: 100%;
        margin-left: 0.24rem;
        font-size: 0.28rem;
        color: #999999;
        line-height: 0.34rem;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .name {
          color: #333333;
        }
        span {
          color: #EF3342;
          font-size: 0.32rem;
        }
      }
      .goodinfo:after {
        display: none;
      }
    }
    .rinfo {
      font-size: 0.26rem;
      color: #999999;
      margin-bottom: 0.28rem;
      line-height: 0.5rem;
    }
    .btn {
      text-align: center;
      .default {
        width: 4rem;
        height: 0.88rem;
        font-size: 0.32rem;
        color: #333333;
      }
    }
  }

  /*输入物流单号*/
  .van-popup {
    border-radius: 0.4rem;
    .popup {
      width: 5.9rem;
      height: 4.48rem;
      padding-top: 0.6rem;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      .title {
        font-size: 0.32rem;
        color: #333333;
        margin-bottom: 0.2rem;
      }
      .content {
        width: 5.1rem;
        height: 0.96rem;
        text-align: center;
        font-size: 0.28rem;
        color: #999999;
        margin: 0.6rem auto;
        background: #F7F7F7;
        border-radius: 0.5rem;
        input {
          width: 80%;
          height: 80%;
          border: none;
          background: transparent;
        }
      }
    }
    .bottom {
      margin: 0 0.4rem;
      display: flex;
      justify-content: space-between;
    }
    .cancel {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 2.46rem;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #333333;
      border-radius: 0.72rem;
      border-right: 1px solid #ebebeb;
    }
    .confirm {
      .cancel;
      background: #EF3342;
      color: #ffffff;
    }
  }
</style>


