<template>
  <div class="wrapper">
    <van-row class="top" type="flex">
      <img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2255384485,690736213&fm=27&gp=0.jpg" alt="">
      <van-row class='mid'>
        <van-row type="flex" algin="center" justify="space-between">
          <van-col>快递公司：</van-col>
          <van-col class="blue">运输中</van-col>
        </van-row>
        <van-col>快递单号：</van-col>
      </van-row>
    </van-row>
    <van-steps direction="vertical" :active="0" active-color="#f44">
      <van-step>
        <h3>【城市】物流状态1</h3>
        <p>2016-07-12 12:40</p>
      </van-step>
      <van-step>
        <h3>【城市】物流状态2</h3>
        <p>2016-07-11 10:00</p>
      </van-step>
      <van-step>
        <h3>快件已发货</h3>
        <p>2016-07-10 09:30</p>
      </van-step>
    </van-steps>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {}
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {},
    created() {
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    min-height: 100vh;
    background: #F5F5F5;
    padding: 0.2rem 0.28rem;
    box-sizing: border-box;
  }
  .top{
    img{
      width: 1.04rem;
      height: 1.04rem;
      border-radius: 0.12rem;
      margin-right: 0.24rem;
    }
    margin: 0 auto;
    padding: 0.24rem;
    box-sizing: border-box;
    width: 6.94rem;
    height: 1.52rem;
    background: #ffffff;
    box-shadow: 0 0.12rem 0.24rem 0 rgba(242,242,242,0.50);
    border-radius: 10px;
    .mid{
      /*height: 100%;*/
      flex: 1;
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      line-height: 0.4rem;
      .blue{
        width: 1.16rem;
        height: 0.4rem;
        text-align: center;
        font-size: 0.2rem;
        color: #FFFFFF;
        background: #5045D8;
        border-radius: 0.34rem;
      }
    }
  }
</style>


