<template>
    <div class="wrapper">
        <div class="home-content-tab">
            <van-tabs @click="gochange" v-model="active">
                <van-tab v-for="(item,index) in second_classify" :key="index" :title="item.name">
                    <van-list v-model="loading" :finished="finished" @load="getdata" :offset="20">
                     <div class="tabbox">
                        <div class="tabboxm" v-for="(item,index) in second_goods" :key="index" @click="golistdetail(item.id)">
                            <div class="tabboxmt">
                                <img :src="item.logo_image" alt="">
                            </div>
                            <div class="tanboxmm1 sigle-h">
                                {{item.name}}
                            </div>
                            <div class="tanboxmm2 more-h" style=" -webkit-box-orient: vertical;">
                                {{item.describe}}
                            </div>
                            <div class="tanboxmb">
                                <span><i>￥</i>{{item.goods_price}}</span>
                                <span><i>￥</i>{{item.original_price}}</span>
                            </div>
                            <div class="tanboxmbb">
                                       已售{{item.yesterday_count}}件
                            </div>
                        </div>
                    </div>
                    <div class="nomore" v-show="finished==true && second_goods.length != 0">没有更多了^v^</div>
                        </van-list>
                </van-tab>
            </van-tabs>
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      second_classify: [],//二级分类id
      second_goods:[],//二级分类商品
       loading: false, //上拉加载
      finished: false, //加载完成
       classifyindexid:'',//下标id
       active:0,//初始显示那个类
       page:1,//
    };
  },
  watch: {},
  computed: {},
  methods: {
    gochange(index) {
      this.finished = false
      console.log(index);
      var secondid = this.second_classify[index].id
      this.classifyindexid = secondid
      this.page = 1
      this.$post("/api/index/second_goods",{id:secondid}).then(response => {
              console.log(response);
              this.second_goods = response.data
              if(response.code==2){
                  return this.$toast({
                            message:response.msg,
                            duration:1000
                        })
              }
            });
    },
    golistdetail(id){
      var goods_id = id
      // console.log(id)
       this.$router.push({path:"/listdetail",query:{goods_id:goods_id}})
    },
     getdata() {
       var page = this.page
       var id = this.classifyindexid
        this.$post("/api/index/second_goods", {
            page,
            id
        }).then(response => {
             if (this.page == 1) {
            this.second_goods = response.data.data;
          } else {
            this.second_goods = this.second_goods.concat(response.data.data);
          }
          this.page++;
          this.loading = false;
          if (response.data.count == this.second_goods.length) {
            this.finished = true;
          }
        });
    }
  },
  created() {},
  mounted() {
    document.title = "为你推荐";
    var id = this.$route.query.first_classify
    var secondid = this.$route.query.second_classify
    // this.second_classify = secondid
    this.classifyindexid = secondid
    this.active = this.$route.query.value
     console.log("this.value",this.active)
    this.$post("/api/index/second_classify",{id:id}).then(response => {
        console.log(response);
        this.second_classify = response.data
           this.$post("/api/index/second_goods",{id:secondid}).then(response => {
              console.log(response);
              this.second_goods = response.data.data
             
              if(response.code==2){
                  return this.$toast({
                            message:response.msg,
                            duration:1000
                        })
              }
            });
      });
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.wrapper {
  width: 100%;
  min-height: 100vh;
  background: #f7f7f7;
}
.tabbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.home-content-tab {
  width: 100%;
  background: #f7f7f7;
}
.home-content-tabtop {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
}
.home-content-tabtop::-webkit-scrollbar {
  display: none;
}
.home-content-tabtopm {
  width: 1.08rem;
  height: 0.56rem;
  font-size: 0.28rem;
  line-height: 0.56rem;
  text-align: center;
}
.tabboxm {
  margin-top: 0.24rem;
  width: 3.24rem;
  height: 5.02rem;
  background: #fff;
  border-radius: 0.2rem;
}
.tabboxm:nth-of-type(2n) {
  margin-left: 0.4rem;
}
.tabboxm:nth-of-type(2n + 1) {
  margin-left: 0.22rem;
}
.tabboxmt img {
  width: 3.24rem;
  height: 3.24rem;
  border-radius: 0.2rem;
}
.tanboxmm1 {
  line-height: 0.32rem;
  margin: 0 0.24rem;
  font-size: 0.24rem;
  margin-top: 0.15rem;
  font-weight: bold;
}
.tanboxmm2 {
  line-height: 0.32rem;
  margin: 0 0.24rem;
  margin-top: 0.15rem;
  font-size: 0.22rem;
  color: #999;
}
.tanboxmb {
  margin-top: 0.25rem;
  width: 3.24rem;
  height: 0.4rem;
  line-height: 0.4rem;
  overflow: hidden;
}
.tanboxmb span:nth-of-type(1) {
  color: #ef3544;
  font-size: 0.32rem;
  margin-left: 0.24rem;
}
.tanboxmb span:nth-of-type(2) {
  color: #333;
  font-size: 0.24rem;
  text-decoration: line-through;
}
.tanboxmb span i{
  font-size: 0.2rem;
}
.tanboxmbb{
  font-size: 0.2rem;
  color: #bbb;
  margin: 0.1rem 0 0 0.24rem;
}
.nomore {
    text-align: center;
    color: #999999;
    line-height: 30px;
  }
</style>
<style lang="stylus" scoped>
.home-content-tab >>> .van-tabs__line{
  background-color: #5045D8 !important;
}
.home-content-tab >>> .van-tab--active{
  color: #5045D8 !important;
}
</style>



