<template>
  <div class="wrapper" v-if="goodsinfo">
    <div class="top">
      <div class="goodlistbox">
        <van-row class="goodlist" type="flex">
          <img :src="goodsinfo.goods_image" alt="">
          <van-row class="goodinfo">
            <van-row class="name more-h">{{goodsinfo.goods_name}}</van-row>
            <van-row class="sigle-h">{{goodsinfo.goods_specification}}</van-row>
            <van-row type="flex" justify="space-between">
              <van-col><span>￥{{goodsinfo.goods_price}}&nbsp;&nbsp;&nbsp;</span></van-col>
              <van-col>x{{goodsinfo.goods_count}}</van-col>
            </van-row>
          </van-row>
        </van-row>
      </div>
      <van-cell-group class="money">
        <van-cell title="退款金额" :value="'¥'+goodsinfo.goods_price"/>
      </van-cell-group>
      <van-cell-group class="mid">
        <van-row class="list" v-if="status == 1">
          <van-field label="商品状态" type="textarea" placeholder="待发货(仅需退款)" rows="1" autosize disabled/>
          <img src="./../../assets/images/icon_youjiantou.svg" alt="">
        </van-row>
        <van-row class="list" v-else>
          <van-field label="商品状态" type="textarea" :placeholder="'待收货('+(goodsinfo.is_retrun == 0 ? '仅需退款':'需退货退款')+')'" rows="1" autosize disabled/>
          <img src="./../../assets/images/icon_youjiantou.svg" alt="">
        </van-row>
        <!--<van-row class="list" @click.native="reasonshow = true">-->
          <!--<van-field label="退款原因" type="textarea" :placeholder="reason" rows="1" autosize disabled/>-->
          <!--<img src="./../../assets/images/icon_youjiantou.svg" alt="">-->
        <!--</van-row>-->
        <van-row class="list">
          <van-field v-model="reason" label="退款原因" type="textarea" placeholder="请输入退款原因" rows="1" autosize/>
        </van-row>
      </van-cell-group>
      <van-row class="input">
        <van-field v-model="message" label="留言" type="textarea" placeholder="请输入留言" rows="1" autosize/>
      </van-row>
      <van-row class="upload">
        <van-row class="title">上传图片凭证(最多4张)</van-row>
        <van-row type="flex">
          <van-col class="img" v-for="(item,index) in imglist" :key="index">
            <img :src="item" alt="">
            <img src="./../../assets/images/icon_close2.svg" alt="" class="colse" @click="delimg(index)">
          </van-col>
          <!--上传图片的框子-->
          <van-uploader :after-read="upload" accept="image/*" v-if="imglist.length !=4">
            <van-row class="uploadbox">
              <img src="./../../assets/images/icon_tjtp.svg" alt="">
              <span>上传凭证</span>
            </van-row>
          </van-uploader>
        </van-row>
      </van-row>
    </div>
    <van-row class="bottom">
      <van-button type="danger" round :disabled="disabled" class="btn" @click="btn">提交申请</van-button>
    </van-row>
    <!--提交成功-->
    <van-popup v-model="show" :close-on-click-overlay=false>
      <van-row class="popup">
        <van-row class="title" type="flex" justify="center" align="center">提交成功</van-row>
        <van-row class="content" type="flex" justify="center" align="center">我们会尽快完成审核，请耐心等待</van-row>
        <van-row>
          <van-col class="cancel" @click.native="sure">确定</van-col>
        </van-row>
      </van-row>
    </van-popup>
    <!--原因选择-->
    <van-popup v-model="reasonshow" position="bottom" class="reason">
      <van-picker show-toolbar :columns="reasonlist" @cancel="reasonshow = false" @confirm="reasonshowconfirm"
                  value-key="classify_name"/>
    </van-popup>
  </div>
</template>

<script>
  import {photoCompress} from '../../assets/js/tool'
  import {dataURLtoFile} from '../../assets/js/tool'
  import {checkempty} from "./../../assets/js/tool"
  import axios from 'axios';
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        s_id:this.$route.query.s_id,//小订单id
        status:this.$route.query.status,
        show: false,
        goodsinfo:"",
        message: "",
        disabled: true,
        imglist:[],
        reasonshow:false,
        reason:"",
        reasonlist:['拍错/不喜欢/效果不好','材质与商品描叙不符','大小尺寸与商品描叙不符','卖家发错货','收到商品少件/破损或污渍','做工粗糙/有瑕疵','颜色/款式/描叙不符'],
      }
    },
    watch: {
      message(){
        this.check()
      },
      imglist(){
        this.check()
      },
      reason(){
        this.check()
      }
    },
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      /**
       * 检查是否全填，全部填写后可以按按钮
       */
      check(){
        const that = this;
        const arr = [
          {val: that.reason, toast: "请选择原因"},
          {val: that.message,toast:'请填写退款说明'},
          {val: that.imglist,toast:'请上传图片凭证'},
        ];
        let result = true;
        for (var index = 0; index < arr.length; index++) {
           if (!checkempty(arr[index].val)) {
            result = false;
            break
          }
        }
        if (!result) {
          this.disabled = true;
          return
        }
        this.disabled = false;
      },
      reasonshowconfirm(res){
        this.reason = res;
        this.reasonshow = false;
      },
      upload(file) {
        const that = this;
        let formData = new FormData();
        let quality = Number((1048576/file.file.size).toFixed(1)); //压缩到1m以内  算出压缩比例
        // console.log(file.file.size,1048576,quality);
        photoCompress(file.file,callback,quality); //将图片进行压缩
        function callback(res){
          // console.log(dataURLtoFile(res, file.file.name).size);
          formData.append('file', dataURLtoFile(res, file.file.name));//将图片从base64转成文件
          //配置凭证
          const instance = axios.create({
            withCredentials: false
          });
          instance.post('api/my/uploadSxImg', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(res => {
            // console.log(res.data);
            if (res.data == '非法图片文件') {
              that.$toast('非法图片文件,请重新上传');
              return
            }
            that.imglist.push(res.data.data);
          });
        }
      },
      delimg(index){
        this.imglist.splice(index, 1);
      },
      sure(){
        this.$store.commit('SENDpayment',1);
        this.show = false;
        this.$router.go(-1);
      },
      btn(){
        let type = "";
       if(this.status == 1){
         type = 0;
       }else{
         if(this.goodsinfo.is_retrun == 0){
           type = 1;
         }else{
           type = 2;
         }
       }
        this.$fetch('/api/orders/apply_refund',{
          user_id:this.userInfo.id,
          order_id:this.s_id,
          refund_reason:this.reason,
          refund_intro:this.message,
          refund_images:this.imglist.join(','),
          // refund_money:this.goodsinfo.goods_price,
          type
        }).then(res=>{
          if(res.code == 1){
            this.show = true;
          }else{
            this.$toast(res.msg);
          }
        })
      }
    },
    created() {
      document.title='申请退款';
      this.$post('/api/orders/goodsinfo',{id:this.s_id}).then(res=>{
        if(res.code == 1){
          this.goodsinfo = res.data;
        }else{
          this.$toast(res.msg);
        }
      })
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    min-height: 100vh;
    background: #F5F5F5;
  }

  .top {
    padding-bottom: 1.08rem;
  }

  .goodlistbox {
    background: #ffffff;
    padding: 0.28rem;
    box-sizing: border-box;
    .goodlist {
      img {
        width: 2.28rem !important;
        height: 2.28rem !important;
        border-radius: 0.12rem;
      }
      .goodinfo {
        margin-left: 0.24rem;
        font-size: 0.24rem;
        color: #999999;
        line-height: 0.34rem;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .name {
          color: #333333;
        }
        span {
          font-size: 0.32rem;
          color: #EF3342;
        }
      }
      .goodinfo:after {
        display: none;
      }
    }
  }

  .money {
    height: 0.96rem;
    margin-top: 0.2rem;
    .van-cell {
      font-size: 0.28rem;
      padding: 0.2rem 0.3rem;
      box-sizing: border-box;
      line-height: 0.56rem;
      color: #333333;
    }
  }

  .mid {
    margin-top: 0.2rem;
    .list {
      position: relative;
      height: 0.96rem;
      .van-cell {
        height: 100%;
        padding: 0.2rem 0.28rem;
      }

      img {
        position: absolute;
        top: 0.32rem;
        right: 0.28rem;
      }
    }
  }

  .input {
    .van-cell {
      height: 100%;
      padding: 0.2rem 0.28rem;
    }
  }

  .upload {
    margin-top: 0.2rem;
    background: #ffffff;
    padding: 0.28rem;
    .title {
      font-size: 0.28rem;
      color: #333333;
    }
    .img {
      margin-top: 0.28rem;
      margin-right: 0.28rem;
      width: 1.52rem;
      height: 1.52rem;
      border-radius: 0.12rem;
      overflow: hidden;
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
      .colse {
        width: 0.4rem;
        height: 0.4rem;
        position: absolute;
        top: 0.06rem;
        right: 0.06rem;
      }
    }
    .uploadbox {
      margin-top: 0.28rem;
      width: 1.52rem;
      height: 1.52rem;
      background: #F5F5F5;
      border-radius: 0.12rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 0.24rem;
      color: #BBBBBB;
      img {
        margin-bottom: 0.08rem;
      }
    }
  }

  .bottom {
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    padding: 0.1rem 0.28rem;
    background: #ffffff;
    .btn {
      width: 100%;
      height: 0.88rem;
      font-size: 0.32rem;
    }
    .van-button--disabled {
      border: 1px solid #EBEBEB;
      color: #BBBBBB;
      background: #EBEBEB;
    }
    .van-button--danger {
      color: #fff;
      background: #EF3342;
      border: 1px solid #EF3342;
    }
  }
  .reason{
    border-radius: 0 !important;
  }
  /*提交成功*/
  .van-popup {
    border-radius: 0.4rem;
    .popup {
      width: 5.18rem;
      height: 2.24rem;
      .title {
        font-size: 0.32rem;
        color: #333333;
        margin-top: 0.6rem;
        margin-bottom: 0.2rem;
      }
      .content {
        /*height: 1.6rem;*/
        text-align: center;
        font-size: 0.28rem;
        color: #999999;
        margin-bottom: 0.6rem;
      }

    }
    .cancel {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 100%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #333333;
      border-right: 1px solid #ebebeb;
    }
  }

</style>


