<template>
  <div class="wrapper">
    <div class="top">
      <van-tabs v-model="active" :swipe-threshold=4 color="#fff" sticky animated swipeable :line-width=10>
        <van-tab title="全部">
          <div class="mid">
            <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
              <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata">
                <div v-for="(item,index) in list" :key="index">
                  <!--获得佣金-->
                  <van-row type="flex" class="box" v-if="item.type == 0">
                    <img src="./../../assets/images/icon_yongjinhuode.svg" alt="">
                    <van-row class="right">
                      <van-row class="name">{{item.title}}</van-row>
                      <van-row v-if="item.goods_name">我的分销商 <span>{{item.goods_name}}</span>分销商品获得佣金</van-row>
                      <van-row>成功分销商品获得佣金</van-row>
                      <van-row type="flex" justify="space-between" align="center" class="bottom">
                        <van-col>{{item.time}}</van-col>
                        <van-col class="red">+{{item.money}}</van-col>
                      </van-row>
                    </van-row>
                  </van-row>
                  <!--提现佣金-->
                  <van-row type="flex" class="box" v-else>
                    <img src="./../../assets/images/icon_yjtx.svg" alt="">
                    <van-row class="right">
                      <van-row class="name">{{item.title}}</van-row>
                      <van-row justify="space-between" align="center" type="flex" class="bottom">
                        <van-col>{{item.time}}</van-col>
                        <van-col class="black">-{{item.money}}</van-col>
                      </van-row>
                    </van-row>
                  </van-row>
                </div>
              </van-list>
            </van-pull-refresh>
          </div>
        </van-tab>
        <van-tab title="获取明细">
          <div class="mid">
            <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
              <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata"
                        :immediate-check=false>
                <!--获得佣金-->
                <van-row type="flex" class="box"  v-for="(item,index) in list" :key="index">
                  <img src="./../../assets/images/icon_yongjinhuode.svg" alt="">
                  <van-row class="right">
                    <van-row class="name">{{item.title}}</van-row>
                    <van-row v-if="item.goods_name">我的分销商 <span>{{item.goods_name}}</span>分销商品获得佣金</van-row>
                    <van-row>成功分销商品获得佣金</van-row>
                    <van-row type="flex" justify="space-between" align="center" class="bottom">
                      <van-col>{{item.time}}</van-col>
                      <van-col class="red">+{{item.money}}</van-col>
                    </van-row>
                  </van-row>
                </van-row>
              </van-list>
            </van-pull-refresh>
          </div>
        </van-tab>
        <van-tab title="提现明细">
          <div class="mid">
            <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
              <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata"
                        :immediate-check=false>
                <!--提现佣金-->
                <van-row type="flex" class="box"  v-for="(item,index) in list" :key="index">
                  <img src="./../../assets/images/icon_yjtx.svg" alt="">
                  <van-row class="right">
                    <van-row class="name">{{item.title}}</van-row>
                    <van-row justify="space-between" align="center" type="flex" class="bottom">
                      <van-col>{{item.time}}</van-col>
                      <van-col class="black">-{{item.money}}</van-col>
                    </van-row>
                  </van-row>
                </van-row>
              </van-list>
            </van-pull-refresh>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        list: "",
        active: 0,
        page:1,
      }
    },
    watch: {
      active(val, old) {
        this.getdata(1)
      }
    },
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.list = "";
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/my/brokerage_detail', {id: this.userInfo.id, page: this.page,type:this.active}).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = res.data.data;
            } else {
              this.list = this.list.concat(res.data.data);
            }
            if (res.data.total_row == this.list.length || res.data.data.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    created() {
      document.title = '佣金明细'
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .top >>> .van-tabs--line .van-tabs__wrap {
    height 0.8rem;
  }

  .top >>> .van-tab {
    background: #EF3342;
    font-size: 0.28rem;
    color: #F698A0;
    line-height: 0.8rem;
  }

  .top >>> .van-tab--active {
    font-size: 0.28rem;
    color: #FFFFFF;
  }

  .top >>> .van-tabs--line {
    padding-top: 0.8rem;
  }
  .top >>> .van-tab__pane{
    height: calc(100vh - 0.8rem);
    overflow :scroll;
  }
  .mid >>> .van-pull-refresh__track {
    min-height: calc(100vh - 1.36rem);
  }
</style>
<style scoped lang='less'>
  .wrapper {
    background: #F5F5F5;
    min-height: 100vh;
    width: 100%;
  }

  .mid {
    min-height: calc(100vh - 0.8rem);
    .box:nth-child(1) {
      margin-top: 0;
    }
    .box {
      margin-top: 0.2rem;
      padding: 0.28rem;
      box-sizing: border-box;
      font-size: 0.24rem;
      color: #999999;
      background: #ffffff;
      .right{
        width: 100%;
      }
      img {
        width: 0.48rem;
        height: 0.48rem;
        margin-right: 0.28rem;
      }
      .name {
        font-size: 0.28rem;
        color: #333333;
        margin-bottom: 0.2rem;
      }
      .bottom {
        margin-top: 0.2rem;
        .red {
          font-size: 0.36rem;
          color: #EF3342;
        }
        .black {
          font-size: 0.36rem;
          color: #333333;
        }
      }

      .blue {
        color: #5045D8;
      }
    }
  }

</style>


