<template>
  <div class="wrapper" v-if="info">
    <!--个人信息-->
    <div class="top">
      <van-row class="bg"></van-row>
      <div class="infobox" v-if="info">
          <img :src="info.head_image" alt="" class="pic">
        <van-row class="infomid">
          <van-row type="flex" align="center" justify="space-between">
            <van-col class="name">{{info.nickname}}</van-col>
            <van-row class="type" type="flex" align="center" justify="center">
              <van-col v-if="info.grade == 0">普通用户</van-col>
              <van-col v-if="info.grade == 1">总代</van-col>
              <van-col v-if="info.grade == 2">董事</van-col>
              <img src="./../../assets/images/icon_wenhao.svg" alt="">
            </van-row>
          </van-row>
          <van-row align="center" type="flex"><img src="./../../assets/images/i_phone.svg" alt="">{{info.telephone|phonehide}}</van-row>
          <van-row class="time">注册时间：{{info.time}}</van-row>
        </van-row>
        <van-row type="flex" align="center" justify="center">
          <van-col span="8" >
            <router-link to="/collect" class="graybg">
              <div>{{info.collect_count}}</div>
              <span>收藏</span>
            </router-link>
          </van-col>
          <van-col span="8" class="graybg">
            <div>{{info.consume_money}}</div>
            <span>消费(元)</span></van-col>
          <van-col span="8" class="graybg">
            <div>{{info.referrer}}</div>
            <span>推荐人</span></van-col>
        </van-row>
      </div>
    </div>
    <!--我的订单-->
    <div class="mid">
      <router-link to="/myorder?index=0">
        <van-row type="flex" align="center" class="title">
          <img src="./../../assets/images/icon.svg" alt="" class="circle">
          <van-col class="name">我的订单</van-col>
          <van-col>查看全部订单</van-col>
          <img src="./../../assets/images/icon_youjiantou.svg" alt="">
        </van-row>
      </router-link>
      <van-row type="flex" align="center" justify="center">
        <router-link to="/myorder?index=1">
          <van-row class="fiveequal"><img src="./../../assets/images/icon_dfk.svg" alt=""><span>待付款</span></van-row>
        </router-link>
        <router-link to="/myorder?index=2">
          <van-row class="fiveequal"><img src="./../../assets/images/icon_dfh.svg" alt=""><span>待发货</span></van-row>
        </router-link>
        <router-link to="/myorder?index=3">
          <van-row class="fiveequal"><img src="./../../assets/images/icon_dsh.svg" alt=""><span>待收货</span></van-row>
        </router-link>
        <router-link to="/myorder?index=4">
          <van-row class="fiveequal"><img src="./../../assets/images/icon_dpj.svg" alt=""><span>待评价</span></van-row>
        </router-link>
        <router-link to="/replacementlist">
          <van-row class="fiveequal"><img src="./../../assets/images/icon_shtk.svg" alt=""><span>退款/退货</span></van-row>
        </router-link>
      </van-row>
    </div>
    <!--会员中心-->
    <div class="mid" v-if="info">
      <van-row type="flex" align="center" class="title">
        <img src="./../../assets/images/icon.svg" alt="" class="circle">
        <van-col class="name">会员中心</van-col>
      </van-row>
      <van-row>
        <van-row class="border-bottom equal">
          <van-col span="12">
            <router-link to="coupon">
              <van-row type="flex" align="center" justify="space-between">
                <van-col class="flex">
                  <div>我的优惠券</div>
                  <span>共有 <span class="red">{{info.coupon_count}}</span> 个红包卡券</span>
                </van-col>
                <img src="./../../assets/images/icon_youhuiq.svg" alt="" class="marginright">
              </van-row>
            </router-link>
          </van-col>
          <van-col span="12" class="border-left">
            <router-link to="/wallet">
              <van-row type="flex" align="center" justify="space-between">
                <van-col class="marginleft flex">
                  <div>我的钱包</div>
                  <span>余额：¥{{info.balance}}</span>
                </van-col>
                <img src="./../../assets/images/icon_qianbao.svg" alt="">
              </van-row>
            </router-link>
          </van-col>
        </van-row>
        <van-row class="equal">
          <van-col span="12">
            <router-link to="/integral">
              <van-row type="flex" align="center" justify="space-between">
                <van-col class="flex">
                  <div>我的积分</div>
                  <span>积分余额：{{info.integral}}</span>
                </van-col>
                <img src="./../../assets/images/icon_jifen.svg" alt="" class="marginright">
              </van-row>
            </router-link>
          </van-col>
          <van-col span="12" class="border-left">
            <router-link to="/addresslist">
              <van-row type="flex" align="center" justify="space-between">
                <van-row class="marginleft flex">
                  <div>收货地址</div>
                </van-row>
                <img src="./../../assets/images/icon_shouhuodizhi.svg" alt="">
              </van-row>
            </router-link>
          </van-col>
        </van-row>
      </van-row>
    </div>
    <!--分销中心-->
    <div class="mid" v-if="info.grade == 2 || info.grade == 1">
      <van-row type="flex" align="center" class="title">
        <img src="./../../assets/images/icon.svg" alt="" class="circle">
        <van-col class="name">分销中心</van-col>
      </van-row>
      <van-row class="moneylist">
        <van-col span="12" class="">佣金余额：<span>¥{{info.brokerage|formatMoney}}</span></van-col>
        <van-col span="12" class=""> 待结算：<span>¥{{info.unbrokerage|formatMoney}}</span></van-col>
      </van-row>
      <van-row>
        <router-link to="/commission">
          <van-col span="8" class="threeequal">
            <img src="./../../assets/images/icon_yongjintiqu.svg" alt=""><span>佣金提现</span>
          </van-col>
        </router-link>
        <router-link to="/logisticsdetails">
          <van-col span="8" class="threeequal"><img src="./../../assets/images/icon_yjmx.svg" alt=""><span>佣金明细</span>
          </van-col>
        </router-link>
        <router-link :to="{path:'/mydistributor',query:{pic:info.head_image,myid:info.grade}}">
          <van-col span="8" class="threeequal">
            <img src="./../../assets/images/icon_fxs.svg" alt=""><span>分销商</span>
          </van-col>
        </router-link>
      </van-row>
      <van-row>
        <router-link to="/distributedorder">
          <van-col span="8" class="threeequal">
            <img src="./../../assets/images/icon_fxdingdan.svg" alt=""><span>分销订单</span>
          </van-col>
        </router-link>
        <router-link to="/rank">
        <van-col span="8" class="threeequal">
          <img src="./../../assets/images/icon_phb.svg" alt=""><span>销量排行榜</span>
        </van-col>
        </router-link>
        <router-link :to="{path:'/myvip',query:{pic:info.head_image,myid:info.grade}}">
          <van-col span="8" class="threeequal">
            <img src="./../../assets/images/icon_wdhy.svg" alt=""><span>我的会员</span>
          </van-col>
        </router-link>
      </van-row>
    </div>
  </div>
</template>

<script>
  import './../../assets/js/tool'
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        info:""
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {},
    created() {
    },
    mounted() {
      document.title = '个人中心';
      this.$fetch('/api/my/personalinfo',{id:this.userInfo.id}).then(res=>{
        console.log(res);
        if(res.code == 1){
          this.info = res.data;
        }else{
          this.$toast(res.msg)
        }
      })
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    margin-bottom: 1rem;
    padding-bottom:1rem;
  }

  .top {
    .bg {
      height: 2.2rem;
      width: 100%;
      background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    }
    .infobox {
      height: auto;
      margin: 0 0.28rem;
      margin-top: -1.42rem;
      padding: 0.74rem 0 0.2rem;
      box-sizing: border-box;
      background: #FFFFFF;
      box-shadow: 0 0.12rem 0.24rem 0 rgba(215, 215, 215, 0.50);
      border-radius: 0.2rem;
      position: relative;
      .pic {
        width: 1.28rem;
        height: 1.28rem;
        position: absolute;
        top: -0.64rem;
        left: 0.4rem;
        border-radius: 50%;
        border: 0.04rem solid #FFFFFF;
      }
      .infomid {
        line-height: 0.38rem;
        font-size: 0.24rem;
        padding: 0.2rem 0.4rem 0.24rem 0.32rem;
        .name {
          font-size: 0.36rem;
          color: #333333;
          line-height: 0.5rem;
        }
        .type {
          background: #5045D8;
          border-radius: 0.2rem;
          padding: 0 0.2rem;
          font-size: 0.24rem;
          color: #FFFFFF;
          height: 0.4rem;
          img {
            width: 0.284rem;
            height: 0.284rem;
            text-align: center;
            border-radius: 50%;
            color: #5045D8;
            background: #FFFFFF;
            margin-left: 0.1rem;
          }
        }
        .time {
          color: #BBBBBB;
        }
      }
      .graybg {
        background: #F5F5F5;
        height: 1.2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        div {
          max-width: 100%;
          font-size: 0.32rem;
          color: #333333;
          line-height: 0.44rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        span {
          font-size: 0.24rem;
          color: #BBBBBB;
        }
      }
    }
  }

  .mid {
    margin-top: 0.2rem;
    background-color: #ffffff;
    .title {
      height: 0.8rem;
      padding: 0 0.32rem;
      font-size: 0.24rem;
      color: #999999;
      .circle {
        margin-left: 0;
      }
      .name {
        font-size: 0.28rem;
        color: #333333;
        line-height: 0.4rem;
        margin: 0 0.4rem 0 0.16rem;
      }
      img {
        margin-left: 0.12rem;
      }
    }
    /*5等分*/
    .fiveequal {
      width: 1.5rem;
      height: 1.72rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      img {
        width: 0.64rem;
        height: 0.64rem;
        margin-bottom: 0.08rem;
      }
    }
    //平分
    .equal {
      height: 1.24rem;
      padding: 0 0.28rem;
      img{
        width: 0.76rem;
        height: 0.76rem;
      }
      div {
        font-size: 0.24rem;
        color: #333333;
        line-height: 0.34rem;
      }
      span {
        font-size: 0.2rem;
        color: #999999;
        line-height: 0.28rem;
        .red {
          color: #EF3342;
        }
      }
      .van-col {
        height: 100%;
      }
      .van-row {
        height: 100%;
      }
    }
    .marginleft {
      margin-left: 0.6rem;
    }
    .marginright {
      margin-right: 0.6rem;
    }
    .flex {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .moneylist {
      height: 0.96rem;
      background: #FFFFFF;
      box-shadow: 0 0 0.24rem 0 rgba(215, 215, 215, 0.50);
      padding: 0 0.28rem;
      box-sizing: border-box;
      font-size: 0.24rem;
      color: #999999;
      line-height: 0.96rem;
      span {
        font-size: 0.28rem;
        color: #333333;
      }
    }
    .threeequal {
      height: 1.6rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 0.24rem;
      color: #333333;
      img {
        width: 0.64rem;
        height: 0.64rem;
        margin-bottom: 0.08rem;
      }
    }
  }
</style>


