<template>
  <div class="wrapper">
    <div class="cart-t" v-show="cartinfo.length!=0">
      <div class="cart-tm">
        共{{count}}件商品
      </div>
    </div>
    <div class="cart-c">
      <div class="cart-cm" v-for="(item,index) in cartinfo" :key="index">
        <div class="cart-cml">
          <img src="../../assets/images/icon_weixuan.svg" alt="" v-if="item.selected==0" @click="selected(index)">
          <img src="../../assets/images/icon_xuanzhong.svg" alt=""  v-if="item.selected==1" @click="selected1(index)">
        </div>
        <div class="cart-cmm">
          <img :src="item.img" alt="">
        </div>
        <div class="cart-cmr">
          <div class="cart-cmrt">
            <div class="cart-cmrtl more-h" style=" -webkit-box-orient: vertical;">
              {{item.name}}
            </div>
            <div class="cart-cmrtr"  @click="delectgood(item.id)">
              <img src="../../assets/images/icon_delete.svg" alt="">
            </div>
          </div>
          <div class="cart-cmrm" @click="changguige(item.goods_id,index,item.id,item.goods_specification)">
            <span class="sigle-h">{{item.goods_specification}}</span> <img src="../../assets/images/icon_ggg.svg" alt="">
          </div>
          <div class="cart-cmrb">
            <div class="cart-cmrbl">
              ¥{{item.goods_price}}
            </div>
            <div class="cart-cmrbr">
              <span @click="reduce(item.attr_id,item.goods_id,item.goods_specification,item.goods_price,index,item.stock,item.min)">-</span><input type="number" v-model="num[index]" @change="inputchange(item.attr_id,item.goods_id,item.goods_specification,item.goods_price,index,item.stock,item.min)">
              <span @click="add(item.attr_id,item.goods_id,item.goods_specification,item.goods_price,index,item.stock,item.min)">+</span>
            </div>
          </div>
        </div>
      </div>
      <div class="cartcm" v-show="cartinfo.length==0">
        <img src="../../assets/images/icon_gwcwsp.svg" alt="">
        <p>购物车是空的</p>
        <p>你还没有选择任何商品哦</p>
      </div>
      <!-- 正常商品 -->
      <div class="invalid" v-show="invalid.length!=0">
        <div class="invalid-t">
          <div class="invalid-tl">
            失效商品{{invalidcount}}件
          </div>
          <div class="invalid-tr" @click="clearinvalid">
            清空失效商品
          </div>
        </div>
        <div class="invalid-c">
          <div class="invalid-cm" v-for="(item,index) in invalid" :key="index" v-show="invalid.length>0">
            <div class="invalid-cml">
              <img :src="item.img" alt="">
              <span>已失效</span>
            </div>
            <div class="invalid-cmr">
              <div class="invalid-cmrt more-h" style=" -webkit-box-orient: vertical;">
               {{item.name}}
              </div>
              <div class="invalid-cmrb">
                商品已不能购买
              </div>
            </div>
          </div>
          <div class="invalidcm" v-show="invalid.length==0">
            暂无失效商品
          </div>
        </div>
      </div>
      <!-- 失效商品 -->
    </div>
    <div class="dibu">
      <div class="dibu-l" @click="selectall">
        <img src="../../assets/images/icon_weixuan.svg" alt="" v-if="alselect">
        <img src="../../assets/images/icon_xuanzhong.svg" alt="" v-else>
        <span>全选</span>
      </div>
      <div class="dibu-m">
        合计：
        <span>¥{{totalprice}}</span>
      </div>
      <div class="dibu-r" @click="gopayorder">
        结算({{goods_num}})
      </div>
    </div>
    <div class="guige-popup">
          <van-popup v-model="show" position="bottom" :overlay="true">
            <div class="guige-popupt">
               <div class="guige-popuptl">
                    <div class="guige-popuptll">
                        <img :src="assessment_detail0.price" alt="">
                    </div>
                    <div class="guige-popuptlr">
                         <p>￥{{assessment_detail0.price}}</p>
                         <p class="sigle-h" v-show="subIndex.length<=0">请选择&nbsp;&nbsp;x{{num[guigeindex]}}</p>
                         <p class="sigle-h" v-show="subIndex.length>0">已选：{{guige}} &nbsp;&nbsp;x{{num[guigeindex]}}</p>
                    </div>
                </div>
                <div class="guige-popuptr" @click="closeshowup">
                    <img src="../../assets/images/icon_close.svg" alt="">
                </div>
            </div>
            <div class="guige-popupc-wrap">
               <div class="guige-popupc" v-for="(item,n) in assessment" :key="n">
                 <div class="guige-popupct">
                     {{item.name}}
                 </div>
                 <div class="guige-popupcc">
                     <div class="guige-popupccm" v-for="(items,index) in item.attribute" :key="index" 
                     v-on:click="specificationBtn(items.name,n,$event,index)" 
                     v-bind:class="[items.isShow?'':'noneActive',subIndex[n] == index?'productActive':'']">
                         {{items.name}}
                     </div>
                 </div>
               </div>
            </div>
            <div class="guige-popupnb">
                <div class="guige-popupnbl" @click="surechange">
                     确定
                </div>
                <div class="guige-popupnbr" @click="golistdetail">
                    查看详情
                </div>
            </div>
          </van-popup>
       </div>
    <!-- 规格弹窗 -->
    <div class="addpopup">
      <van-popup v-model="showdelect">
          <div class="vanbox">
             <div class="vanboxt">
                 确定要删除购物车商品吗？
             </div>
             <div class="vanboxb">
                <div class="vanboxbl" @click="closedelect">
                    取消
                </div>
                <div class="vanboxbr" @click="suredelect">
                    确定
                </div>
             </div>
          </div>
      </van-popup>
     </div>
     <!-- 弹出提示地址 -->
  </div>
</template>

<script>
  import {mapGetters} from "vuex";
export default {
  
  components: {},
  props: {},
  data() {
    return {
      count:"",//购物车商品数量
      invalidcount:"",//购物车失效商品数量
      num: [], //加减数
      show: false, //规格弹窗
      alselect: true ,//全选
      showdelect:false,//删除提示
      cartinfo:[],//购物车列表
      invalid:[],//失效商品列表
      goods_id:'',//商品id
      goods_attribute:"请选择",//请选择规格
      assessment:[],//规格属性
      assessment_detail:[],//规格匹配所有属性组合
      guige:"",//已选规格
      assessment_detail0:'',//
      selectArr: [], //存放被选中的值
      shopItemInfo: {}, //存放要和选中的值进行匹配的数据
      subIndex: [], //是否选中 因为不确定是多规格还是但规格，所以这里定义数组来判断
      guigeindex:"",//点击的第几个商品的下标改变规格
      guigeobj:"",//改变规格对象
      delectid:"",//删除商品的id
      attr_id:"",//商品的各种规格
      goods_price:"",//商品的各种价格
      totalprice:0,//总价（不包括优惠券）
      goods_num:"0",//结算商品的数量
    };
  },
  watch: {},
  computed: {
     ...mapGetters(['userInfo']),
  },
  methods: {
    inputchange(attr_id,goods_id,goods_specification,goods_price,index,stock,min){
       console.log(this.num[index],stock)
       if(this.num[index]>stock){
          this.num[index]=stock 
          this.$toast({
            message:'这已经是最大库存了',
            duration:1000
          })
       }else if (this.num[index]<min) {
         this.num[index]=min 
         this.$toast({
            message:'不能在减了',
            duration:1000
          })
       }
            this.alselect =true
            var user_id = 1
            var goods_id = goods_id
            var goods_specification = goods_specification
            var goods_price = goods_price
            var attr_id = attr_id
            var count = this.num[index]
            this.$fetch("/api/cart/add_cart",{user_id:user_id,goods_id:goods_id,attr_id:attr_id,goods_price:goods_price,goods_specification:goods_specification,count:count}).then(response => {
               console.log("response1:",response);
               if(response.code==1){
                  var id = this.userInfo.id
                  this.$post("/api/cart/cart_list",{id:id}).then(response => {
                    console.log("response1:",response);
                    this.cartinfo = response.data.cartinfo
                    if(response.code==1){
                      this.cartinfo.forEach((el)=>{
                              el.selected=0
                      })
                    }
                    this.totalpriceclick()
                    this.count = response.data.count
                  });
                }else if (response.code==2) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==3) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==4) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }
            })
            this.$forceUpdate(); //重绘
    },
    reduce(attr_id,goods_id,goods_specification,goods_price,index,stock,min) {
            //  this.alselect =true
      console.log(attr_id,goods_id,goods_specification,goods_price,index,stock,min,this.num[index])
            if(this.num[index]<=min){
             this.num[index] = min 
              this.$toast({
                message:'不能在减了',
                duration:1000
              })
            }else{
               this.num[index]--
            }
            if(this.num[index]>stock||this.num[index]<min){
               return 
            } 
            
            var user_id = 1
            var goods_id = goods_id
            var goods_specification = goods_specification
            var goods_price = goods_price
            var attr_id = attr_id
            var count = this.num[index]
            this.$fetch("/api/cart/add_cart",{user_id:user_id,goods_id:goods_id,attr_id:attr_id,goods_price:goods_price,goods_specification:goods_specification,count:count}).then(response => {
               console.log("response1:",response);
               if(response.code==1){
                  var id = this.userInfo.id
                  this.$post("/api/cart/cart_list",{id:id}).then(response => {
                    console.log("response1:",response);
                    // this.cartinfo = response.data.cartinfo
                    this.cartinfo[index].count = count
                    // if(response.code==1){
                    //   this.cartinfo.forEach((el)=>{
                    //           el.selected=0
                    //   })
                    // }
                    this.totalpriceclick()
                    this.count = response.data.count
                  });
                }else if (response.code==2) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==3) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==4) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }
            })
            this.$forceUpdate(); //重绘

    },
    add(attr_id,goods_id,goods_specification,goods_price,index,stock,min) {
      console.log(attr_id,goods_id,goods_specification,goods_price,index,stock,min)
      console.log(this.num)
            if(this.num[index]>=stock){
                return this.$toast({
                          message:'这已经是最大库存了',
                          duration:1000
                        })
            }
            this.num[index]++
            // this.alselect =true
            // console.log(attr_id,goods_id,goods_specification,goods_price)
            var user_id = this.userInfo.id
            var goods_id = goods_id
            var goods_specification = goods_specification
            var goods_price = goods_price
             var attr_id = attr_id
            var count = this.num[index]
            // console.log(this.num[index])
            this.$fetch("/api/cart/add_cart",{user_id:user_id,goods_id:goods_id,attr_id:attr_id,goods_price:goods_price,goods_specification:goods_specification,count:count}).then(response => {
              console.log("http://localhost:8080/#/index",response)
                if(response.code==1){
                  // this.num[index]++
                  var id = this.userInfo.id
                  this.$post("/api/cart/cart_list",{id:id}).then(response => {
                    console.log("response1:",response);
                    // this.cartinfo = response.data.cartinfo
                    this.cartinfo[index].count = count
                    // if(response.code==1){
                    //   this.cartinfo.forEach((el)=>{
                    //           el.selected=0
                    //   })
                    // }
                    this.totalpriceclick()
                    this.count = response.data.count
                  });
                }else if (response.code==2) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==3) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }else if (response.code==4) {
                            this.$toast({
                                message:response.msg,
                                duration:1000
                             })
                }
            })
         this.$forceUpdate(); //重绘


    },
    changguige(goods_id,index,id,goods_specification) {
      this.guigeindex = index
      this.show = true;
      this.goods_id = goods_id
      this.guigeobj = {id:id,goods_specification:goods_specification,goods_id:goods_id}
      this.$post("/api/index/goods_attribute",{id:goods_id}).then(response => {
            // console.log("response：",response)
            // this.goods_detail = response.data
            this.assessment= response.data.assessment
            this.assessment_detail = response.data.assessment_detail
           this.assessment_detail0=this.assessment_detail[0]
            console.log("123",this.assessment_detail)
            var self = this;
            for (var i in self.assessment_detail) {
                self.shopItemInfo[self.assessment_detail[i].assessment_detail] = self.assessment_detail[i]; //修改数据结构格式，改成键值对的方式，以方便和选中之后的值进行匹配
                // console.log("this.shopItemInfo:",self.shopItemInfo)
            }
            self.checkItem();
      });
    },
    surechange(){
       this.show=false;
       var selectArr = this.selectArr.join(',')
       if(this.selectArr.length!=this.assessment.length){
          return   this.$toast({
                    message:'请选择规格',
                    duration:1000
                  })         
       }
       console.log(this.selectArr)
       if(this.selectArr.length==this.assessment.length){
            for(var i in this.selectArr){
              if(this.selectArr[i]==''){
               return  this.$toast({
                        message:'请选择规格',
                        duration:1000
                      })     
                  } 
            }
       }
       this.assessment_detail.forEach((el)=>{
          if(selectArr==el.assessment_detail){
            console.log("1",selectArr,el.assessment_detail)
            this.attr_id = el.id
            this.goods_price = el.price
            console.log(this.goods_price)
          }
       })
       var user_id = this.userInfo.id
       var goods_id =this.goods_id
       this.$fetch("/api/cart/update_cart_attr",{id:this.guigeobj.id,attr_id:this.attr_id,goods_specification:selectArr,user_id:user_id,goods_id:goods_id,goods_price:this.goods_price}).then(response => {
          console.log("response3:",response);
          if(response.code== 1){
            this.$post("/api/cart/cart_list",{id:user_id}).then(response => {
              console.log("response1:",response);
              this.cartinfo = response.data.cartinfo
              this.cartinfo.forEach((el)=>{
                el.selected=0
              })
              this.alselect = true;
              this.totalpriceclick()
              this.count = response.data.count
              var selectArr = this.selectArr.join(',')
              this.assessment_detail.forEach(el=>{
                    if(selectArr==el.assessment_detail){
                        this.num[this.guigeindex] = el.sell_num 
                    }
                })
            });
          }else{
            this.$toast({
                      message:response.msg,
                      duration:1000
                    })
                    return
          }
       });
      
    },
    closeshowup() {
      this.show = false;
      console.log(this.subIndex)
      this.subIndex.forEach((el) => {
        el = -1
      });
      console.log(this.subIndex)
    },
    selected(index){
      console.log(index)
      this.cartinfo[index].selected=1
      this.$forceUpdate(); //重绘
      var selectarr = []
      this.cartinfo.forEach((el)=>{
         if(el.selected==1){
           selectarr.push(el.selected)
         }
      })
      console.log(selectarr)
      if(this.cartinfo.length==selectarr.length){
         this.alselect = false;
      }else{
        this.alselect = true;
      }
      this.totalpriceclick()
            this.goodsnum()
    },
    selected1(index){
        this.cartinfo[index].selected=0
        this.$forceUpdate(); //重绘
        var selectarr = []
      this.cartinfo.forEach((el)=>{
         if(el.selected==1){
           selectarr.push(el.selected)
         }
      })
      console.log(selectarr)
      if(this.cartinfo.length==selectarr.length){
         this.alselect = false;
      }else{
        this.alselect = true;
      }
      this.totalpriceclick()
      this.goodsnum()
    },
    golistdetail() {
      var id = 1
      var goods_id = this.goods_id
      this.$router.push({ path: "/listdetail",query:{id:id,goods_id:goods_id}});
    },
    selectall() {
      if (this.alselect == true) {
        this.alselect = false;
        this.cartinfo.forEach((el)=>{
          el.selected=1
        })
      } else {
        this.alselect = true;
         this.cartinfo.forEach((el)=>{
          el.selected=0
        })
      }
      this.totalpriceclick()
      this.goodsnum()
    },
    goodsnum(){
      this.goods_num = 0
      this.cartinfo.forEach(el=>{
        if(el.selected==1){
          this.goods_num +=1 
        }
      })
    },
    delectgood(id){
      this.showdelect=true
      this.delectid = id
    },
    closedelect(){
      this.showdelect=false
    },
    suredelect(){
      console.log(this.cartinfo)
      this.showdelect=false
      this.alselect = true;
      var id = this.delectid
      this.$post("/api/cart/del_cart",{id:id}).then(response => {
        console.log(response);
        var id = this.userInfo.id
        this.$post("/api/cart/cart_list",{id:id}).then(response => {
          console.log("suredelect:",response);
          this.cartinfo = response.data.cartinfo
          this.cartinfo.forEach((el)=>{
            el.selected=0
          })
          this.count = response.data.count
        });
      });
      this.goods_num = 0
    },//删除单个商品
    clearinvalid(){
      var delarr = []
      this.invalid.forEach((el)=>{
          delarr.push(el.id)
      })
      var id = delarr.join(',')
      if(this.invalid.length==0){
        return
      }else{
          this.$post("/api/cart/del_cart",{id:id}).then(response => {
            console.log(response);
                if(response.code==1){
                  this.$toast({
                    message:response.msg,
                    duration:1000
                  })
                }            
            var type = 1
            this.$post("/api/cart/cart_list",{id:this.userInfo.id,type:type}).then(response => {
              console.log(response);
              this.invalid = response.data.cartinfo
              this.invalidcount =response.data.count
            });
            console.log(this.invalid.length)
        });
      }
      
    },//删除失效商品
    specificationBtn: function (item, n, event, index) {
      console.log(item, n, event, index)
            var self = this;
            if (self.selectArr[n] != item) {
                self.selectArr[n] = item;
                self.subIndex[n] = index; 
            } else {
                self.selectArr[n] = "";
                self.subIndex[n] = -1; //去掉选中的颜色 
            }
            var selectArr=self.selectArr.join(',')
            var selectArr1=self.selectArr.join(' ')
             this.guige = selectArr1
             console.log(selectArr)
             if(selectArr==","){
                self.subIndex.length=0 
             }
            self.assessment_detail.forEach((el)=>{
              if(el.assessment_detail==selectArr){
                 this.assessment_detail0=el
              }
            })
            self.checkItem();
    },
    checkItem: function () {
        var self = this;
        var option = self.assessment;
        var result = [];  //定义数组存储被选中的值
        for (var i in option) {
            result[i] = self.selectArr[i] ? self.selectArr[i] : '';
        }
        for (var i in option) {
            var last = result[i];  //把选中的值存放到字符串last去
            for (var k in option[i].attribute) {
                result[i] = option[i].attribute[k].name; //赋值，存在直接覆盖，不存在往里面添加name值
                option[i].attribute[k].isShow = self.isMay(result); //在数据里面添加字段isShow来判断是否可以选择
            }
            result[i] = last; //还原，目的是记录点下去那个值，避免下一次执行循环时避免被覆盖

        }
        self.$forceUpdate(); //重绘
    },
    isMay: function (result) {
        for (var i in result) { 
            if (result[i] == '') {
                return true; //如果数组里有为空的值，那直接返回true
            }
        }
        return this.shopItemInfo[result].num == 0 ? false : true; //匹配选中的数据的库存，若不为空返回true反之返回false
    },
    changeCount(){
        if(this.assessment_detail0.num<this.num){
            this.num = this.assessment_detail0.num
        }
    },
    totalpriceclick(){
      console.log(this.cartinfo)
           this.totalprice = 0
           this.cartinfo.forEach((el)=>{
                if(el.selected==1){
                  this.totalprice =(Number(this.totalprice)+Number(el.goods_price)*el.count).toFixed(2)
                }    
            })
    },
    gopayorder(){
      var arr = []
      if(this.goods_num==0){
        return   this.$toast({
                    message:'请选择商品',
                    duration:1000
                  })
      }else{
        this.cartinfo.forEach((el)=>{
          if(el.selected==1){
            arr.push(el.id)
          }
        })
       var id = this.userInfo.id
        var cart_id = arr.join(',')
        this.$router.push({path:"/placeordercart",query:{cart_id:cart_id,id:id}})
      }
      this.$store.commit("SENDcoupon",'');
    }
  },
  created() {},
  mounted() {
    console.log("cart:",this.userInfo.id)
    document.title = "购物车";
    var id = this.userInfo.id
    this.$post("/api/cart/cart_list",{id:id}).then(response => {
      console.log("response1:",response);
      this.cartinfo = response.data.cartinfo
      this.cartinfo.forEach((el)=>{
        this.num.push(el.count)
      })
      if(response.code==1){
        this.cartinfo.forEach((el)=>{
                el.selected=0
        })
      }
      console.log(this.num)
      this.totalpriceclick()
      this.count = response.data.count
    });
    var type = 1
    this.$post("/api/cart/cart_list",{id:id,type:type}).then(response => {
      console.log("response2:",response);
      this.invalid = response.data.cartinfo
      this.invalidcount =response.data.count
    });
  }
};
