<template>
  <div class="wrapper">
    <div class="order-address">
      <div class="order-address-l">
        <div class="order-address-ll">
          <img src="../../assets/images/icon_xzshdz.svg" alt="">
        </div>
        <div class="order-address-lr">
          <p>{{address.name}} {{address.telephone}}</p>
          <p>{{address.address}}</p>
        </div>
        <!-- <div class="order-address-lr">
               <p>您暂未添加任何地址</p>               
            </div> -->
      </div>
      <div class="order-address-r" @click="goaddresslist">
        <img src="../../assets/images/icon_youjiantou.svg" alt="">
      </div>
    </div>
    <!-- 地址 -->
    <div class="prder-good">
      <div class="prder-goodm" v-for="(item,index) in mustbuy.goodsinfo" :key="index">
        <div class="prder-goodml">
          <img :src="item.goods_image" alt="">
        </div>
        <div class="prder-goodmr">
          <div class="prder-goodmrt more-h" style=" -webkit-box-orient: vertical;">
            {{item.goods_name}}
          </div>
          <div class="prder-goodmrm">
            {{item.goods_specification}}&nbsp;&nbsp;×{{mustbuy.goodsinfo[0].goods_count}}
          </div>
          <div class="prder-goodmrb">
            <div class="prder-goodmrbl">
              ￥{{item.goods_price}}
            </div>
            <!-- <div class="prder-goodmrbr">
                            <span @click="reduce">-</span><input type="number" v-model="num">
                            <span @click="add">+</span>
                        </div> -->
          </div>
        </div>
      </div>

    </div>
    <!-- 商品 -->
    <div class="discount">
      <div class="discount-l">
        优惠
      </div>
      <div class="discount-r" @click="gocoupon" v-if="gocouponshow==false">
        <img src="../../assets/images/icon_youjiantou.svg" alt="">
      </div>
      <div class="discount-r" @click="gocoupon" v-if="gocouponshow==true">
        {{coupons.limit_money}}
      </div>
    </div>
    <!-- 优惠 -->
    <div class="discount">
      <div class="discount-l">
        运费
      </div>
      <div class="discount-r">
        ￥{{mustbuy.freight_price}}
      </div>
    </div>
    <!-- 运费 -->
    <div class="discount1">
      <div class="discount1-l">
        买家留言
      </div>
      <div class="discount1-r">
        <input type="text" placeholder="选填" v-model="inp">
      </div>
    </div>
    <!-- 留言 -->
    <div class="dibu">
      <div class="dibu-l">
        共{{mustbuy.goodsinfo[0].goods_count}}件商品
      </div>
      <div class="dibu-m">
        合计：
        <span>￥{{total_price}} </span>
      </div>
      <div class="dibu-r" @click="pay">
        去支付
      </div>
    </div>
    <!-- 底部 -->
    <div class="addpopup">
      <van-popup v-model="show">
        <div class="vanbox">
          <div class="vanboxt">
            您暂未添加任何收货地址是否现在设置？
          </div>
          <div class="vanboxb">
            <div class="vanboxbl" @click="close">
              取消
            </div>
            <div class="vanboxbr" @click="goaddress">
              确定
            </div>
          </div>
        </div>
      </van-popup>
    </div>
    <!-- 弹出提示地址 -->
    <div class="paypopup-wrap">
      <van-popup v-model="showup" position="bottom" :overlay="true">
        <div class="paypopup">
          <div class="paypopup-close">
            <div class="paypopup-closel">
            </div>
            <div class="paypopup-closel" @click="payclose">
              <img src="../../assets/images/icon_close.svg" alt="">
            </div>

          </div>
          <div class="paypopup-order">
            支付订单
          </div>
          <div class="paypopup-money">
            ¥{{total_price}}
          </div>
          <div class="paypopup-balance">
            <div class="paypopup-balancel">
              <img src="../../assets/images/icon_yezf.svg" alt="">
              <span>余额支付</span>
            </div>
            <div class="paypopup-balancer" @click="xuanz1">
              <img src="../../assets/images/icon_weixuan.svg" alt="" v-if="xuanzhong1">
              <img src="../../assets/images/icon_xuanzhong.svg" alt="" v-else>
            </div>
          </div>
          <div class="paypopup-weixin">
            <div class="paypopup-weixinl">
              <img src="../../assets/images/icon_wxzf.svg" alt="">
              <span>微信支付</span>
            </div>
            <div class="paypopup-weixinr" @click="xuanz2">
              <img src="../../assets/images/icon_weixuan.svg" alt="" v-if="xuanzhong2">
              <img src="../../assets/images/icon_xuanzhong.svg" alt="" v-else>
            </div>
          </div>
          <div class="paypopup-pay" @click="gopay">
            立即支付
          </div>
        </div>
      </van-popup>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  components: {},
  props: {},
  data() {
    return {
      // num: "1", //加减数
      inp: "", //留言
      show: false, //提示地址信息
      showup: false, //支付弹窗
      xuanzhong1: false, //
      xuanzhong2: true, //
      mustbuy: "", //立即购买返回的数据
      address: {}, //地址
      orderdata: "", //生成订单返回的数据
      coupons: {
        id: 0,
        limit_money: 0
      }, //优惠券
      gocouponshow: false, //优惠券存在？
      total_price: "" //支付价格
    };
  },
  watch: {},
  computed: {
    ...mapGetters(["selectaddress", "coupon", "userInfo"])
  },
  methods: {
    close() {
      this.show = false;
    },
    goaddress() {
      var address = 1;
      this.$router.push({ path: "/addresslist", query: { address: address } });
    },
    goaddresslist() {
      var address = 1;
      this.$router.push({ path: "/addresslist", query: { address: address } });
    },
    gocoupon() {
      var totalprice =this.mustbuy.goodsinfo[0].goods_price;
      this.$router.push({ path: "/coupon", query: { totalprice: totalprice } });
    },
    pay() {
      console.log(this.coupon);
      this.showup = true;
      var user_id = this.$route.query.id;
      var attr_id = this.$route.query.attr_id;
      var count = this.$route.query.count;
      var goods_specification = this.mustbuy.goodsinfo[0].goods_specification;
      var cart_id = "";
      var goodsinfo = this.mustbuy.goodsinfo;
      console.log(goodsinfo);
      var address_id = this.mustbuy.address_id;
      var goods_id = this.mustbuy.goodsinfo[0].goods_id;
      var address = this.mustbuy.address;
      var name = this.mustbuy.name;
      var telephone = this.mustbuy.telephone;
      var goods_price = this.mustbuy.goods_total_price;
      var coupon_id = this.coupons.id;
      var remark = this.inp;
      this.$post("/api/orders/create_orders", {
        user_id: user_id,
        attr_id: attr_id,
        count: count,
        cart_id,
        user_id,
        address_id,
        address,
        name,
        telephone,
        goods_price,
        goods_id,
        coupon_id,
        remark,
        goodsinfo,
        goods_specification
      }).then(response => {
        console.log("response", response);
        this.orderdata = response.data;
        this.$store.commit("SENDcoupon", "");
      });
    },
    payclose() {
      this.showup = false;
    },
    xuanz1() {
      this.xuanzhong1 = false;
      this.xuanzhong2 = true;
    },
    xuanz2() {
      this.xuanzhong1 = true;
      this.xuanzhong2 = false;
    },
    gopay() {
      if (this.xuanzhong1 == false) {
        var id = this.userInfo.id;
        var orders_num = this.orderdata.orders_num;
        var type = 0;
        this.$post("/api/orders/ordersinfo", {
          type,
          id,
          orders_num
        }).then(response => {
          console.log(response);
          if (response.code == 1) {
            this.$toast({
              message: response.msg,
              duration: 1000
            });
            this.showup = false;
            this.$router.go(-1);
          }else{
            this.$toast({
              message: response.msg,
              duration: 1000
            });
          } 
        });
      } else if (this.xuanzhong2 == false) {
        var that = this
        var orders_num = this.orderdata.orders_num;
        this.$post("/api/pay/pay", {
        orders_num:orders_num
        }).then(res => {
          console.log("res:",res);
          callpay();
          //调用微信JS api 支付
          function jsApiCall() {
            WeixinJSBridge.invoke("getBrandWCPayRequest",JSON.parse(res), function(res) {
              if (res.err_msg == "get_brand_wcpay_request:ok") {
                that.showup = false;
                that.xuanzhong1 = false;
                that.xuanzhong2 = false;
                that.$router.go(-1)
              } else if (res.err_msg == "get_brand_wcpay_request:cancel" || res.err_msg == "get_brand_wcpay_request:fail") {
                alert("支付失败!");
              }
            });
          }
          function callpay() {
            if (typeof WeixinJSBridge == "undefined") {
              if (document.addEventListener) {
                document.addEventListener(
                  "WeixinJSBridgeReady",
                  jsApiCall,
                  false
                );
              } else if (document.attachEvent) {
                document.attachEvent("WeixinJSBridgeReady", jsApiCall);
                document.attachEvent("onWeixinJSBridgeReady", jsApiCall);
              }
            } else {
              jsApiCall();
            }
          }
        });
      }
    }
  },
  created() {},
  mounted() {
    console.log(this.coupon);
    if (this.coupon) {
      console.log(this.coupon);
      this.coupons = this.coupon;
      this.gocouponshow = true;
    } else {
      this.gocouponshow = false;
    }
    if (this.selectaddress) {
      var address_id = this.selectaddress.address_id;
    } else {
      var address_id = 0;
    }
    document.title = "提交订单";
    var id = this.$route.query.id;
    var attr_id = this.$route.query.attr_id;
    var count = this.$route.query.count;
    var goods_specification = this.$route.query.goods_specification;
    this.$post("/api/orders/promptly_goods", {
      id: id,
      attr_id: attr_id,
      count: count,
      goods_specification: goods_specification,
      address_id
    }).then(response => {
      console.log(response);
      this.mustbuy = response.data;
      if (this.coupons) {
        this.total_price = Number(
          this.mustbuy.goods_total_price - this.coupons.limit_money).toFixed(2);
      } else {
        this.total_price = this.mustbuy.goods_total_price.toFixed(2);
      }
      if (!response.data.address && !response.data.telephone) {
        this.show = true;
      }
      this.address = {
        address: response.data.address,
        address_id: response.data.address_id,
        name: response.data.name,
        telephone: response.data.telephone
      };
      if (this.selectaddress) {
        this.address = this.selectaddress;
      }
    });
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
