  flex-direction: row;
  line-height: 0.96rem;
  justify-content: space-between;
  border-bottom: 1px solid #ebebeb;
  background: #fff;
}
.discount1 {
  padding: 0 0.28rem;
  display: flex;
  flex-direction: row;
  line-height: 0.96rem;
  border-bottom: 1px solid #ebebeb;
  background: #fff;
}
.discount1-r input {
  border: none;
