<template>
  <div class="wrapper">
    <div class='container'>
      <!-- 个人信息 -->
      <div class='personinfo'>
        <img src='./../../assets/images/wdhy_fxs@2x.png' class='bjpic'/>
        <div class='imgbox'>
          <img :src='pic'/>
          <span>我的身份: {{myid}}</span>
        </div>
        <!-- 两个白耳朵 -->
        <div class='my_box'>
          <div class='left'>
            <span class='title'>我的分销商</span>
            <span class='value'>{{agent_count}}</span>
          </div>
          <div class=''>
            <span class='title'>累计佣金</span>
            <span class='value'>¥{{total_brokerage}}</span>
          </div>
        </div>
      </div>
      <!-- 分销商列表 -->
      <div class='distri_list'>
        <div class='scroll_div'>
          <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list v-model="loading" :finished="finished" :finished-text="distri_list.length ==0?'暂时没有分销商':'没有更多了'"
                      @load="getdata">
              <div class='list_item' v-for="(item,index) in distri_list" :key="index">
                <van-row class='top' type="flex" justify="space-between">
                  <van-col>
                    <img :src='item.head_image'/>
                    <span class='nickname'>{{item.nickname}}</span>
                    <span class='_id'>（id:{{item.id}}）</span>
                  </van-col>
                  <van-col v-if="item.grade == 0" class="type">普通用户</van-col>
                  <van-col v-if="item.grade == 1" class="type">总代</van-col>
                  <van-col v-if="item.grade == 2" class="type">董事</van-col>
                </van-row>
                <div class='mid'>
                  <div class='left'>
                    <img src='./../../assets/images/icon_shoujihao.svg'/>
                    <span>{{item.telephone}}</span>
                  </div>
                  <div class=''>
                    <img src='./../../assets/images/icon_zcsj.svg'/>
                    <span>{{item.time}}</span>
                  </div>
                </div>
                <div class='bot'>
                  <div class='left'>
                    <img src='./../../assets/images/icon_diqu.svg'/>
                    <span>{{item.address}}</span>
                  </div>
                </div>
              </div>
            </van-list>
          </van-pull-refresh>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        page: 1,
        pic:"",
        myid:"",
        agent_count: '0',
        total_brokerage: '0.00',
        distri_list: [],
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      onRefresh() {
        this.page = 1;
        this.finished = false;
        // this.getdata();
      },
      getdata() {
        this.$post('/api/my/my_distributor', {id: this.userInfo.id, page: this.page, type: 0}).then(res => {
          if (res.code == 1) {
            // if (this.page == 1) {
            this.distri_list = res.data.userinfo;
            //   this.isLoading = false;
            // } else {
            //   this.list = this.list.concat(res.data);
            // }
            this.agent_count = res.data.agent_count;
            this.total_brokerage = res.data.total_brokerage;
            this.isLoading = false;
            this.loading = false;
            // this.page ++;
            // if (res.data.count == this.list.length) {
            this.finished = true;
            // }
          } else {
            this.isLoading = false;
            this.loading = false;
            this.finished = true;
            this.$toast(res.msg)
          }
        })
      },
    },
    created() {
      document.title = '我的分销商';
      this.pic = this.$route.query.pic;
      let that = this;
      switch (that.$route.query.myid) {
        case 0 :that.myid = '普通用户';break;
        case 1 :that.myid = '总代';break;
        case 2 :that.myid = '董事';break;
      }

    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .wrapper >>> .van-pull-refresh__track {
    min-height: calc(100vh - 4.74rem);
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
  }

  .container {
    /*width: 7.50rem;*/
  }

  .personinfo {
    width: 100%;
    height: 3.92rem;
    position: relative;
    display: flex;
    justify-content: center;
  }

  .bjpic {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  .personinfo .imgbox {
    height: 2.08rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: .52rem;
    font-family: PingFang-SC-Bold;
    font-size: .28rem;
    z-index: 2;
    color: #ffffff;
  }

  .personinfo .imgbox img {
    border: .04rem solid #FFFFFF;
    box-shadow: 0 4px 12px 0 rgba(239, 51, 66, 0.60);
    width: 1.28rem;
    height: 1.28rem;
    border-radius: 50%;
    margin-bottom: .20rem;
  }

  .my_box {
    width: 100%;
    height: 1.40rem;
    position: absolute;
    z-index: 2;
    bottom: -.70rem;
    display: flex;
    justify-content: center;
  }

  .my_box div {
    width: 3.28rem;
    height: 1.40rem;
    background: #FFFFFF;
    box-shadow: 0 6px 12px 0 rgba(242, 242, 242, 0.50);
    border-radius: .20rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .my_box div .title {
    font-size: .24rem;
    color: #999999;
    margin-bottom: .14rem;
  }

  .my_box div .value {
    font-size: .32rem;
    color: #333;
  }

  .my_box .left {
    margin-right: .36rem;
  }

  .distri_list {
    width: 100%;
    height: calc(100vh - 4.74rem);
    margin-top: .80rem;
  }

  .distri_list .scroll_div {
    width: 100%;
    height: calc(100vh - 4.74rem);
    padding: .28rem;
    box-sizing: border-box;
    overflow-y: scroll;
  }

  .distri_list .list_item {
    width: 100%;
    height: 2.16rem;
    background: #fff;
    box-shadow: 0 6px 12px 0 rgba(242, 242, 242, 0.50);
    border-radius: .20rem;
    box-sizing: border-box;
    padding: .28rem .40rem;
    margin-bottom: .28rem;
  }

  .distri_list .list_item .top {
    display: flex;
    align-items: center;
    font-family: PingFang-SC-Bold;
    font-size: .28rem;
    font-weight: bold;
    color: #333333;
    height: .48rem;
    margin-bottom: .24rem;
    .type {
      width: 0.88rem;
      height: 0.4rem;
      text-align: center;
      line-height: 0.4rem;
      background: #5045D8;
      border-radius: 0.2rem;
      font-size: 0.24rem;
      color: #FFFFFF;
    }
  }

  .distri_list .list_item .top ._id {
    font-weight: normal;
    color: #999;
    margin-left: .12rem;
  }

  .distri_list .list_item .top img {
    width: .48rem;
    height: .48rem;
    border-radius: 50%;
    margin-right: .20rem;
  }

  .distri_list .list_item .mid {
    height: .32rem;
    margin-bottom: .24rem;
    display: flex;
    align-items: center;
  }

  .distri_list .list_item .mid div.left {
    margin-right: 1.80rem;
  }

  .distri_list .list_item .mid div {
    display: flex;
    align-items: center;
    font-size: .24rem;
    color: #333333;
  }

  .distri_list .list_item .mid img, .distri_list .list_item .bot img {
    width: .32rem;
    height: .32rem;
    margin-right: .16rem;
  }

  .distri_list .list_item .bot .left {
    font-size: .24rem;
    color: #333333;
    display: flex;
    align-items: center;
  }
</style>


