            <div class="coupons-m" v-for="(item,index) in coupon_list" :key="index">
              <div class="coupons-ml">
                <p>{{item.name}}</p>
                <p>{{item.limit_condition}}</p>
              </div>
              <div class="coupons-mr">
                <span></span>
                <span></span>
                <p>¥
                  <i>{{item.limit_money}}</i>
                </p>
                <p v-if="item.coupon_listshow==0" @click="getcoupon(item.id,index)">立即领取</p>
                <p v-if="item.coupon_listshow==1">已领取</p>
              </div>
            </div>
          </div>
          <div class="coupons-close" @click="couponsclose">
            <img src="../../assets/images/icon_close2.svg" alt="">
          </div>
        </div>
      </van-popup>
    </div>
    <!-- 优惠券弹窗 -->
    <div class="bindphone-wrap">
      <van-popup v-model="showphone" :close-on-click-overlay="false" :lock-scroll="true">
        <div class="bindphone">
          <div class="bindphone-t">
            <img src="../../assets/images/dingphone.png" alt="">
          </div>
          <div class="bindphone-m1">
            <input type="number" name="" id="" v-model="telephone" placeholder="输入手机号">
          </div>
          <div class="bindphone-m2">
            <input type="number" name="" id="" v-model="codecode" placeholder="手机验证码">
            <span v-show="show" @click="getCode">获取验证码</span>
            <span v-show="!show" class="count">{{count}} s</span>
          </div>
          <div class="bindphone-b" @click="surebind">
            确认绑定
          </div>
        </div>
      </van-popup>
    </div>
    <!-- 绑定手机号弹窗 -->
  </div>
</template>

<script>
  import {mapMutations} from "vuex";
  import {mapGetters} from "vuex";

  export default {
    components: {},
    props: {},
    data() {
      return {
        slides: [],
        content: "123", //tab切换内容
        showphone: false, //弹出绑定手机号
        showcoupons: false,//优惠券弹窗
        couponsshow: true,//优惠券弹窗显隐
        show: true,//获取验证码
        count: '',
        timer: null,
        yesterday_goods: [],//昨日爆款
        today_goods: [],//今日主推
        first_classify: [],//商品一级分类
        goods_classify: [],//一级分类下的商品
        telephone: "",//电话号码
        coupon_list: [],//优惠券
        // coupon_listshow:true,//立即领取优惠券
        code: "",//验证码
        codecode: "",//绑定验证码
        gu_id: "",//扫码进入的值
      };
    },
    watch: {
      coupon_list(newval, oldval) {
        console.log(newval, oldval)
        this.coupon_list = newval
      }
    },
    computed: {
      ...mapGetters(['userInfo']),
    },
    methods: {
      gochange(index) {
        console.log(this.first_classify);
        this.content = this.first_classify[index].name;
        var id = this.first_classify[index].id
        console.log(id)
        this.$post("/api/index/goods_classify", {id: id}).then(response => {
          this.goods_classify = response.data
          console.log(response);
        });
      },
      surebind() {
        var id = this.userInfo.id
        var telephone = this.telephone
        var code = this.code
        if (this.codecode == '') {
          return this.$toast({
            message: '请输入验证码',
            duration: 1000
          })
        }
        if (code == this.codecode) {
          this.$post("/api/other/binding_telephone", {id: id, telephone: telephone}).then(response => {
            this.goods_classify = response.data
            // console.log(response);
            if (response.code == 1) {
              this.$toast({
                message: response.msg,
                duration: 1000
              })
              this.showphone = false;
              this.showcoupons = true;
            } else {
              this.$toast({
                message: response.msg
              })
            }
          });
        } else {
          this.$toast({
            message: '验证码错误',
            duration: 1000
          })
        }
      },
      couponsclose() {
        this.showcoupons = false;
        if (this.gu_id) {
          var goods_id = this.gu_id.split(",")[0]
          this.$router.push({path: "/listdetail", query: {goods_id: goods_id}})
        }
      },
      getCode() {
        if (this.telephone.length != 11) {
          this.$toast({
            message: '手机号格式错误',
            duration: 1000
          })
        }
        const TIME_COUNT = 60;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.show = false;
          this.timer = setInterval(() => {
            if (this.count > 0 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.show = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1000)
        }
        var telephone = this.telephone
        // console.log("telephone:",telephone)
        this.$fetch("/api/other/code", {telephone: telephone}).then(response => {
          // console.log(response);
          this.code = response.data
        });
      },
      getcoupon(id, index) {
        // this.coupon_listshow=index;
        this.coupon_list[index].coupon_listshow = 1
        this.$forceUpdate();
        console.log(this.coupon_list)
        var user_id = this.userInfo.id
        var coupon_id = id
        this.$fetch("/api/coupon/coupon_get", {coupon_id: coupon_id, user_id: user_id}).then(response => {
        });
      },
      golistdetail(id) {
        var goods_id = id
        this.$router.push({path: "/listdetail", query: {goods_id: goods_id}})
      },
      getUrlParam(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      },
      wxLogin() {
        var appId = 'wx474dc15e39abdda7';
        var oauth_url = 'http://bsk.mumarenkj.com/push/push/openid';
        var url = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=" + appId + "&redirect_uri=" + location.href.split('#')[0] + "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect";
        var code = this.getUrlParam("code");
        var gu_id = this.getUrlParam("id")
        this.gu_id = gu_id
        console.log("gu_id", gu_id)
        if (gu_id) {
          var id = gu_id.split(",")[1]
          var goods_id = gu_id.split(",")[0]
        } else {
          var id = 0
        }
        if (!code) {
          window.location = url;
        } else {
          this.$post("/api/login/openids", {code: code, id: id}).then(response => {
            console.log("授权", response);
            var userInfo = response.data
            this.senduserInfo(userInfo)
            if (!userInfo.telephone || userInfo.telephone == '' || userInfo.telephone == null) {
              this.showphone = true
            } else {
              this.showphone = false
            }
            this.$post("/api/coupon/coupon_list", {id: userInfo.id}).then(response => {
              this.coupon_list = response.data
              if (response.code == 1) {
                this.coupon_list.forEach((el) => {
                  el.coupon_listshow = 0
                })
              }
              console.log(this.coupon_list);
              if (response.code == '2') {
                this.couponsshow = false;
                if (this.gu_id) {
                  this.$router.push({path: "/listdetail", query: {goods_id: goods_id}})
                }
              }
            });
            this.$post("/api/index/slideshow_list").then(response => {
              this.slides = response.data
            });
            this.$post("/api/index/yesterday_goods").then(response => {
              this.yesterday_goods = response.data
            });
            this.$post("/api/index/today_goods").then(response => {
              this.today_goods = response.data
            });
            this.$post("/api/index/first_classify").then(response => {
              this.first_classify = response.data
              // console.log(this.first_classify);
              var id = this.first_classify[0].id
              this.$post("/api/index/goods_classify", {id: id}).then(response => {
                this.goods_classify = response.data
              });
            });
          })
        }
      },
      ...mapMutations({
        senduserInfo: "SENDuserInfo"
      }),
    },
    created() {
    },
    mounted() {
      var that = this
      if (this.userInfo.id) {
        document.title = "易享购";
        //  console.log("this.userInfo.telephone",this.userInfo.telephone)
        //  console.log("用户id1：",this.userInfo.id)
        if (!this.userInfo.telephone || this.userInfo.telephone == '' || this.userInfo.telephone == null) {
          this.showphone = true
          if (this.coupon_list) {
            this.showcoupons = false;
          }
        } else {
          this.showphone = false
          if (this.coupon_list) {
            this.showcoupons = true;
          }
        }
        this.$post("/api/coupon/coupon_list", {id: this.userInfo.id}).then(response => {
          // console.log(response);
          this.coupon_list = response.data
          if (response.code == 1) {
            this.coupon_list.forEach((el) => {
              el.coupon_listshow = 0
            })
          }
          //  console.log(this.coupon_list);
          if (response.code == '2') {
            this.couponsshow = false;
          }
        });

        this.$post("/api/index/slideshow_list").then(response => {
          // console.log(response);
          this.slides = response.data
        });
        this.$post("/api/index/yesterday_goods").then(response => {
          this.yesterday_goods = response.data
          //  console.log(this.yesterday_goods);
        });
        this.$post("/api/index/today_goods").then(response => {
          this.today_goods = response.data
          //  console.log(this.today_goods);
        });
        this.$post("/api/index/first_classify").then(response => {
          this.first_classify = response.data
          console.log(this.first_classify);
          var id = this.first_classify[0].id
          this.$post("/api/index/goods_classify", {id: id}).then(response => {
            this.goods_classify = response.data
            // console.log(response);
          });
        });
      } else {
        document.title = "易享购";
        this.wxLogin()
        console.log("用户id2：", this.userInfo.id)
      }
    }
  };
</script>
<style scoped lang='stylus'>
</style>
