    height: 2.8rem;
    margin: 0.2rem 0 0 0.24rem;
    border-radius: 0.2rem;
  }

  .home-content-jmr {
    margin-left: 0.28rem;
  }

  .home-content-jmrt {
    width: 3rem;
    margin-top: 0.34rem;
    font-size: 0.32rem;
    font-weight: bold;
  }

  .home-content-jmrm1 {
    width: 3.14rem;
    height: 0.6rem;
    padding-top: 0.2rem;
    color: #999;
    font-size: 0.26rem;
    line-height: 0.32rem;
  }

  .home-content-jmrm2 {
    margin-top: 0.27rem;
    width: 0.92rem;
    height: 0.36rem;
    text-align: center;
    line-height: 0.36rem;
    border: 1px solid #ebebeb;
    box-sizing: border-box;
    font-size: 0.2rem;
    border-radius: 0.2rem;
  }

  .home-content-jmrb {
    margin-top: 0.3rem;
    width: 3.14rem;
    line-height: 0.5rem;
    height: 0.5rem;
    overflow: hidden;
  }

  .home-content-jmrb span:nth-of-type(1) {
    color: #ef3544;
    font-size: 0.36rem;
  }

  .home-content-jmrb span:nth-of-type(2) {
    color: #333;
    font-size: 0.24rem;
    text-decoration: line-through;
  }

  .home-content-jmrb span i {
    font-size: 0.2rem;
  }

  .home-content-tab {
    width: 100%;
    background: #f7f7f7;
    padding-top: 0.52rem;
  }

  .home-content-tabtop {
    display: flex;
    flex-direction: row;
    overflow-x: auto;
  }

  .home-content-tabtop::-webkit-scrollbar {
    display: none;
  }

  .home-content-tabtopm {
    width: 1.08rem;
    height: 0.56rem;
    font-size: 0.28rem;
    line-height: 0.56rem;
    text-align: center;
  }

  .tabbox {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .tabboxm {
    margin-top: 0.24rem;
    width: 3.24rem;
    height: 5.32rem;
    background: #fff;
    border-radius: 0.2rem;
    // overflow: hidden;
  }

  .tabboxm:nth-of-type(2n) {
    margin-left: 0.4rem;
  }

  .tabboxm:nth-of-type(2n - 1) {
    margin-left: 0.22rem;
  }

  .tabboxmt img {
    width: 3.24rem;
    height: 3.24rem;
    border-radius: 0.2rem;
  }

  .tanboxmm1 {
    line-height: 0.32rem;
    margin: 0 0.24rem;
    font-size: 0.24rem;
    margin-top: 0.15rem;
    font-weight: bold;
  }

  .tanboxmm2 {
    line-height: 0.32rem;
    margin: 0 0.24rem;
    margin-top: 0.15rem;
    font-size: 0.22rem;
    color: #999;
  }

  .tanboxmb {
    margin-top: 0.25rem;
    width: 3.24rem;
    height: 0.4rem;
    line-height: 0.4rem;
    overflow: hidden;
  }

  .tanboxmb span:nth-of-type(1) {
    color: #ef3544;
    font-size: 0.32rem;
    margin-left: 0.24rem;
  }

  .tanboxmb span:nth-of-type(2) {
    color: #333;
    font-size: 0.24rem;
    text-decoration: line-through;
  }

  .tanboxmb span i {
    font-size: 0.2rem;
  }

  .bindphone-wrap .van-popup {
    border-radius: 0.4rem;
    overflow: hidden;
  }

  .bindphone {
    width: 5.9rem;
    padding: 0 0 0.3rem 0;
  }

  .bindphone-t img {
    width: 5.9rem;
    border-top-left-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
  }

  .bindphone-m1 input {
    width: 5.1rem;
    background: #f7f7f7;
    height: 0.96rem;
    line-height: 0.96rem;
    outline: 0;
    border: none;
    border-radius: 0.5rem;
    margin: 0.6rem 0 0 0.4rem;
    padding-left: 0.2rem;
  }

  .bindphone-m2 {
    width: 5.1rem;
    background: #f7f7f7;
    height: 0.96rem;
    line-height: 0.96rem;
    border-radius: 0.5rem;
    margin: 0.6rem 0 0 0.4rem;
  }

  .bindphone-m2 input {
    width: 2.9rem;
    outline: 0;
    border: none;
    background: #f7f7f7;
    border-radius: 0.5rem;
    padding-left: 0.2rem;
  }

  .bindphone-m2 span {
    font-size: 0.24rem;
    color: #5045d8;
    width: 1.8rem;
    text-align: center;
    display: inline-block;
  }

  .bindphone-b {
    width: 5.1rem;
    line-height: 0.96rem;
    background: #ef3342;
    color: #fff;
    height: 0.96rem;
    margin-left: 0.4rem;
    text-align: center;
    margin-top: 0.6rem;
    border-radius: 0.5rem;
  }

  .coupons-wrap .van-popup {
    width: 6.8rem;
    height: 10.06rem;
    background: rgba(0, 0, 0, 0);
  }

  .coupons {
    position: relative;
  }

  .couponsimg img {
    width: 6.8rem;
    height: 8.06rem;
  }

  .coupons-m-wrap {
    position: absolute;
    left: 0.24rem;
    top: 1.54rem;
    height: 6rem;
    overflow: hidden;
    overflow-y: auto;
  }

  .coupons-m {
    display: flex;
    flex-direction: row;
    width: 5.5rem;
    height: 1.84rem;
    background: #fff;
    margin-left: 0.4rem;
    border-radius: 0.2rem;
    margin-top: 0.15rem;
  }

  .coupons-ml {
    width: 4.5rem;
  }

  .coupons-ml p:nth-of-type(1) {
    margin: 0.44rem 0 0 0.42rem;
    font-size: 0.3rem;
  }

  .coupons-ml p:nth-of-type(2) {
    margin: 0.22rem 0 0 0.42rem;
    font-size: 0.24rem;
    color: #999;
  }

  .coupons-mr {
    width: 2.3rem;
    height: 1.84rem;
    box-sizing: border-box;
    border-left: 1px dashed #ffbabf;
    position: relative;
  }

  .coupons-mr p:nth-of-type(1) {
    color: #ef3342;
    margin: 0.3rem 0 0 0.26rem;
  }

  .coupons-mr p:nth-of-type(1) i {
    font-size: 0.4rem;
  }

  .coupons-mr p:nth-of-type(2) {
    width: 1.36rem;
    height: 0.56rem;
    background: #5045d8;
    text-align: center;
    line-height: 0.56rem;
    color: #fff;
    border-radius: 0.5rem;
    margin: 0.2rem 0 0 0.22rem;
  }

  .coupons-mr span:nth-of-type(1) {
    width: 0.2rem;
    height: 0.2rem;
    display: inline-block;
    background: #ef3342;
    border-radius: 0.5rem;
    position: absolute;
    left: -0.1rem;
    top: -0.1rem;
  }

  .coupons-mr span:nth-of-type(2) {
    width: 0.2rem;
    height: 0.2rem;
    display: inline-block;
    background: #ef3342;
    border-radius: 0.5rem;
    position: absolute;
    left: -0.1rem;
    bottom: -0.1rem;
  }

  .coupons-close {
    position: absolute;
    left: 3rem;
    bottom: -1rem;
    z-index: 2018;
  }
</style>
<style lang="stylus" scoped>
  .home-top-banner >>> .carousel-3d-container {
    width 100% !important;
    height 4rem !important;
  }

  .home-top-banner >>> .carousel-3d-slider {
    width 6.7rem !important;
    height 4rem !important;
  }

  .home-top-banner >>> .carousel-3d-slide {
    width 6.7rem !important;
    height 4rem !important;
    border none !important;
  }

  .home-top-banner >>> .carousel-3d-slider img {
    width 100% !important;
    height 4rem !important;
  }

  .home-content-tab >>> .van-tabs__line {
    background-color: #5045D8 !important;
  }

  .home-content-tab >>> .van-tab--active {
    color: #5045D8 !important;
  }
</style>



