<template>
    <div class="wrapper">
        <div class="home-content-tab">
            <van-tabs @click="gochange">
                <van-tab v-for="(item,index) in first_classify" :key="index" :title="item.name">
                      <van-list v-model="loading" :finished="finished" @load="getdata" :offset="20">
                         <div class="tabbox">
                        <div class="tabboxm" v-for="(item,index) in goods_list" :key="index"  @click="golistdetail(item.id)">
                            <div class="tabboxmt">
                                <img :src="item.logo_image" alt="">
                            </div>
                            <div class="tanboxmm1 sigle-h">
                                {{item.name}}
                            </div>
                            <div class="tanboxmb">
                                <span><i>￥</i>{{item.goods_price}}</span>
                                <span><i>￥</i>{{item.original_price}}</span>
                            </div>
                            <div class="tanboxmbb">
                                       已售{{item.yesterday_count}}件
                            </div>
                        </div>
                        </div>
                          <div class="nomore" v-show="finished==true && goods_list.length != 0">没有更多了^v^</div>
                        </van-list>
                    
                  
                       
                </van-tab>
            </van-tabs>
             
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
     first_classify: [],
      goods_list:"",//商品
      loading: false, //上拉加载
      finished: false, //加载完成
      classifyindexid:'',//下标id
      page:1
    };
  },
  watch: {},
  computed: {},
  methods: {
     gochange(index) {
      this.finished = false
      var classify_id = this.first_classify[index].id
      this.classifyindexid = classify_id
      var type = 1
      this.page = 1
      var page = this.page
      this.$fetch("/api/index/goods_list",{classify_id:classify_id,type:type,page:page}).then(response => {
          this.goods_list=response.data.data
           console.log(response);
          if(response.code==2){
              return this.$toast({
                        message:response.msg,
                        duration:1000
                     })
          }
      });
    },
    golistdetail(id){
      var goods_id = id
       this.$router.push({path:"/listdetail",query:{goods_id:goods_id}})
    },
     getdata() {
       var page = this.page
       var type = 1;
       var classify_id = this.classifyindexid
        this.$post("/api/index/goods_list", {
            type,
            page,
            classify_id
        }).then(response => {
             if (this.page == 1) {
                console.log("page:",this.page)
            this.goods_list = response.data.data;
            console.log("this.page == 1:",this.goods_list)
          } else {
            console.log("page:",this.page)
            this.goods_list = this.goods_list.concat(response.data.data);
            console.log("this.goods_list:",this.goods_list)
          }
          this.page++;
          this.loading = false;
          console.log("response.data.count :",response.data.count,"this.goods_list.length:",this.goods_list.length)
          if (response.data.count == this.goods_list.length) {
             console.log("response.data.count :",response.data.count,"this.goods_list.length:",this.goods_list.length)
            this.finished = true;
          }
        });
    }
  },
  created() {},
  mounted() {
    document.title = "今日主推";
    this.$post("/api/index/first_classify").then(response => {
        this.first_classify=response.data
        console.log(response);
          var classify_id = this.first_classify[0].id
          this.classifyindexid = classify_id
          var type = 1
          var page = this.page
          this.$fetch("/api/index/goods_list",{classify_id:classify_id,type:type,page:page}).then(response => {
              this.goods_list=response.data.data
              console.log("response:",response);
              if(response.code==2){
              return this.$toast({
                        message:response.msg,
                        duration:1000
                     })
          }
          });
    });
   
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.wrapper {
  width: 100%;
  min-height: 100vh;
  background: #f7f7f7;
}
.tabbox {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.home-content-tab {
  width: 100%;
  background: #f7f7f7;
}
.home-content-tabtop {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
}
.home-content-tabtop::-webkit-scrollbar {
  display: none;
}
.home-content-tabtopm {
  width: 1.08rem;
  height: 0.56rem;
  font-size: 0.28rem;
  line-height: 0.56rem;
  text-align: center;
}
.tabboxm {
  margin-top: 0.24rem;
  width: 3.24rem;
  height: 5.02rem;
  background: #fff;
  border-radius: 0.2rem;
}
.tabboxm:nth-of-type(2n) {
  margin-left: 0.4rem;
}
.tabboxm:nth-of-type(2n + 1) {
  margin-left: 0.22rem;
}
.tabboxmt img {
  width: 3.24rem;
  height: 3.24rem;
  border-radius: 0.2rem;
}
.tanboxmm1 {
  line-height: 0.32rem;
  margin: 0 0.24rem;
  font-size: 0.24rem;
  margin-top: 0.15rem;
  font-weight: bold;
}
.tanboxmm2 {
  line-height: 0.32rem;
  margin: 0 0.24rem;
  margin-top: 0.15rem;
  font-size: 0.22rem;
  color: #999;
}
.tanboxmb {
  margin-top: 0.25rem;
  width: 3.24rem;
  height: 0.4rem;
  line-height: 0.4rem;
  overflow: hidden;
}
.tanboxmb span:nth-of-type(1) {
  color: #ef3544;
  font-size: 0.32rem;
  margin-left: 0.24rem;
}
.tanboxmb span:nth-of-type(2) {
  color: #333;
  font-size: 0.24rem;
  text-decoration: line-through;
}
.tanboxmb span i{
  font-size: 0.2rem;
}
.tanboxmbb{
  font-size: 0.2rem;
  color: #bbb;
  margin: 0.1rem 0 0 0.24rem;
}
.nomore {
    text-align: center;
    color: #999999;
    line-height: 30px;
  }
</style>
<style lang="stylus" scoped>
.home-content-tab >>> .van-tabs__line{
  background-color: #5045D8 !important;
}
.home-content-tab >>> .van-tab--active{
  color: #5045D8 !important;
}
</style>



