<template>
    <div class="wrapper">
        <div class="moreclass">
            <div class="moreclassl">
                <van-badge-group :active-key="activeKey">
                    <van-badge :title="item.name" @click="onClick" v-for="(item, index) in classify" :key="index" />
                </van-badge-group>
            </div>
            <div class="moreclassr">
                <div class="moreclassrm sigle-h" v-for="(item,index) in list" :key="index" @click="gorecommend(item.id,index)">
                    <div class="moreclassrmt">
                        <img :src="item.cover" alt="">
                    </div>
                    <div class="moreclassrmb sigle-h">
                        {{item.name}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      activeKey: 0,
      show: 0,
      list: [] ,//列表
      classify:{},//分类 
      first_classify:"",//一级分类id
      second_classify:"",//二级分类id
    };
  },
  watch: {},
  computed: {},
  methods: {
    onClick(key) {
      this.activeKey = key;
      this.show = key;
      console.log(key)
        var id = this.classify[key].id
        this.first_classify = id
        this.$post("/api/index/second_classify",{id:id}).then(response => {
          console.log(response)
          this.list = response.data
          if(response.code==2){
              return this.$toast({
                        message:response.msg,
                        duration:1000
                     })
          }
        });
    },
    gorecommend(id,index){
      var first_classify=this.first_classify
      var second_classify = id
      var value = index
      this.$router.push({path:"/recommend",query:{first_classify:first_classify,second_classify:second_classify,value:value}})
    }
  },
  created() {},
  mounted() {
    document.title="分类"
    this.$post("/api/index/first_classify").then(response => {
       console.log(response)
       this.classify = response.data
       var id = this.classify[0].id
       this.first_classify = id
        this.$post("/api/index/second_classify",{id:id}).then(response => {
          console.log(response)
          this.list = response.data
          if(response.code==2){
              return this.$toast({
                        message:response.msg,
                        duration:1000
                     })
          }
        });
    });
    
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.wrapper {
  width: 100%;
}
.moreclass {
  display: flex;
  flex-direction: row;
}
.moreclassl {
  width: 1.82rem;
  font-size: 0.28rem;
}
.moreclassr {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 5.56rem;
  height: 2rem;
  margin-left: 0.15rem;
}
.moreclassrm {
  margin-top: 0.4rem;
  margin-left: 0.35rem;
  height: 1.84rem;;
}
.moreclassrm img {
  width: 1.4rem;
  height: 1.4rem;
}
.moreclassrmb {
  width: 1.4rem;
  margin-top: 0.12rem;
  height: 0.32rem;
  line-height: 0.32rem;
  font-size: 0.24rem;
}
</style>
<style lang="stylus" scoped>
.moreclassl >>> .van-badge{
  width 1.92rem;
  text-align  center;
}
.moreclassl >>> .van-badge--select{
  border-color:#5045D8; 
  color:#5045D8;
}
</style>



