<template>
  <div class="wrapper">
    <div class='container'>
      <!-- 红色部分 -->
      <div class='redbox'>
        <div class='left'>
          <div class='top'>
            <span class='state'>积分余额:</span>
            <span class='money'>{{integral}}</span>
          </div>
          <div class='bot'>
            每次下单或分享可获得一定数量的积分，积分后续可兑换精美好物，敬请期待。
          </div>
        </div>
        <div class='right'>
          <img src="./../../assets/images/jifen@2x.png" alt="">
        </div>
      </div>
      <!-- 积分明细 -->
      <div class='listbox'>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata">
            <div class='title'>积分明细</div>
            <div class='list_item' v-for="(item,index) in pointsList" :key="index">
              <img :src='item.goods_image'/>
              <div class='infobox'>
                <span class='name'>{{item.title}}</span>
                <div class='bot'>
                  <span class='left'>{{item.time}}</span>
                  <span class='right'>{{item.integral}}积分</span>
                </div>
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        integral: 0,
        page: 1,
        pointsList: []
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      onRefresh() {
        this.page = 1;
        this.finished = false;
      },
      getdata() {
        this.$post('/api/my/integral_detail', {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            // if (this.page == 1) {
            this.pointsList = res.data.data;
            //   this.isLoading = false;
            // } else {
            //   this.list = this.list.concat(res.data);
            // }
            this.integral = res.data.integral;
            this.isLoading = false;
            this.loading = false;
            // this.page ++;
            // if (res.data.count == this.list.length) {
            this.finished = true;
            // }
          } else {
            this.isLoading = false;
            this.loading = false;
            this.finished = true;
            this.$toast(res.msg)
          }
        })
      },
    },
    created() {
      document.title = '我的积分';
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .listbox >>> .van-pull-refresh__track {
    min-height: calc(100vh - 3rem);
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    position: relative;
    min-height: 100vh;
  }

  .redbox {
    width: 100%;
    height: 3.92rem;
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    display: flex;
    padding: .48rem .40rem;
    box-sizing: border-box;
    justify-content: space-between;
    padding-right: 0;
  }

  .redbox img {
    width: 2.36rem;
    height: 2.36rem;
  }

  .redbox .left, .redbox .right {
    height: 2.36rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }

  .redbox .left {
    width: 3.50rem;
    height: 1.80rem;
  }

  .redbox .state {
    font-family: PingFang-SC-Bold;
    font-size: .28rem;
    color: #FFFFFF;
    margin-right: .10rem;
  }

  .redbox .money {
    font-size: .40rem;
    color: #fff;
  }

  .redbox .bot {
    font-size: .24rem;
    color: #FBB6B9;
  }

  /* 积分明细 */
  .listbox {
    height: calc(100vh - 3rem);
    overflow-y: auto;
    width: 6.70rem;
    position: absolute;
    top: 3.00rem;
    background: #fff;
    border-radius: .20rem;
    left: 50%;
    transform: translateX(-50%);
    padding: 0 .28rem;
  }

  .listbox .title {
    width: 100%;
    height: .88rem;
    display: flex;
    align-items: center;
    font-family: PingFang-SC-Bold;
    font-size: 0.28rem;
    color: #333333;
  }

  .listbox img {
    width: 1.52rem;
    height: 1.52rem;
    border-radius: .12rem;
    margin-right: .28rem;
  }

  .listbox .list_item {
    height: 1.92rem;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #EBEBEB;
  }

  .listbox .list_item .infobox {
    width: 100%;
    height: 1.52rem;
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
  }

  .list_item .infobox .name {
    font-size: .28rem;
    color: #333333;
  }

  .list_item .infobox .bot {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .list_item .infobox .bot .left {
    font-size: .24rem;
    color: #999999;
  }

  .list_item .infobox .bot .right {
    font-size: .28rem;
    color: #EF3342;
  }
</style>


