<template>
  <div class="wrapper">
    <div class="top" v-if="progressinfo">
      <van-tabs v-model="active" :swipe-threshold=4 color="#fff" sticky animated swipeable :line-width=10>
        <van-tab title="提现到余额">
          <div class="mid">
            <van-row class="title">全部佣金(元)</van-row>
            <van-row class="money">{{money|formatMoney}}</van-row>
          </div>
          <van-row class="inputbox" v-if="progressinfo.type ==2|| progressinfo.type1 == 0">
            <van-row class="inputlist" type="flex" justify="space-between" align="center">
              <van-row type="flex" align="center">
                <van-col class="label">金额</van-col>
                <van-row type="flex" align="center">￥<input type="number" :placeholder="'可提现'+money+'元'"
                                                            v-model="withdrawmoney"></van-row>
              </van-row>
              <van-col class="all" @click.native="withdrawmoney = money">全部提现</van-col>
            </van-row>
          </van-row>
          <!--正在提现中-->
          <van-row class="inputbox" v-if="progressinfo.type == 1 && progressinfo.type1 == 1">
            <van-row class="inputlist" type="flex" justify="space-between" align="center">
              <van-row>
                <van-row type="flex" align="center"><input type="number" :placeholder="progressinfo.money+'元正在提现中'"
                                                           disabled></van-row>
              </van-row>
              <van-col class="all" @click.native="cancel">取消提现</van-col>
            </van-row>
          </van-row>
          <van-button type="danger" round class="btn" v-if="progressinfo.type == 2" @click.native="withdraw">确认提现</van-button>
          <van-button type="danger" round class="btn" :disabled=true v-else>提现中</van-button>
        </van-tab>
        <van-tab title="提现到微信">
          <div class="mid">
            <van-row class="title">全部佣金(元)</van-row>
            <van-row class="money">{{money|formatMoney}}</van-row>
          </div>
          <van-row class="inputbox" v-if="progressinfo.type ==2||progressinfo.type1 == 1">
            <van-row class="inputlist" type="flex" justify="space-between" align="center">
              <van-row type="flex" align="center">
                <van-col class="label">微信号</van-col>
                <van-row type="flex" align="center"><input type="text" placeholder="请输入提现微信号" v-model="wechat">
                </van-row>
              </van-row>
            </van-row>
            <van-row class="inputlist" type="flex" justify="space-between" align="center">
              <van-row type="flex" align="center">
                <van-col class="label">金额</van-col>
                <van-row type="flex" align="center">￥<input type="number" :placeholder="'可提现'+money+'元'"
                                                            v-model="withdrawmoney"></van-row>
              </van-row>
              <van-col class="all" @click.native="withdrawmoney = money">全部提现</van-col>
            </van-row>
          </van-row>
          <!--正在提现中-->
          <van-row class="inputbox" v-if="progressinfo.type == 1 && progressinfo.type1 == 0">
            <van-row class="inputlist" type="flex" justify="space-between" align="center">
              <van-row>
                <van-row type="flex" align="center"><input type="number" :placeholder="progressinfo.money+'元正在提现中'"
                                                           disabled></van-row>
              </van-row>
              <van-col class="all" @click.native="cancel">取消提现</van-col>
            </van-row>
          </van-row>
          <van-button type="danger" round class="btn" v-if="progressinfo.type == 2" @click.native="withdraw">确认提现</van-button>
          <van-button type="danger" round class="btn" :disabled=true v-else>提现中</van-button>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import {checkempty} from "./../../assets/js/tool"
  export default {
    components: {},
    props: {},
    data() {
      return {
        active: 0,
        money: "0.00",
        progressinfo: '',
        withdrawmoney: "",
        wechat: "",
      }
    },
    watch: {
      withdrawmoney(val, old) {
        if (Number(val) > Number(this.money)) {
          this.withdrawmoney = Number(this.money);
          this.$toast('提现金额请勿超出余额上限');
        }
      }
    },
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      withdraw() {
        const that = this;
        let data = {
          user_id: this.userInfo.id,
          money: that.withdrawmoney,
          type1: 1, //0到微信 1到余额
          type2: 1,
        }
        let arr = [
          {val: that.withdrawmoney, toast: "请输入提现金额"},
        ];
        if (this.active == 1) { //提现到微信
          arr.push({val: that.wechat, toast: '请输入提现微信号'});
          data.wechat = that.wechat;
          data.type1 = 0;
        }
        let result = true;
        for (var index = 0; index < arr.length; index++) {
          if (!checkempty(arr[index].val)) {
            result = false;
            break
          }
        }
        if (!result) {
          return
        }
        that.$post('/api/my/withdraw_deposit', data).then(res => {
          console.log(res);
          if (res.code == 1) {
            this.withdrawmoney = "";
            this.wechat = "";
            this.show = true;
            this.getdata();
          } else {
            this.$toast(res.msg);
          }
        })
      },
      /**
       * 取消提现
       */
      cancel() {
        this.$post('/api/my/withdraw_cancel', {id: this.progressinfo.id}).then(res => {
          this.$toast(res.msg);
          if (res.code == 1) {
            this.getdata();
          }
        })
      },
      getdata() {
        this.$post('/api/my/withdraw_deposit_list', {id: this.userInfo.id, type: 2}).then(res => {
          if (res.code == 1) {
            this.progressinfo = res.data;
            this.money = res.data.brokerage;
          } else {
            this.$toast(res.msg);
          }
        })
      }
    },
    created() {
      document.title = '佣金提现';
      this.getdata();
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .top >>> .van-tabs--line .van-tabs__wrap {
    height 0.8rem;
  }

  .top >>> .van-tab {
    background: #EF3342;
    font-size: 0.28rem;
    color: #F698A0;
    line-height: 0.8rem;
  }

  .top >>> .van-tab--active {
    font-size: 0.28rem;
    color: #FFFFFF;
  }

  .top >>> .van-tabs--line {
    padding-top: 0.8rem;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    background: #F5F5F5;
    min-height: 100vh;
    width: 100%;
  }

  .mid {
    height: 2.58rem;
    padding: 0.28rem 0 0.28rem 0.28rem;
    box-sizing: border-box;
    font-size: 0.28rem;
    color: #333333;
    line-height: 0.44rem;
    background: #ffffff;
    border-bottom: 1px solid #ebebeb;
    .title {

    }
    .money {
      margin-top: 0.6rem;
      font-size: 0.6rem;
      line-height: 0.7rem;
    }
  }

  .inputbox {
    background: #ffffff;
    .inputlist {
      height: 1.08rem;
      padding: 0.28rem;
      box-sizing: border-box;
      border-bottom: 1px solid #ebebeb;
      .van-row {
        height: 100%;
      }
      .label {
        width: 1.24rem;
      }
      input:disabled {
        background: transparent;
      }
      input {
        border: none;
        width: 4rem;
        height: 100%;
      }
    }
    .all {
      font-size: 0.24rem;
      color: #5045D8;
    }
  }

  .btn {
    display: block;
    width: 6.94rem;
    margin: .4rem auto;
    height: 0.88rem;
    font-size: 0.32rem;
  }

  .van-button--danger {
    color: #fff;
    background: #EF3342;
    border: 1px solid #EF3342;
  }

</style>


