<template>
  <div class="wrapper">
    <router-link to="/addaddress">
      <van-row class="top" type="flex" justify="center" align="center">
        <img src="./../../assets/images/icon_xzdz.svg" alt="">
        新增地址
      </van-row>
    </router-link>
    <van-row class="no" v-if="list =='' ">
      <img src="./../../assets/images/icon_wu.svg" alt="">
      <van-col>暂未设置</van-col>
      <van-col>没有收货地址收不了快递哦～</van-col>
    </van-row>
    <van-row class="mid" v-else>
      <van-row class="box" v-for="(item,index) in list" :key="index" @click.native="click(index)">
        <van-row>{{item.province+item.city+item.area+item.detail}}</van-row>
        <van-row class="name">{{item.name+' '+item.telephone}}</van-row>
        <van-row type="flex" justify="space-between" align="center">
          <van-row class="select" @click.native="setdefault(index)">
            <img
              :src="item.is_default?require('./../../assets/images/icon_xuanzhong.svg'):require('./../../assets/images/icon_weixuan.svg')"
              alt="">默认地址
          </van-row>
          <van-row type="flex" align="center">
            <van-row class="right" type="flex" align="center" @click.native="del(item.id)">
              <img src="./../../assets/images/icon_delete.svg" alt="">删除
            </van-row>
            <van-row class="right" type="flex" align="center" @click.native="edit(index)">
              <img src="./../../assets/images/icon_bianji.svg" alt="">编辑
            </van-row>
          </van-row>
        </van-row>
      </van-row>
    </van-row>
    <!--取消订单-->
    <van-popup v-model="show" :close-on-click-overlay=false>
      <van-row class="popup">
        <van-row class="content" type="flex" justify="center" align="center">确定要删除收货地址吗？</van-row>
        <van-row>
          <van-col class="cancel" @click.native="show=false">取消</van-col>
          <van-col class="confirm" @click.native="confirm">确定</van-col>
        </van-row>
      </van-row>
    </van-popup>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        delid: "",
        list: "",
        show: false,
        address:"",
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      /**
       * 点击切换地址 将地址存在vuex中
       */
      click(index){
        const that = this;
        if(!that.address){
          return
        }
        let obj ={
          name:that.list[index].name,
          address:that.list[index].address_detail,
          telephone:that.list[index].telephone,
          address_id:that.list[index].id,
        };
        that.$store.commit("SENDselectaddress",obj);
        console.log(that.$store)
        this.$router.go(-1);
      },
      confirm() {
        this.$post('/api/address/del_address', {id: this.delid}).then(res => {
          if (res.code == 1) {
            this.getdata()
            this.show = false;
          } else {
            this.$toast(res.msg)
          }
        })
      },
      setdefault(index) {
        let data = {};
        let that = this;
        data.user_id = this.userInfo.id;
        for (let key in that.list[index]) {
          if (key == 'time' || key == 'address_detail') {
            continue;
          }
          if (key == 'is_default') {
            if (that.list[index][key]) {
              data[key] = 0;
            } else {
              data[key] = 1;
            }
            continue;
          }
          data[key] = that.list[index][key];
        }
        this.$fetch('/api/address/add_address', data).then(res => {
          if (res.code == 3 || res.code == 1) {
            this.$toast('设置成功！');
            this.list.forEach(function (val) {
              val.is_default = 0;
            });
            this.list[index].is_default = 1;
          } else {
            this.$toast(res.msg)
          }
        })
      },
      edit(index) {
        this.$router.push({path: '/addaddress', query: this.list[index]});
      },
      del(id) {
        this.delid = id;
        this.show = true;
      },
      getdata() {
        this.$fetch("/api/address/address_list", {id:this.userInfo.id}).then(res => {
          console.log(res);
          if (res.code == 1) {
            this.list = res.data;
          } else {
            this.list ="";
            this.$toast(res.msg)
          }
        })
      }
    },
    created() {
      document.title = '收货地址';
      if(this.$route.query.address){
        this.address = this.$route.query.address;
      }
      this.getdata()
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    padding-top: 0.96rem;
    width: 100%;
    background: #F5F5F5;
    min-height: calc(100vh - 0.96rem);
  }

  .top {
    position: fixed;
    top: 0;
    height: 0.96rem;
    width: 100%;
    background: #ffffff;
    font-size: 0.32rem;
    color: #EF3342;
    img {
      width: 0.44rem;
      height: 0.44rem;
    }
  }

  .no {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1.96rem;
    font-size: 0.28rem;
    line-height: 0.4rem;
    img {
      width: 2.8rem;
      height: 2.8rem;
    }
    :nth-child(3) {
      font-size: 0.24rem;
      color: #999999;
    }
  }

  .box:after {
    display: none;
  }

  .mid {
    padding-top: 0.2rem;
  }

  .box {
    width: 6.94rem;
    height: 2.96rem;
    padding: 0.28rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 0.32rem;
    color: #333333;
    background: #ffffff;
    margin: 0 auto 0.2rem;
    box-shadow: 0 0.12rem 0.24rem 0 rgba(242, 242, 242, 0.50);
    border-radius: 0.2rem;
    line-height: 0.44rem;
    img {
      width: 0.4rem;
      height: 0.4rem;
      margin-right: 0.08rem;
    }
    .name {
      font-size: 0.28rem;
      color: #999999;
    }

    .select {
      font-size: 0.24rem;
      color: #666666;
    }
    .right {
      font-size: 0.24rem;
      margin-left: 0.4rem;
    }
  }

  /*取消订单*/
  .van-popup {
    border-radius: 0.4rem;
  }

  .popup {
    width: 5.18rem;
    height: 2.56rem;
    .content {
      height: 1.6rem;
      text-align: center;
      font-size: 0.28rem;
      color: #999999;
    }
    .cancel {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #333333;
      border-right: 1px solid #ebebeb;
    }
    .confirm {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #EF3342;
    }
  }
</style>


