<template>
    <div class="wrapper">
        <div class="detail-top">
            <van-swipe :autoplay="3000">
                <van-swipe-item v-for="(image, index) in images" :key="index">
                    <img :src="image" />
                </van-swipe-item>
            </van-swipe>
        </div>
        <div class="detail-price">
            <div class="detail-price1">
                ￥{{goods_detail.goods_price}}
            </div>
            <div class="detail-price2">
                秒杀价
            </div>
            <div class="detail-price3">
                积分赠送{{goods_detail.own_integral}}
            </div>
        </div>
        <div class="detail-sale">
            定价：
            <span>¥{{goods_detail.original_price}}</span>
        </div>
        <div class="detail-down">
            <div class="detail-downl">
                <div class="detail-downll">
                    <img src="../../assets/images/icon_daojishi.svg" alt="">
                    <span>距结束</span>
                </div>
                <div class="detail-downlr">
                    {{countdown}}
                    <!-- 13456 -->
                </div>
            </div>
            <div class="detail-downr">
                <!-- 快递：¥{{goods_detail.freight}} -->
            </div>
        </div>
        <div class="detail-name">
            {{goods_detail.name}}
        </div>
        <!--  -->
        <div class="detail-jj sigle-h">
            {{goods_detail.describe}}
        </div>
        <!--  -->
        <div class="detail-kuc">
            <div class="detail-kucm">
                库存：{{goods_detail.num}}
            </div>
            <div class="detail-kucm">
                销量：{{goods_detail.sales}}
            </div>
            <div class="detail-kucm">
                收藏：{{goods_detail.collected_count}}
            </div>
        </div>
        <!--  -->
        <div class="detail-guige-wrap" @click="showup">
            <div class="detail-guige">
                <div class="detail-guigel">
                    规格
                    <span>{{guige}}</span>
                </div>
                <div class="detail-guiger">
                    <img src="../../assets/images/icon_youjiantou.svg" alt="">
                </div>
            </div>
        </div>
       <!-- 规格 -->
       <div class="detail-comment-wrap">
          <div class="detail-comment">
             <div class="detail-comment-t">
                <div class="detail-comment-tl">
                   用户评价
                </div>
                <div class="detail-comment-tm" @click="gocomment(goods_detail.id)">
                    查看全部条{{goods_detail.opinion_count}}评价
                </div>
                <div class="detail-comment-tr"  @click="gocomment(goods_detail.id)">
                    <img src="../../assets/images/icon_qbpj.svg" alt="">
                </div>
             </div>
             <div class="detail-comment-content" v-for="(item,index) in goods_detail.opinion_content" :key="index">
                 <div class="detail-comment-contentt">
                     <div class="detail-comment-contenttl">
                         <img :src="item.head_image" alt=""><span>{{item.nickname}}</span>
                     </div>
                     <div class="detail-comment-contenttr">
                            <van-rate
                            v-model="item.goods_intro"
                            :size="12"
                            :count="5"
                            color="#EF3342"
                            void-color="#bbb"
                            :readonly='true'
                            />
                     </div>
                 </div>
                  <div class="detail-comment-contentw sigle-h">
                   {{item.content}}
                 </div>
                  <div class="detail-comment-contentimg" v-if="item.images.length!=0">
                    <img :src="items" alt="" v-for="(items,index) in item.images" :key="index">
                 </div>
                  <div class="detail-comment-contentb">
                    {{item.goods_specification}}
                 </div>
             </div>
          </div>
       </div>
       <!-- 评价 -->
       <div class="detail-list">
          <div class="detail-list-t">
            图文详情
          </div>
          <div class="detail-list-m" v-html="goods_detail.goods_detail"></div>
       </div>
       <!-- 图文详情 -->
       <div class="detail-dibu">
          <div class="detail-dibu1"  @click="goqrcode">
             <div class="detail-dibu1t">
                <img src="../../assets/images/icon_share.svg" alt="">
             </div>
             <div class="detail-dibu1b">
                分享
             </div>
          </div>
          <div class="detail-dibu2" @click="collected">
             <div class="detail-dibu1t">
                <img src="../../assets/images/icon_shouc1.svg" alt="" v-if="goods_detail.is_collected==0">
                <img src="../../assets/images/icon_copy-3.svg" alt="" v-else>
             </div>
             <div class="detail-dibu1b">
                <span v-if="goods_detail.is_collected==1">已收藏</span> 
                <span v-else>未收藏</span>
             </div>
          </div>
          <router-link to="/cart">
          <div class="detail-dibu3">
             <div class="detail-dibu1t">
                <img src="../../assets/images/icon_gwcc.svg" alt="">
             </div>
             <div class="detail-dibu1b">
                购物车
             </div>
          </div>
          </router-link>
          <div class="detail-dibu4" @click="upguige">
              加入购物车
          </div>
          <div class="detail-dibu5" @click="upguige">
              立即购买

          </div>
       </div>
       <!-- 底部 -->
       <div class="guige-popup">
          <van-popup v-model="show" position="bottom" :overlay="true">
            <div class="guige-popupt">
               <div class="guige-popuptl">
                    <div class="guige-popuptll">
                        <img :src="assessment_detail0.img" alt="">
                    </div>
                    <div class="guige-popuptlr">
                         <p>￥{{assessment_detail0.price}}</p>
                         <p class="sigle-h" v-show="subIndex.length<=0">请选择&nbsp;&nbsp;x{{num}}</p>
                         <p class="sigle-h" v-show="subIndex.length>0">已选：{{guige}} &nbsp;&nbsp;x{{num}}</p>
                    </div>
                </div>
                <div class="guige-popuptr" @click="closeshowup">
                    <img src="../../assets/images/icon_close.svg" alt="">
                </div>
            </div>
            <div class="guige-popupc-wrap">
               <div class="guige-popupc" v-for="(item,n) in assessment" :key="n">
                 <div class="guige-popupct">
                     {{item.name}}
                 </div>
                 <div class="guige-popupcc">
                     <div class="guige-popupccm" v-for="(items,index) in item.attribute" :key="index" 
                     v-on:click="specificationBtn(items.name,n,$event,index)" 
                     v-bind:class="[items.isShow?'':'noneActive',subIndex[n] == index?'productActive':'']">
                         {{items.name}}
                     </div>
                 </div>
               </div>
            </div>
            <div class="guige-popupn">
               <div class="guige-popupnl">
                   数量 <span>库存{{assessment_detail0.num}}件</span>
               </div>
               <div class="guige-popupnr">
                   <span @click="reduce">-</span><input type="number" v-model="num" @blur.prevent="changeCount"><span @click="add">+</span>
               </div>
            </div>
            <div class="guige-popupnb">
                <div class="guige-popupnbl" @click="gocart">
                     加入购物车
                </div>
                <div class="guige-popupnbr" @click="gobuy">
                    立即购买
                </div>
            </div>
          </van-popup>
       </div>
       <!-- 规格弹窗 -->
       <div class="container" v-show="qrcodeshare">
           <van-popup v-model="showshare"  @click-overlay='colseshare'>
                <img :src="imgUrl" alt="分享图" class="creat-imgimg">
           </van-popup>
       </div>
    </div>
</template>

<script>
  import {mapGetters} from "vuex";
export default {
  components: {},
  props: {},
  data() {
    return {
      show:false,//规格弹窗
      num:'',//加减数量
      images: [],//轮播
      goods_detail:'',//商品所有信息
      timestamp:"",//
      countdown:"00:00:00",//倒计时
      goods_attribute:"请选择",//请选择规格
      assessment:[],//规格属性
      assessment_detail:[],//规格匹配所有属性组合
      guige:"请选择",//已选规格
      assessment_detail0:'',//
      selectArr: [], //存放被选中的值
      shopItemInfo: {}, //存放要和选中的值进行匹配的数据
      subIndex: [], //是否选中 因为不确定是多规格还是但规格，所以这里定义数组来判断
      qrcodeshare:false,//分享显隐
      showshare:false,//是否显示分享图片
      imgUrl:'',
    };
  },
  watch: {
  },
  computed: {
      ...mapGetters(['userInfo']),
  },
  methods: {
    gocomment(id){
        var id = id
        this.$router.push({path:"/comment",query:{id:id}})
    },
    showup(){
        this.show=true    
    },
    closeshowup(){
        this.show=false
    },
    reduce(){
        this.assessment_detail.forEach(el=>{
            if(el.assessment_detail==this.selectArr){
            this.num--
            if(this.num<el.sell_num){
                this.num=el.sell_num
                this.$toast({
                    message:"亲！这是最小购买数量了",
                    duration:1000
                })
            }
            }
        })
    },
    add(){
        console.log(this.selectArr)
        this.assessment_detail.forEach(el=>{
            if(el.assessment_detail==this.selectArr){
                if(this.num<el.num){
                    this.num++
                }else {
                    this.$toast({
                            message:'亲！这是最大库存了。',
                            duration:1000
                    })
                }
            }
        })
        
    },
    gobuy(){
        var id = this.userInfo.id
        var selectArr = this.selectArr.join(',')
        if(this.selectArr.length != this.assessment.length){
           this.$toast({
                message:'请选择规格',
                duration:1000
           })
        }
        this.selectArr.forEach((el)=>{
          if(el==''){
              this.$toast({
                message:'请选择规格',
                duration:1000
           })
          }
        })
        this.assessment_detail.forEach((el)=>{
            if(el.assessment_detail==selectArr){
                var attr_id =el.id
                    var count = this.num
                var goods_specification = selectArr
                console.log(count,goods_specification,attr_id)
                this.$router.push({path:"/placeorder",query:{id:id,attr_id:attr_id,count:count,goods_specification:goods_specification}})
            }
        })  
            this.$store.commit("SENDcoupon",'');
    },
    collected(){
        var id = this.userInfo.id
        var goods_id= this.goods_detail.id
        this.$fetch("/api/index/goods_collected",{id:id,goods_id:goods_id}).then(response => {
            console.log(response)
            var goods_id = this.$route.query.goods_id
            this.$fetch("/api/index/goods_detail",{id:id,goods_id:goods_id}).then(response => {
                console.log(response)
                this.goods_detail = response.data
            });
        });
    },
    specificationBtn: function (item, n, event, index) {
        var self = this;
        if (self.selectArr[n] != item) {
            self.selectArr[n] = item;
            self.subIndex[n] = index; 
        } else {
            self.selectArr[n] = "";
            self.subIndex[n] = -1; //去掉选中的颜色 
        }
        var selectArr=self.selectArr.join(',')
        var selectArr1=self.selectArr.join(' ')
            this.guige = selectArr1
            console.log(selectArr)
            if(selectArr==","){
            self.subIndex.length=0 
            }
        self.assessment_detail.forEach((el)=>{
            if(el.assessment_detail==selectArr){
                this.assessment_detail0=el
            }
        })
        console.log(selectArr)
        this.assessment_detail.forEach(el=>{
            if(selectArr==el.assessment_detail){
                this.num = el.sell_num
            }
        })
        self.checkItem();
    },
    checkItem: function () {
        var self = this;
        var option = self.assessment;
        var result = [];  //定义数组存储被选中的值
        for (var i in option) {
            result[i] = self.selectArr[i] ? self.selectArr[i] : '';
        }
        for (var i in option) {
            var last = result[i];  //把选中的值存放到字符串last去
            for (var k in option[i].attribute) {
                result[i] = option[i].attribute[k].name; //赋值，存在直接覆盖，不存在往里面添加name值
                option[i].attribute[k].isShow = self.isMay(result); //在数据里面添加字段isShow来判断是否可以选择
            }
            result[i] = last; //还原，目的是记录点下去那个值，避免下一次执行循环时避免被覆盖

        }
        self.$forceUpdate(); //重绘
    },
    isMay: function (result) {
        for (var i in result) { 
            if (result[i] == '') {
                return true; //如果数组里有为空的值，那直接返回true
            }
        }
        return this.shopItemInfo[result].num == 0 ? false : true; //匹配选中的数据的库存，若不为空返回true反之返回false
    },
    gocart(){
        if(this.selectArr.length !=this.assessment.length){
           this.$toast({
                message:'请选择规格',
                duration:1000
           })
        }
        this.selectArr.forEach((el)=>{
          if(el==''){
              this.$toast({
                message:'请选择规格',
                duration:1000
           })
          }
        })
        var user_id = this.userInfo.id
        var goods_id = this.$route.query.goods_id
        var selectArr=this.selectArr.join(',')
        var goods_specification = selectArr
        this.assessment_detail.forEach((el)=>{
            if(el.assessment_detail==selectArr){
                var attr_id = el.id
                var goods_price = el.price
                var count = this.num
                this.$fetch("/api/cart/add_cart",{user_id:user_id,goods_id:goods_id,attr_id:attr_id,goods_price:goods_price,goods_specification:goods_specification,count:count}).then(response => {
                    console.log(response)
                    this.show=false
                    if(response.code==1){
                            this.$toast({
                            message:response.msg,
                            duration:1000
                            })
                    }else {
                            
                          }
                })
            }
        })
        
        
    },
    changeCount(){
        console.log("this.assessment_detail:",this.assessment_detail)
        var selectArr=this.selectArr.join(',')
         this.assessment_detail.forEach((el)=>{
           if(el.assessment_detail==selectArr){
               if(this.num <= el.sell_num){
                    this.num = el.sell_num
               }else if (this.num>=el.num) {
                    this.num = el.num
               }
           }
         })
         console.log("this.num:",this.num)
    },
    upguige(){
        this.show =true
    },
    goqrcode(){
        this.qrcodeshare=true
        this.showshare=true
    },
    colseshare(){
        console.log(123)
        this.qrcodeshare=false
        this.showshare=false
    }
  },
  created() {
  },
  mounted() {
      this.qrcodeshare=false
      this.showshare=false
      document.title="商品详情"
      var id =this.userInfo.id
      var goods_id = this.$route.query.goods_id
      this.$fetch("/api/index/goods_detail",{id:id,goods_id:goods_id}).then(response => {
        console.log(response)
        this.goods_detail = response.data
        this.images = response.data.goods_images
        this.timestamp = response.data.end_time - response.data.new_time
        // console.log(this.timestamp)
         var seconds = parseInt(this.timestamp);
         var that = this
         function timer(){
           var h = parseInt(seconds/3600)
            var m = parseInt(seconds % 3600 / 60)
            var s = parseInt(seconds % 60)
            var  t = "";
            t += (h > 0) ? (h+":") : "00"; 
            t += (m > 0) ? (m+":") : "00";
            t += (s > 0) ? (s+"") : "00";
            that.countdown = t
         }
            timer()
        setInterval(function(){
            seconds--
            timer()
        },1000)
        
      });
      this.$post("/api/index/goods_attribute",{id:goods_id}).then(response => {
            console.log(response)
            // this.goods_detail = response.data
            this.assessment= response.data.assessment
            this.assessment_detail = response.data.assessment_detail
           this.assessment_detail0=this.assessment_detail[0]
           this.num = this.assessment_detail0.sell_num
            console.log(this.assessment_detail)
            var self = this;
            for (var i in self.assessment_detail) {
                self.shopItemInfo[self.assessment_detail[i].assessment_detail] = self.assessment_detail[i]; //修改数据结构格式，改成键值对的方式，以方便和选中之后的值进行匹配
                // console.log("this.shopItemInfo:",self.shopItemInfo)
            }
            self.checkItem();
      });
       var uid = this.userInfo.id
       this.$post("api/wxshare/index",{g_id:goods_id,uid:uid}).then(response => {
            console.log("responseresponseresponse",response)
            this.imgUrl = response.data
      });
  }
};
</script>
<style scoped lang='stylus'>
</style>
