.detail-dibu{
    position: fixed;
    bottom: 0;
   display: flex;
   flex-direction: row;
   width: 100%;
   height: 1.08rem;
   background: #fff;
   z-index: 2;
}
.detail-dibu1,.detail-dibu2,.detail-dibu3{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1.08rem;
  height: 1.08rem;
  border-top: 1px solid#ebebeb;
}
.detail-dibu1t img{
    width: 0.48rem;
    height: 0.48rem;
    margin-top: 0.2rem;
}
.detail-dibu1b{
    margin-top: 0.06rem;
    font-size: 0.2rem;
    color: #999;
}
.detail-dibu4,.detail-dibu5{
    width: 2.12rem;
    height: 1.08rem;
    color: #fff;
    text-align: center;
    line-height: 1.08rem;
    font-size: 0.32rem;
}
.detail-dibu4{
    background: #FF7A84;
}
.detail-dibu5{
    width: 2.21rem;
    background: #EF3342;
}
