.guige-popup{
    position: relative;
    z-index: 3
}
.guige-popupt{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 0.24rem;
    margin-top: 0.24rem;
}
.guige-popuptl{
    display: flex;
    flex-direction: row;
}
.guige-popuptlr{
    margin-left: 0.28rem;
}
.guige-popuptlr p:nth-of-type(1){
    color: #EF3342;
    font-size: 0.36rem;
    margin-top: 1rem;
}
.guige-popuptlr p:nth-of-type(2){
    font-size: 0.24rem;
    margin-top: 0.2rem;
    width: 3.2rem;
}
.guige-popuptlr p:nth-of-type(3){
    font-size: 0.24rem;
    margin-top: 0.2rem;
    width: 3.2rem;
}
.guige-popuptll img{
    width: 1.96rem;
    height: 1.96rem;
    border-radius: 0.2rem;
}
.guige-popupc-wrap{
    width: 100%;
    margin-top: 0.2rem;
    height: 4.8rem;
    overflow-y: auto;
}
.guige-popupct{
    font-size: 0.24rem;
    margin-left: 0.24rem;
    margin-top: 0.4rem;
}
.guige-popupcc{
    width: 92%;
    margin-left: 4%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.guige-popupccm{
    padding: 0.2rem 0.45rem;
    box-sizing: border-box;
    background: #f5f5f5;
    border-radius: 0.5rem;
    margin-right: 0.2rem;
    margin-top:0.2rem;
    border: 1px solid transparent;
}
.guige-popupcc .productActive {
    background-color: #E0DEFF !important;
    color: #5045D8 !important;
    border: 1px solid #5045D8 !important;
    box-sizing: border-box;
}
.guige-popupcc .noneActive {
    background-color: #ccc !important;
    opacity: 0.4 !important;
    color: #000 !important;
    pointer-events: none !important;
}
.guige-popupn{
    margin: 0 0.24rem;
    height: 1.08rem;
    line-height: 1.08rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #fff;
    position: relative;
}
.guige-popupnb{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    left: 0;
}
.guige-popupnbl{
    width: 3.8rem;
    text-align: center;
    line-height: 1.08rem;
    font-size: 0.32rem;
    color: #fff;
    background: #FF7A84;
}
.guige-popupnbr{
    width: 3.8rem;
    text-align: center;
    line-height: 1.08rem;
    font-size: 0.32rem;
    color: #fff;
    background: #EF3342;
}
.guige-popupnl span{
    margin-left: 0.4rem;
    color: #999;
}
.guige-popupnr span{
    display: inline-block;
    width: 0.44rem;
    height: 0.44rem;
    text-align: center;
    line-height: 0.44rem;
    background: #f8f8f8;
    vertical-align: middle;
}
.guige-popupnr input{
    width: 1.36rem;
    height: 0.44rem;
    line-height: 0.44rem;
    background: #f8f8f8;
    text-align: center;
    border: none;
    outline: 0;
    padding-top: 0.01rem;
    margin: 0 0.05rem;
    vertical-align: middle;
}
// 
.container{
    position: fixed;
    top: 0rem;
    left: 0rem;
    z-index: 9;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    
}
.container .van-popup{
     width: 4.94rem;
    height: 8.06rem;
    border-radius: 0.5rem;
}
.container img{
    width: 100%;
    height: 100%;
}
