<template>
    <div class="wrapper">
        <div class="comment-t">
            <div class="comment-tl">
                商品描述：
                <span> {{data.goods_intro }}</span>
            </div>
            <div class="comment-tm">
                物流服务：
                <span>{{data.logistics_server}}</span>
            </div>
            <div class="comment-tr">
                卖家服务：
                <span>{{data.seller_server}}</span>
            </div>
        </div>
        <van-list v-model="loading" :finished="finished" @load="getdata" :offset="20">
        <div class="detail-comment-wrap"> 
            <div class="detail-comment-content" v-for="(item,index) in opinion_content" :key="index">
                <div class="detail-comment-contentt">
                    <div class="detail-comment-contenttl">
                        <img :src="item.head_image" alt="">
                        <span>{{item.nickname}}</span>
                    </div>
                    <div class="detail-comment-contenttr">
                        <van-rate v-model="item.goods_intro" :size="12" :count="5" color="#EF3342" void-color="#bbb" :readonly='true' />
                    </div>
                </div>
                <div class="detail-comment-contentw sigle-h">
                    {{item.content}}
                </div>
                <div class="detail-comment-contentimg" v-if="item.images.length!=0">
                    <img :src="items" alt="" v-for="(items,index) in item.images" :key="index">
                </div>
                <div class="detail-comment-contentb">
                    <div class="detail-comment-contentbl">
                        {{item.goods_specification}}
                    </div>
                    <div class="detail-comment-contentbr">
                        {{item.time}}
                    </div>

                </div>
            </div>
        </div>
        <div class="nomore" v-show="finished==true && opinion_content.length != 0">没有更多了^v^</div>
        </van-list>
    </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
      data: "", //所有数据
      opinion_content: [] ,//评论
      loading: false, //上拉加载
      finished: false, //加载完成
      page:1,//
    };
  },
  watch: {},
  computed: {},
  methods: {
       getdata() {
       var id = this.$route.query.id;
       var page = this.page
        this.$post("/api/index/goods_opinion", {
            id,
            page,
        }).then(response => {
             if (this.page == 1) {
            this.opinion_content = response.data.goods_opinion;
          } else {
            this.opinion_content = this.opinion_content.concat(response.data.goods_opinion);
          }
          this.page++;
          this.loading = false;
          if (response.data.total_row == this.opinion_content.length) {
            this.finished = true;
          }
        });
    }
  },
  created() {},
  mounted() {
    document.title = "全部评价";
    var id = this.$route.query.id;
    this.$fetch("/api/index/goods_opinion", { id: id }).then(response => {
      console.log(response);
      this.data = response.data;
      this.opinion_content = response.data.goods_opinion;
    });
  }
};
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
.wrapper {
  width: 100%;
  background: #f7f7f7;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}
.detail-comment-wrap {
  width: 100%;
  //   background: #f7f7f7;
  overflow-y: auto;
  height: calc(100vh - 1.1rem);
  position: absolute;
  top: 1.1rem;
  left: 0.32rem;
}
.detail-comment {
  width: 100%;
  padding: 0 0.24rem;
  margin-top: 0.2rem;
}
.detail-comment-t {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.detail-comment-tl {
  font-size: 0.28rem;
  font-weight: bold;
}
.detail-comment-tm {
  font-size: 0.24rem;
  color: #999;
  margin-left: 0.4rem;
}
.detail-comment-tr img {
  margin-left: 0.16rem;
  width: 0.32rem;
  height: 0.32rem;
}
.detail-comment-content {
  width: 6.94rem;
  background: #fff;
  border-radius: 0.2rem;
  padding: 0.2rem 0 0.28rem 0;
  margin-top: 0.2rem;
}
.detail-comment-content:nth-of-type(1) {
  margin-top: 0;
}
.detail-comment-contentt {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 0.24rem;
}
.detail-comment-contenttl img {
  width: 0.48rem;
  height: 0.48rem;
  border-radius: 0.2rem;
}
.detail-comment-contenttl span {
  margin-left: 0.2rem;
  font-size: 0.24rem;
  color: #666;
}
.detail-comment-contentimg {
  margin-top: 0.28rem;
  margin-left: 0.24rem;
}
.detail-comment-contentimg img {
  width: 1.52rem;
  height: 1.52rem;
  margin-left: 0.12rem;
  border-radius: 0.2rem;
}
.detail-comment-contentimg img:nth-of-type(1) {
  margin-left: 0rem;
}
.detail-comment-contentw {
  width: 6.46rem;
  margin-left: 0.24rem;
  margin-top: 0.24rem;
}
.detail-comment-contentb {
  font-size: 0.2rem;
  color: #999;
  margin: 0.28rem 0.24rem 0 0.24rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.comment-t {
  width: 100%;
  height: 1.78rem;
  background-image: linear-gradient(0deg, #fe6664 0%, #ef3342 100%);
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  color: #fff;
  padding-top: 0.4rem;
}
.comment-t span {
  font-size: 0.36rem;
}
.nomore {
  width: 100%;
    text-align: center;
    color: #999999;
    line-height: 30px;
  }
</style>


