<template>
  <div class="wrapper">
    <van-row class="toptop">
      <img src="./../../assets/images/phb.svg" alt="">
      <van-row type="flex" align="center" justify="space-between">每周一上午 05:00 更新</van-row>
    </van-row>
    <div class="top">
      <van-tabs v-model="active" :swipe-threshold=4 color="#fff" sticky animated swipeable :line-width=10>
        <van-tab title="佣金收入">
          <div class="mid">
            <van-row class="title">
              <van-col class="one">排名</van-col>
              <van-col class="two">用户</van-col>
              <van-col class="three">佣金收入(元)</van-col>
            </van-row>
            <div class="box">
              <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
                <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata" :offset="20">
                  <van-row class="list" v-for="(item,index) in list" :key="index">
                    <van-col class="one">
                      <img src="./../../assets/images/icon_1@2x.png" alt="" v-if="index == 0">
                      <img src="./../../assets/images/icon_2@2x.png" alt="" v-if="index == 1">
                      <img src="./../../assets/images/icon_3@2x.png" alt="" v-if="index == 2">
                      <span v-if="index!=0 &&index!=1&&index!=2">{{index+1}}</span>
                    </van-col>
                    <van-col class="two">
                      <img :src="item.head_image" alt="">{{item.nickname}}
                    </van-col>
                    <van-col class="three">{{item.brokerage|formatMoney}}</van-col>
                  </van-row>
                </van-list>
              </van-pull-refresh>
            </div>
          </div>
        </van-tab>
        <van-tab title="个人销量">
          <div class="mid">
            <van-row class="title">
              <van-col class="one">排名</van-col>
              <van-col class="two">用户</van-col>
              <van-col class="three">销售商品(件)</van-col>
            </van-row>
            <div class="box">
              <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
                <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata"
                          :immediate-check=false :offset="20">
                  <van-row class="list" v-for="(item,index) in list" :key="index">
                    <van-col class="one">
                      <img src="./../../assets/images/icon_1@2x.png" alt="" v-if="index == 0">
                      <img src="./../../assets/images/icon_2@2x.png" alt="" v-if="index == 1">
                      <img src="./../../assets/images/icon_3@2x.png" alt="" v-if="index == 2">
                      <span v-if="index!=0 &&index!=1&&index!=2">{{index+1}}</span>
                    </van-col>
                    <van-col class="two">
                      <img :src="item.head_image" alt="">{{item.nickname}}
                    </van-col>
                    <van-col class="three">{{item.sales_count}}</van-col>
                  </van-row>
                </van-list>
              </van-pull-refresh>
            </div>
          </div>
        </van-tab>
        <van-tab title="团队业绩">
          <div class="mid">
            <van-row class="title">
              <van-col class="one">排名</van-col>
              <van-col class="two">董事</van-col>
              <van-col class="three">团队佣金(元)</van-col>
            </van-row>
            <div class="box">
              <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
                <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getdata"
                          :immediate-check=false :offset="20">
                  <van-row class="list" v-for="(item,index) in list" :key="index">
                    <van-col class="one">
                      <img src="./../../assets/images/icon_1@2x.png" alt="" v-if="index == 0">
                      <img src="./../../assets/images/icon_2@2x.png" alt="" v-if="index == 1">
                      <img src="./../../assets/images/icon_3@2x.png" alt="" v-if="index == 2">
                      <span v-if="index!=0 &&index!=1&&index!=2">{{index+1}}</span>
                    </van-col>
                    <van-col class="two">
                      <img :src="item.head_image" alt="">{{item.nickname}}
                    </van-col>
                    <van-col class="three">{{item.remain_brokerage|formatMoney}}</van-col>
                  </van-row>
                </van-list>
              </van-pull-refresh>
            </div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        active: 0,
        isLoading: false,
        loading: false,
        finished: false,
        page: 1,
        list: "",
      }
    },
    watch: {
      active(val, old) {
        this.getdata(1)
      }
    },
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.list = "";
          this.page = 1;
          this.finished = false;
        }
        let url = "";
        const that = this;
        switch (that.active) {
          case 0:
            url = '/api/my/brokerage_ranking';
            break;
          case 1:
            url = '/api/my/sales_ranking';
            break;
          case 2:
            url = '/api/my/achievememt_ranking';
            break;
        }
        this.$post(url, {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = "";
              this.list = res.data.data;
            } else {
              this.list = this.list.concat(res.data.data);
            }
            if (res.data.total_row == this.list.length || res.data.data.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    created() {
      document.title = '排行榜';
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .top >>> .van-tabs--line .van-tabs__wrap {
    height 0.8rem;
  }

  .top >>> .van-tabs__nav {
    background: none;

  }

  .top >>> .van-hairline--top-bottom::after {
    border: none;
  }

  .top >>> .van-tab {
    background: transparent;
    font-size: 0.28rem;
    color: #F698A0;
    line-height: 0.8rem;
  }

  .top >>> .van-tab--active {
    font-size: 0.28rem;
    color: #FFFFFF;
  }

  .top >>> .van-tabs--line {
    padding-top: 0.8rem;
  }

  .box >>> .van-pull-refresh__track {
    min-height: calc(100vh - 5.4rem);
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    overflow: hidden;
    height: 100vh;
  }

  .toptop {
    height: 3.92rem;
    padding-top: 0.32rem;
    text-align: center;
    font-size: 0.24rem;
    letter-spacing: 0.12rem;
    color: #FFFFFF;
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    .van-row {
      display: inline-block;
      margin-top: 0.2rem;
    }
    img {
      width: 4.04rem;
      height: .98rem;
    }
  }

  .top {
    margin-top: -1.4rem;
    z-index: 2;
    .mid {
      margin-top: 0.2rem;
      height: calc(100vh - 3.9rem);
      background: #ffffff;
      border-radius: .40rem .40rem 0 0;
      padding-top: 0.6rem;
      box-sizing: border-box;
      .title {
        height: 0.96rem;
        line-height: 0.96rem;
        font-size: 0.28rem;
        color: #999999;
        display: flex;
        justify-content: space-between;
        background: #F5F5F5;
      }
      .one {
        width: 1.4rem;
        padding-left: 0.28rem;
        span {
          font-size: 0.36rem;
          margin-left: 0.24rem;
          color: #333333;
        }
      }
      .two {
        flex: 1;
        padding-left: 0.28rem;
      }
      .three {
        width: 2.42rem;
        padding-left: 0.28rem;
      }
      .box {
        height: calc(100% - 0.9rem);
        overflow: scroll;
        .list:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0.28rem;
          height: 1px;
          background: #ebebeb;
          width: 100%;
        }
        .list {
          .title;
          height: 1.42rem;
          line-height: 1.42rem;
          background: #ffffff;
          position: relative;
          .one {
            .one;
            img {
              width: 0.56rem;
              height: 0.56rem;
            }
          }
          .two {
            .two;
            font-size: 0.3rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: #333333;
            img {
              width: 0.72rem;
              height: 0.72rem;
              margin-right: 0.2rem;
            }
          }
          .three {
            .three;
            font-size: 0.3rem;
            color: #EF3342;
          }
        }
      }
    }
  }
</style>


