<template>
  <div class="wrapper">
      <div class='scroll_div'>
        <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
          <van-list v-model="loading" :finished="finished" :finished-text="order_list.length?'没有更多了':''" @load="getdata">
            <div class="no" v-if="order_list.length == 0">
              <img src="./../../assets/images/icon_gwcwsp.svg" alt="">
              <div>暂无数据</div>
            </div>
            <div class='itembox' v-for="(item,index) in order_list" :key="index">
              <span class='date'>{{item.time}}</span>
              <div class='goodsbox'>
                <img :src='item.goods_image'/>
                <div class='info'>
                  <span class='title'>{{item.goods_name}}</span>
                  <div class='box mid'>付款金额<span class='paymoney'>￥{{item.goods_price}}</span></div>
                  <div class='box'>佣金金额<span class='commi_money'>￥{{item.money}}</span></div>
                </div>
              </div>
            </div>
          </van-list>
        </van-pull-refresh>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        page: 1,
        order_list: []
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      getdata(onRefresh) {
        if(onRefresh){
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/my/distributor_detail_list', {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            // if (this.page == 1) {
            this.order_list = res.data;
            //   this.isLoading = false;
            // } else {
            //   this.list = this.list.concat(res.data);
            // }
            this.isLoading = false;
            this.loading = false;
            // this.page ++;
            // if (res.data.count == this.list.length) {
            this.finished = true;
            // }
          } else {
            this.isLoading = false;
            this.loading = false;
            this.finished = true;
            this.$toast(res.msg)
          }
        })
      },
    },
    created() {
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .wrapper >>> .van-pull-refresh__track {
    min-height: calc(100vh - 0.4rem);
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
  }

  .scroll_div {
    width: 100%;
    /*height: 100vh;*/
    /*overflow-y: scroll;*/
    padding: .20rem .28rem;
    box-sizing: border-box;
  }

  .scroll_div .itembox {
    width: 100%;
    height: 2.90rem;
    background: #fff;
    margin-bottom: .20rem;
    box-shadow: 0 .06px .12px 0 rgba(242, 242, 242, 0.50);
    border-radius: .20rem;
    padding: .20rem .24rem .28rem;
    box-sizing: border-box;
  }
   .no{
    margin:0 auto 0;
    padding-top :2rem;
    text-align: center;
  }
  .scroll_div .itembox .date {
    display: flex;
    width: 100%;
    align-items: center;
    font-size: .24rem;
    color: #999999;
    margin-bottom: .28rem;
  }

  .scroll_div .goodsbox {
    display: flex;
    height: 1.80rem;
    font-size: .24rem;
  }

  .scroll_div .goodsbox img {
    border: 1px solid #ebebeb;
    border-radius: .12rem;
    width: 1.80rem;
    height: 1.80rem;
    margin-right: .24rem;
  }

  .scroll_div .goodsbox .info {
    display: flex;
    height: 100%;
    flex-direction: column;
    flex: 1;
    width: 100%;
    justify-content: space-between;
  }

  .scroll_div .goodsbox .info .title {
    width: 100%;
    height: .68rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    color: #333;
    margin-bottom: .24rem;
  }

  .scroll_div .goodsbox .box {
    font-size: .24rem;
    color: #999999;
  }

  .scroll_div .goodsbox .mid {
    margin-bottom: .20rem;
  }

  .scroll_div .goodsbox .box span {
    color: #333;
    margin-left: .10rem;
  }

  .scroll_div .goodsbox .box span.commi_money {
    color: #EF3342;
  }
</style>


