<template>
  <div class="wrapper">
    <div class='container'>
      <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
        <van-list v-model="loading" :finished="finished" :finished-text="myCouponsList.length?'没有更多了':''" @load="getdata">
          <div class="no" v-if="myCouponsList.length == 0">
            <img src="./../../assets/images/icon_gwcwsp.svg" alt="">
            <div>暂无优惠券</div>
          </div>
          <div class='couponbox' v-for="(item,index) in myCouponsList" :key="index"  @click="select(index)">
            <!-- 分割线 -->
            <div class='line'>
              <div class='circle c_t'></div>
              <div class='circle c_b'></div>
            </div>
            <!-- 左侧满减券 -->
            <div class='left' v-if="item.limit_condition !=0">
              <!-- 减得金额 -->
              <div class='top'>
                <span class='icon red'>￥</span>
                <span class='money'>{{item.limit_money}}</span>
              </div>
              <div class='bot'>满<span class='icon'>￥</span>{{item.limit_condition}}使用</div>
            </div>
            <!-- 左侧无门槛券 -->
            <div class='left' v-if="item.limit_condition==0">
              <!-- 减得金额 -->
              <div class='top'>
                <span class='icon red'>￥</span>
                <span class='money'>{{item.limit_money}}</span>
              </div>
              <div class='bot'>无门槛券</div>
            </div>
            <div class='mid'>
              <span class='top'>{{item.name}}</span>
              <span class='bot'>{{item.end_time1}} 到期</span>
            </div>
            <div class='right'>
              <span class='usebtn'>去使用</span>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
      <!-- 历史优惠券 -->
      <div class='historybox'>
        <router-link to="couponhistory">
          <van-button class='histxt' round type="default" size="small">优惠券历史记录</van-button>
        </router-link>
      </div>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        totalprice:"",
        isLoading: false,
        loading: false,
        finished: false,
        page: 1,
        myCouponsList: [],
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      /**
       * 选择优惠券
       */
      select(index){
        if(!this.totalprice){
          return
        }
        //判断是否可用
        if(this.myCouponsList[index].limit_condition != 0&&this.totalprice < this.myCouponsList[index].limit_condition){
          this.$toast({message:'未满'+this.myCouponsList[index].limit_condition+'',duration:1000});
          return
        }
        this.$store.commit("SENDcoupon",this.myCouponsList[index]);
        this.$router.go(-1);
      },
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/my/my_coupon', {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            // if (this.page == 1) {
            this.myCouponsList = res.data;
            //   this.isLoading = false;
            // } else {
            //   this.list = this.list.concat(res.data);
            // }
            this.isLoading = false;
            this.loading = false;
            // this.page ++;
            // if (res.data.count == this.list.length) {
            this.finished = true;
            // }
          } else {
            this.isLoading = false;
            this.loading = false;
            this.finished = true;
            this.$toast(res.msg)
          }
        })
      },
    },
    created() {
      document.title = '我的优惠券';
      if(this.$route.query.totalprice){
        console.log(this.$route.query.totalprice);
        this.totalprice = this.$route.query.totalprice;
      }
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .wrapper >>> .van-pull-refresh__track {
    min-height: calc(100vh - 1.36rem);
  }
  .wrapper >>> .no {
    margin: 0 auto 0;
    padding-top: 2rem;
    text-align: center;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
  }

  .container {
    min-height: 100vh;
    background: #fff;
    padding: 0.28rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 1.08rem;
  }

  .couponbox {
    width: 6.94rem;
    height: 1.84rem;
    background: #FFF7F7;
    display: flex;
    position: relative;
    margin-bottom: .28rem;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: .20rem;
  }

  /* left */
  .couponbox .left {
    width: 1.80rem;
    display: flex;
    height: clac(100% - .02rem);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #FFBABF;
    border-right: none;
    border-radius: .20rem 0 0 .20rem;
  }

  .couponbox .left .top {
    display: flex;
    justify-content: center;
    align-items: baseline;
    font-family: PingFang-SC-Bold;
    font-size: .48rem;
    margin-bottom: .08rem;
    color: #EF3342;
  }

  .couponbox .left .top .icon {
    font-size: .24rem;
  }

  .couponbox .left .top .icon.red {
    font-weight: bold;
  }

  .couponbox .left .bot {
    font-size: .24rem;
    color: #999999;
  }

  /* 线 */
  .line {
    width: 0.02rem;
    position: absolute;
    left: 1.80rem;
    z-index: 4;
    height: 100%;
    background: #FFBABF;
  }

  .line .circle {
    width: .20rem;
    height: .20rem;
    border-radius: 50%;
    background: #fff;
    position: absolute;
    border: 1px solid #FFBABF;
    left: 50%;
    transform: translateX(-50%);
  }

  .line .c_t {
    top: -.10rem;
  }

  .line .c_b {
    bottom: -.10rem;
  }

  /* mid */
  .couponbox .mid {
    width: 100%;
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border-top: 1px solid #FFBABF;
    border-bottom: 1px solid #FFBABF;
  }

  .mid .top {
    font-size: .30rem;
    color: #333333;
    margin-bottom: .20rem;
  }

  .mid .bot {
    font-size: .24rem;
    color: #999999;
  }

  /* right */
  .couponbox .right {
    width: 1.56rem;
    height: clac(100% - .02rem);
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid #FFBABF;
    border-left: none;
    border-radius: 0 .20rem .20rem 0;
  }

  .usebtn {
    background: #EF3342;
    box-shadow: 0 0 0 0 #EBEBEB;
    border-radius: .30rem;
    display: flex;
    height: .60rem;
    width: 1.28rem;
    font-family: PingFang-SC-Bold;
    font-size: .24rem;
    color: #FFFFFF;
    justify-content: center;
    align-items: center;
  }

  /* 历史优惠券 */
  .historybox {
    width: 100%;
    height: 1.08rem;
    border-top: 1px solid #EBEBEB;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
  }

  .historybox .histxt {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5.20rem;
    height: .80rem;
    border: 1px solid #EBEBEB;
    border-radius: .44rem;
    font-size: .28rem;
    color: #666666;
  }
</style>


