import Vue from 'vue'
import Router from 'vue-router'
import Cart from '@/components/cart/cart'
import Index from '@/components/index/index'
import explosive from '@/components/index/explosive'
import mainpush from '@/components/index/mainpush'
import moreclass from '@/components/index/moreclass'
import recommend from '@/components/index/recommend'
import search from '@/components/index/search'
import searchlist from '@/components/index/searchlist' 
import listdetail from '@/components/index/listdetail' 
import comment from '@/components/index/comment'  
import placeorder from '@/components/index/placeorder'  
import placeordercart from '@/components/index/placeordercart'  
import Tabbar from '@/components/tabbar'
import Information from '@/components/information/information'
import myorder from '@/components/information/myorder'
import orderdetail from '@/components/information/orderdetail'
import evaluate from '@/components/information/evaluate'
import logisticsdetails from '@/components/information/logisticsdetails'
import replacement from '@/components/information/replacement'
import replacementlist from '@/components/information/replacementlist'
import replacementdetail from '@/components/information/replacementdetail'
import collect from '@/components/information/collect'
import coupon from '@/components/information/coupon'
import couponhistory from '@/components/information/couponhistory'
import wallet from '@/components/information/wallet'
import bill from '@/components/information/bill'
import recharge from '@/components/information/recharge'
import withdrawcash from '@/components/information/withdrawcash'
import addresslist from '@/components/information/addresslist'
import integral from '@/components/information/integral'
import addaddress from '@/components/information/addaddress'
import commission from '@/components/information/commission'//佣金提现
import mydistributor from '@/components/information/mydistributor'//我的分销商
import myvip from '@/components/information/myvip'//我的会员
import distributedorder from '@/components/information/distributedorder'//分销订单
import rank from '@/components/information/rank'
import logistics from '@/components/information/logistics'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'tabbar',
      component: Tabbar,
      children:[
        {
          path: '/index',
          name: 'index',
          component: Index,
          meta: {
            keepAlive: true, //此组件需要被缓存
          }
        },
        {
          path: '/cart',
          name: 'cart',
          component: Cart
        },
        {
          path: '/information',
          name: 'information',
          component: Information
        }
      ]
    },
    {
      path: '/explosive',
      name: 'explosive',
      component: explosive 
    },
    {
      path: '/mainpush',
      name: 'mainpush',
      component: mainpush 
    },
    {
      path: '/moreclass',
      name: 'moreclass',
      component: moreclass  
    },
    {
      path: '/recommend',
      name: 'recommend',
      component: recommend   
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/searchlist',
      name: 'searchlist',
      component: searchlist  
    },
    {
      path: '/listdetail',
      name: 'listdetail',
      component: listdetail  
    },
    {
      path: '/comment',
      name: 'comment',
      component: comment    
    },
    {
      path: '/placeorder',
      name: 'placeorder',
      component: placeorder    
    },
    {
      path: '/placeordercart',
      name: 'placeordercart',
      component: placeordercart    
    },
    {
      path: '/myorder',
      name: 'myorder',
      component: myorder,
      meta: {
        keepAlive: true, //此组件需要被缓存
      }
    },
    {
      path: '/orderdetail',
      name: 'orderdetail',
      component: orderdetail
    },
    {
      path: '/evaluate',
      name: 'evaluate',
      component: evaluate
    },
    {
      path: '/logisticsdetails',
      name: 'logisticsdetails',
      component: logisticsdetails
    },
    {
      path: '/replacement',
      name: 'replacement',
      component: replacement
    },
    {
      path: '/replacementlist',
      name: 'replacementlist',
      component: replacementlist
    },
    {
      path: '/replacementdetail',
      name: 'replacementdetail',
      component: replacementdetail
    },
    {
      path: '/collect',
      name: 'collect',
      component: collect
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: coupon
    },
    {
      path: '/couponhistory',
      name: 'couponhistory',
      component: couponhistory
    },
    {
      path: '/wallet',
      name: 'wallet',
      component: wallet
    },
    {
      path: '/bill',
      name: 'bill',
      component: bill
    },
    {
      path: '/recharge',
      name: 'recharge',
      component: recharge
    },
    {
      path: '/withdrawcash',
      name: 'withdrawcash',
      component: withdrawcash
    },
    {
      path: '/integral',
      name: 'integral',
      component: integral
    },
    {
      path: '/addresslist',
      name: 'addresslist',
      component: addresslist
    },
    {
      path: '/addaddress',
      name: 'addaddress',
      component: addaddress
    },
    {
      path: '/commission',
      name: 'commission',
      component: commission
    },
    {
      path: '/mydistributor',
      name: 'mydistributor',
      component: mydistributor
    },
    {
      path:'/myvip',
      name:'myvip',
      component:myvip
    },
    {
      path:'/distributedorder',
      name:'distributedorder',
      component:distributedorder
    },
    {
      path:'/rank',
      name:'rank',
      component:rank
    },
    {
      path: '/logistics',
      name: 'logistics',
      component: logistics
    }
  ]
})



