<template>
  <div class="wrapper">
    <div class="toptop">
      <van-row class="top">
        <van-row type="flex" align="center">
          <span class="ev">物流服务</span>
          <van-rate v-model="logistics_server" :size="25" :count="5"  color="#EF3342" void-color="#bbbbbb" />
        </van-row>
        <van-row type="flex" align="center">
          <span class="ev">卖家服务</span>
          <van-rate v-model="seller_server" :size="25" :count="5"  color="#EF3342" void-color="#bbbbbb" />
        </van-row>
      </van-row>
      <van-row class="goodbox" v-for="(item,index) in list" :key="index">
        <van-row class="info" type="flex" align="center">
          <img :src="item.goods_image" alt="">
          <van-col class="name sigle-h">
            <span >{{item.goods_name}}</span>
            <van-row type="flex" align="center">
              商品描述&nbsp;&nbsp;&nbsp;&nbsp;
              <van-rate v-model="item.evaluate" :size="20" :count="5"  color="#EF3342" void-color="#bbbbbb" />
            </van-row>
          </van-col>
        </van-row>
        <van-row class="input">
          <van-field  type="textarea" placeholder="说说你的评价…" rows="5" autosize v-model="item.content"/>
        </van-row>
        <van-row class="upload" @click.native="choose(index)">
          <van-row class="title">上传图片(最多4张)</van-row>
          <van-row type="flex">
            <van-col class="img" v-for="(item1,index1) in item.imglist" :key="index1">
              <img :src="item1" alt="">
              <img src="./../../assets/images/icon_close2.svg" alt="" class="colse" @click="delimg(index1)">
            </van-col>
            <!--上传图片的框子-->
            <van-uploader :after-read="upload" accept="image/*" v-if="item.imglist.length !=4">
              <van-row class="uploadbox">
                <img src="./../../assets/images/icon_tjtp.svg" alt="">
                <span>上传凭证</span>
              </van-row>
            </van-uploader>
          </van-row>
        </van-row>
      </van-row>
    </div>

    <van-row class="bottom">
      <van-button type="danger" round class="btn" @click.native="btn">发布</van-button>
    </van-row>
  </div>
</template>

<script>
  import {photoCompress} from '../../assets/js/tool'
  import {dataURLtoFile} from '../../assets/js/tool'
  import axios from 'axios';
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        logistics_server:0,
        seller_server:0,
        list:"",
        imglistindex:0,//用于记录上传图片点的是第几个商品,用于上传完成图片之后展示图片
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      choose(index){
        this.imglistindex = index;
      },
      upload(file) {
        const that = this;
        let formData = new FormData();
        let quality = Number((1048576/file.file.size).toFixed(1)); //压缩到1m以内  算出压缩比例
        photoCompress(file.file,callback,quality); //将图片进行压缩
        function callback(res){
          formData.append('file', dataURLtoFile(res, file.file.name));//将图片从base64转成文件
          //配置凭证
          const instance = axios.create({
            withCredentials: false
          });
          instance.post('api/my/uploadSxImg', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then(res => {
            if (res.data == '非法图片文件') {
              that.$toast('非法图片文件,请重新上传');
              return
            }
            console.log(that.list[that.imglistindex]);
            that.list[that.imglistindex].imglist.push(res.data.data);
          });
        }
      },
      delimg(index){
        this.list[this.imglistindex].imglist.splice(index, 1);
      },
      btn(){
        let commentInfo = [];
        const that = this;
        that.list.forEach(function (val,index) {
          let obj = {
            user_id:that.userInfo.id,
            goods_id:val.goods_id,
            sun_order_id:val.id,
            logistics_server:that.logistics_server,
            seller_server:that.seller_server,
            goods_intro:val.evaluate,
            content:val.content,
            images:val.imglist.join(','),
          };
          commentInfo.push(obj);
        });
        console.log(commentInfo);
        that.$post('/api/orders/comment_goods',{commentInfo:commentInfo}).then(res=>{
          console.log(res);
          that.$toast(res.msg);
          if(res.code == 1){
            setTimeout(function () {
              that.$store.commit('SENDpayment', 1);
              that.$router.go(-1)
            }, 1000)
          }
        })
      },
    },
    created() {
      document.title='评价订单';
      this.$post('/api/orders/comment_orders',{id:this.$route.query.b_id}).then(res=>{
        console.log(res);
        if(res.code == 1){
          res.data.forEach(function (val,index) {
            val.imglist = [];
            val.evaluate = 0;
            val.content = "";
          });
          this.list = res.data;
        }else{
          this.$toast(res.msg);
        }
      })
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .top >>> .van-icon{
    font-size :0.5rem !important;
  }
  .goodbox >>> .van-icon{
    font-size :0.4rem !important;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    min-height: 100vh;
    background: #F5F5F5;
  }
  .toptop{
    padding-bottom: 1.08rem;
    .top{
      background: #ffffff;
      font-size: 0.28rem;
      color: #333333;
      height: 2.04rem;
      display: flex;
      padding: 0.44rem 0.28rem;
      box-sizing: border-box;
      justify-content: space-between;
      flex-direction: column;
      .ev{
        display: inline-block;
        margin-right: 0.5rem;
      }
    }
    .top:after{
      display: none;
    }
    .goodbox{
      padding: 0.28rem;
      box-sizing: border-box;
      background: #fff;
      margin-top: 0.2rem;
      .info{
        padding-bottom: 0.28rem;
        border-bottom: 1px solid #ebebeb;
        .van-row:after {
          display: none;
        }
        img{
          width: 1.04rem;
          height: 1.04rem;
          border-radius: 0.12rem;
          margin-right: 0.2rem;
        }
        .name{
          height: 1.04rem;
          font-size: 0.28rem;
          color: #333333;
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          span{
            font-size: 0.24rem;
            line-height: 0.4rem;
            color: #666666;
          }
        }
      }
      .input{
        .van-cell{
          padding: 0.24rem 0 0 0;
        }
      }
      .upload{
        margin-top: 0.2rem;
        background: #ffffff;
        .title{
          font-size: 0.28rem;
          color: #333333;
        }
        .img{
          margin-top: 0.28rem;
          margin-right: 0.28rem;
          width: 1.52rem;
          height: 1.52rem;
          border-radius: 0.12rem;
          overflow: hidden;
          position: relative;
          img{
            width: 100%;
            height: 100%;
          }
          .colse{
            width: 0.4rem;
            height: 0.4rem;
            position: absolute;
            top: 0.06rem;
            right:0.06rem;
          }
        }
        .uploadbox{
          margin-top: 0.28rem;
          width: 1.52rem;
          height: 1.52rem;
          background: #F5F5F5;
          border-radius: 0.12rem;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          font-size: 0.24rem;
          color: #BBBBBB;
          img{
            margin-bottom: 0.08rem;
          }
        }
      }
    }
  }
  .bottom{
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    bottom: 0;
    padding: 0.1rem 0.28rem;
    background: #ffffff;
    .btn{
      width: 100%;
      height: 0.88rem;
      font-size: 0.32rem;
    }
    .van-button--danger {
      color: #fff;
      background: #EF3342;
      border: 1px solid #EF3342;
    }
  }

</style>


