<template>
  <div class="wrapper">
    <div class="mid">
      <van-pull-refresh v-model="isLoading" @refresh="getdata(1)">
        <van-list v-model="loading" :finished="finished" :finished-text="list.length?'没有更多了':''" @load="getdata" :offset="20">
          <div class="no" v-if="list.length == 0">
            <img src="./../../assets/images/icon_gwcwsp.svg" alt="">
            <div>暂无数据</div>
          </div>
          <van-row class="box" v-for="(item,index) in list" :key="index">
            <van-row type="flex" justify="space-between">
              <van-col>{{item.title}}</van-col>
              <van-col class="red" v-if="item.type == 0">+{{item.money}}</van-col>
              <van-col class="" v-else>-{{item.money}}</van-col>
            </van-row>
            <van-row class="time">{{item.time}}</van-row>
          </van-row>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        isLoading: false,
        loading: false,
        finished: false,
        list:"",
        page:1,
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/my/withdraw_deposit_lists', {id: this.userInfo.id, page: this.page}).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = "";
              this.list = res.data.data;
            } else {
              this.list = this.list.concat(res.data.data);
            }
            if (res.data.total_row == this.list.length || res.data.data.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    created() {
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
  .mid >>> .van-pull-refresh__track {
    min-height: calc(100vh - 0.2rem);
  }
  .mid >>> .no{
    margin:0 auto 0;
    padding-top 2rem;
    text-align: center;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    background: #F5F5F5;
    min-height: 100vh;
    width: 100%;
    padding-top: 0.2rem;
    box-sizing: border-box;
  }

  .box:after {
    display: none;
  }

  .box {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    font-size: 0.28rem;
    color: #333333;
    width: 6.94rem;
    height: 1.58rem;
    padding: 0.28rem 0.4rem;
    box-sizing: border-box;
    background: #ffffff;
    box-shadow: 0 0.12rem 0.24rem 0 rgba(242, 242, 242, 0.50);
    border-radius: 10px;
    margin: 0 auto 0.2rem;

    .red {
      font-size: 0.36rem;
      color: #EF3342;
    }
    .time {
      font-size: 0.24rem;
      color: #999999;
    }
  }
</style>


