<template>
  <div class="wrapper">
    <van-row class="bg">
      <van-row type="flex" justify="end">
      </van-row>
      <van-row class="balance">
        <img src="./../../assets/images/qbye@2x.png" alt="">
        <van-row>可用余额(元)</van-row>
        <span>{{money}}</span>
      </van-row>
    </van-row>

    <van-row class="btn" type="flex" justify="center" align="center">
      <span>￥</span>
      <input type="number" placeholder="请输入充值金额" class="input" v-model="rechargemoney">
    </van-row>
    <van-button round type="default" size="small" class="btn" @click.native="btn">立即充值</van-button>
  </div>
</template>

<script>
  import {checkempty} from "./../../assets/js/tool"
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        money:"0.00",
        rechargemoney:""
      }
    },
    watch: {
      rechargemoney(val,old){
      }
    },
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      btn(){
        if (!checkempty(this.rechargemoney, '请输入充值金额')) {
          return
        }
        const that = this;
        this.$post('api/pay/recharge',{id:that.userInfo.id,money:that.rechargemoney}).then(res=>{
          console.log(res);
            callpay();
            //调用微信JS api 支付
            function jsApiCall() {
              console.log(JSON.parse(res));
              WeixinJSBridge.invoke("getBrandWCPayRequest", JSON.parse(res), function(res) {
                if (res.err_msg == "get_brand_wcpay_request:ok") {
                  that.rechargemoney = "";
                  that.getdata();
                } else if (res.err_msg == "get_brand_wcpay_request:cancel" || res.err_msg == "get_brand_wcpay_request:fail") {
                  alert("支付失败!");
                }
              });
            }
            function callpay() {
              if (typeof WeixinJSBridge == "undefined") {
                if (document.addEventListener) {
                  document.addEventListener(
                    "WeixinJSBridgeReady",
                    jsApiCall,
                    false
                  );
                } else if (document.attachEvent) {
                  document.attachEvent("WeixinJSBridgeReady", jsApiCall);
                  document.attachEvent("onWeixinJSBridgeReady", jsApiCall);
                }
              } else {
                jsApiCall();
              }
            }
        })
      },
      getdata(){
        this.$fetch('/api/my/personalinfo',{id:this.userInfo.id}).then(res=>{
          if(res.code == 1){
            this.money = res.data.balance;
          }else{
            this.$toast(res.msg)
          }
        })
      }
    },
    created() {
      document.title='充值';
      this.getdata();
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
    overflow: hidden;
  }
  .bg{
    height:7.88rem;
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    padding-top: 1.56rem;
    .balance{
      margin: 0 auto 0;
      position: relative;
      width: 4.74rem;
      height: 4.44rem;
      text-align: center;
      color: #FFFFFF;
      .van-row{
        position: absolute;
        top: -0.14rem;
        left: 0;
        width: 100%;
        font-size: 0.28rem;
      }
      span{
        font-size: 0.68rem;
        margin-top: 1.46rem;
        display: inline-block;
      }
      img{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
  .btn{
    display: flex;
    margin:-0.6rem auto 1rem ;
    width: 6.94rem;
    height: 01.2rem;
    background: #ffffff;
    border-radius: 0.64rem;
    line-height: 1.2rem;
    text-align: center;
    font-size: 0.32rem;
    color: #333333;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    .input{
      height: 100%;
      width: 2.4rem;
      border: none;
    }
  }
  .van-button{
    color: #EF3342;
    text-align: center;
  }
</style>


