<script>
  import pay from './pay'
  import {mapGetters} from 'vuex'

  export default {
    components: {
      pay: pay
    },
    props: {},
    data() {
      return {
        paymoney: 0,//付款的钱
        order_number: "",//付款的订单号
        ordercancellation: "",//取消订单的订单号
        show: false,
        active: 0,
        isLoading: false,
        loading: false,
        finished: false,
        cancelid: "",//取消订单的id
        list: "",
        page: 1,
        keepAlive: true,//
      }
    },
    watch: {
      active(val, old) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        this.page = 1;
        this.getdata(1)
      },
      $route(to, from) {
        if (from.path == "/information") {
          this.keepAlive = true;
        } else {
          this.keepAlive = false;
        }
      }
    },
    computed: {
      ...mapGetters(['payment', 'userInfo'])
    },
    methods: {
      gopay(orders_num, money) {
        this.paymoney = money;
        this.order_number = orders_num;
        this.$refs.pay.showpay();
      },
      /**
       *支付失败
       */
      goback() {

      },
      /**
       *支付成功
       */
      paysuccess() {
        this.getdata(1);
      },
      /**
       * 确认收货
       * @param b_id 大订单号
       */
      confirmreceipt(b_id) {
        // 加载框子
        const toast = this.$toast({
          type:'loading',
          message:'正在提交...',
          mask:true,
        });
        this.$fetch('/api/orders/orders_arrive', {id: b_id}).then(res => {
          toast.clear();
          if (res.code == 1) {
            this.page = 1;
            this.loading = true;
            this.getdata(1)
          }
          this.$toast(res.msg);
        })
      },
      cancelorder(b_id) {
        this.cancelid = b_id;
        this.show = true;
      },
      confirm() {
        // 加载框子
        const toast = this.$toast({
          type:'loading',
          message:'正在提交...',
          mask:true,
        });
        this.$fetch('/api/orders/cancel_orders', {id: this.cancelid}).then(res => {
          toast.clear();
          if (res.code == 1) {
            this.show = false;
            this.page = 1;
            this.loading = true;
            this.getdata(1)
          }
          this.$toast(res.msg);
        })
      },
      getdata(onRefresh) {
        this.loading = true; //手动控制上啦正在加载，防止多次请求
        if (onRefresh) { //判断存在就是下拉刷新
          this.page = 1;
          this.finished = false;
        }
        this.$post('/api/orders/orders_list', {
          id: this.userInfo.id,
          page: this.page,
          status: this.active
        }).then(res => {
          if (res.code == 1) {
            if (this.page == 1) {
              this.list = "";
              this.list = res.data.orders;
            } else {
              this.list = this.list.concat(res.data.orders);
            }
            if (res.data.total_row == this.list.length || res.data.orders.length == 0) {
              this.finished = true;
            }
            this.page++;
          } else {
            this.finished = true;
            this.$toast(res.msg);
          }
          this.isLoading = false;
          this.loading = false;
        })
      },
    },
    //被缓存时任执行
    activated() {
      if (this.payment) {
        this.getdata(1);
        this.$store.commit('SENDpayment', "");
      }
      if (!this.keepAlive) {
        return
      }
      //果然从上个页面过来 参数与当前选中索引一样则手动刷新列表
      if(this.active == Number(this.$route.query.index)){
        this.getdata(1);
      }else{
        this.active = Number(this.$route.query.index);
      }
    },
    beforeRouteLeave(to, from, next) {
      // if (to.path == "/orderdetail" || to.path == '/logistics') {
      //   this.keepAlive = false;
      // } else {
      //   this.keepAlive = true;
      // }
      //在路由监听里面控制 是否缓存
      next();
    },
    created() {

    },
    mounted() {
      document.title = '我的订单'
    }
  }
</script>
<style scoped lang='stylus'>
  .top >>> .van-tabs--line .van-tabs__wrap {
    height 0.8rem;
  }

  .top >>> .van-tab {
    background: #EF3342;
    font-size: 0.28rem;
    color: #F698A0;
    line-height: 0.8rem;
  }

  .top >>> .van-tab--active {
    font-size: 0.28rem;
    color: #FFFFFF;
  }

  .top >>> .van-tab__pane {
    height: calc(100vh - 0.8rem);
    overflow: scroll;
  }

  .top >>> .van-tabs--line {
    padding-top: 0.8rem;
  }

  .mid >>> .van-pull-refresh__track {
    min-height: calc(100vh - 1.36rem);
  }

  .mid >>> .no {
    margin: 0 auto 0;
    padding-top: 2rem;
    text-align: center;
  }
</style>
<style scoped lang='less'>
  .wrapper {
    background: #F5F5F5;
    width: 100%;
  }

  .mid {
    padding: 0.28rem 0.28rem 0;
    box-sizing: border-box;
    min-height: calc(100vh - 0.8rem);

    .box {
      padding: 0 0.24rem 0.28rem;
      box-sizing: border-box;
      background: #FFFFFF;
      margin-bottom: 0.2rem;
      box-shadow: 0 0.12rem 0.24rem 0 rgba(242, 242, 242, 0.50);
      border-radius: 0.2rem;
      .typelist {
        height: 0.8rem;
        line-height: 0.8rem;
        font-size: 0.28rem;
        color: #999999;
        .type {
          color: #5045D8;
        }
      }
      .goodlist {
        margin-bottom: 0.28rem;
        img {
          width: 1.8rem !important;
          height: 1.8rem !important;
          border-radius: 0.12rem;
        }
        .goodinfo {
          flex: 1;
          width: 100%;
          margin-left: 0.24rem;
          font-size: 0.24rem;
          color: #999999;
          line-height: 0.34rem;
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          .name {
            color: #333333;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
          }
        }
        .goodinfo:after {
          display: none;
        }
      }
      .subtotal {
        font-size: 0.24rem;
        color: #333333;
        text-align: right;
        margin-bottom: 0.28rem;
        span {
          color: #EF3342;
          font-size: 0.32rem;
        }
      }
      .btn {
        text-align: right;
        .default {
          width: 1.68rem;
          color: #666666;
          height: 0.6rem;
          font-size: 0.28rem;
        }
        .purple {
          width: 1.68rem;
          background: #5045D8;
          height: 0.6rem;
          color: #FFFFFF;
          font-size: 0.28rem;
        }
      }
    }
  }

  /*取消订单*/
  .van-popup {
    border-radius: 0.4rem;
  }

  .popup {
    width: 5.18rem;
    height: 2.56rem;
    .content {
      height: 1.6rem;
      text-align: center;
      font-size: 0.28rem;
      color: #999999;
    }
    .cancel {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #333333;
      border-right: 1px solid #ebebeb;
    }
    .confirm {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #EF3342;
    }
  }


</style>


