<template>
  <div class="wrapper">
    <van-row class="bg">
      <van-row type="flex" justify="end">
        <router-link to="/bill">
          <van-col class="detail">账单明细</van-col>
        </router-link>
      </van-row>
      <van-row class="balance">
        <img src="./../../assets/images/qbye@2x.png" alt="">
        <van-row>可用余额(元)</van-row>
        <span>{{money}}</span>
      </van-row>
    </van-row>
    <router-link to="/recharge">
    <van-button round type="default" size="small" class="btn">充值</van-button>
    </router-link>
    <router-link to="/withdrawcash">
      <van-button round type="default" size="small" class="btn">提现</van-button>
    </router-link>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    components: {},
    props: {},
    data() {
      return {
        money:"0.00"
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {},
    created() {
      document.title='我的钱包';
      this.$fetch('/api/my/personalinfo',{id:this.userInfo.id}).then(res=>{
        if(res.code == 1){
          this.money = res.data.balance;
        }else{
          this.$toast(res.msg)
        }
      })
    },
    mounted() {
    }
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    background: #F5F5F5;
    min-height: 100vh;
    overflow: hidden;
  }
  .bg{
    height:7.88rem;
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
    .detail{
      width: 1.2rem;
      height: 0.48rem;
      line-height: 0.48rem;
      text-align: center;
      border: 1px solid #FFFFFF;
      border-radius: 0.04rem;
      font-size: 0.24rem;
      color: #FFFFFF;
      margin-top: 0.4rem;
      margin-right: 0.4rem;
    }
    .balance{
      margin: 0.68rem auto 0;
      position: relative;
      width: 4.74rem;
      height: 4.44rem;
      text-align: center;
      color: #FFFFFF;
      .van-row{
        position: absolute;
        top: -0.14rem;
        left: 0;
        width: 100%;
        font-size: 0.28rem;
      }
      span{
        font-size: 0.68rem;
        margin-top: 1.46rem;
        display: inline-block;
      }
      img{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
      }
    }

  }
  .btn{
    display: block;
    margin:-0.6rem auto 1rem ;
    width: 6.94rem;
    height: 01.2rem;
    background: #ffffff;
    border-radius: 0.64rem;
    line-height: 1.2rem;
    text-align: center;
    font-size: 0.32rem;
    color: #333333;
  }
</style>


