<template>
  <div class="wrapper" v-if="list">
    <van-row class="top" align="center">
      <!--待付款-->
      <van-row v-if="list.status == 0">付款剩余时间{{counttime}}</van-row>
      <!--待发货-->
      <van-row v-if="list.status == 1">待发货</van-row>
      <!--待收货-->
      <van-row type="flex" justify="space-between" v-if="list.status == 2">
        <van-col>待收货</van-col>
        <van-row align="center" justify="center" type="flex">查看物流 <img
          src="./../../assets/images/icon_baiyoujiantou.svg" alt=""></van-row>
      </van-row>
      <!--待评价-->
      <van-row v-if="list.status == 3">待评价</van-row>
      <!--交易成功-->
      <van-row v-if="list.status == 5">交易成功</van-row>
      <!--交易失败-->
      <van-row v-if="list.status == 7">交易失败</van-row>
    </van-row>
    <van-row class="address" type="flex">
      <img src="./../../assets/images/icon_shouhuodizhi.svg" alt="">
      <van-row>
        <div>{{list.name+' '+list.telephone}}</div>
        <span>{{list.address}}</span>
      </van-row>
    </van-row>
    <!--商品行-->
    <div class="goodlistbox">
      <div v-for="(item,index) in list.orders_detail" :key="index">
        <van-row class="goodlist" type="flex">
          <img :src="item.goods_image" alt="">
          <van-row class="goodinfo">
            <van-row class="name more-h">{{item.goods_name}}</van-row>
            <van-row class="sigle-h">{{item.goods_specification}}</van-row>
            <van-row type="flex" justify="space-between">
              <van-col><span>￥{{item.goods_price}}</span></van-col>
              <van-col>x{{item.goods_count}}</van-col>
            </van-row>
          </van-row>
        </van-row>
        <van-row class="btn" v-if="list.status != 0 && list.status != 3 && list.status != 5 && list.status != 7">
          <van-button round type="default" size="small" class="default"
                      v-if="item.status1 != 2 && item.status1 != 3 && item.status != 4"
                      @click.native="replacement(list.status,item.s_id)">申请退款
          </van-button>
          <van-button round type="default" size="small" class="default" v-if="item.status1 ==2">退款中</van-button>
          <van-button round type="default" size="small" class="default" v-if="item.status1 ==3">退款成功</van-button>
        </van-row>
      </div>
    </div>
    <van-cell-group class="list">
      <van-cell title="优惠" :value="'-¥'+list.coupon"/>
      <van-cell title="运费" :value="'¥'+list.freight_price"/>
      <van-cell title="买家留言" :value="list.remark"/>
      <van-row class="subtotal">共{{list.orders_count}}件商品
        &nbsp;&nbsp;&nbsp;&nbsp;合计：<span>¥{{list.orders_total_price}}</span></van-row>
    </van-cell-group>
    <van-row class="orderbox">
      <van-col>订单编号：{{list.orders_num}}</van-col>
      <van-col>下单时间：{{list.create_time}}</van-col>
    </van-row>
    <van-row class="btn bottom" v-if="list.status != 1 && counttime != '00时00分00秒'">
      <van-button round type="default" size="small" class="default" v-if="list.status == 5||list.status == 7"
                  @click.native="delorder(list.b_id)">删除订单
      </van-button>
      <van-button round type="default" size="small" class="default" v-if="list.status == 0"
                  @click.native="cancelorder(list.b_id)">取消订单
      </van-button>
      <van-button round type="default" size="small" class="default" v-if="list.status == 2"
                  @click.native="$router.push({path:'/logistics',query:{}})">查看物流
      </van-button>
      <van-button round type="default" size="small" class="purple" v-if="list.status == 0" @click.native="gopay">去付款
      </van-button>
      <van-button round type="default" size="small" class="purple" v-if="list.status == 2"
                  @click.native="confirmreceipt(list.b_id)">确认收货
      </van-button>
      <van-button round type="default" size="small" class="purple" v-if="list.status == 3"
                  @click.native.stop="$router.push({path:'/evaluate',query:{b_id:list.b_id}})">评价
      </van-button>
    </van-row>
    <pay :order_number="order_number" :paymoney="paymoney" ref="pay" @goback="goback" @paysuccess="paysuccess"></pay>
    <!--取消订单-->
    <van-popup v-model="show" :close-on-click-overlay=false>
      <van-row class="popup">
        <van-row class="content" type="flex" justify="center" align="center">确定要取消订单吗？</van-row>
        <van-row>
          <van-col class="cancel" @click.native="show=false">取消</van-col>
          <van-col class="confirm" @click.native="confirm">确定</van-col>
        </van-row>
      </van-row>
    </van-popup>
  </div>
</template>

<script>
  import pay from './pay'
  import {mapGetters} from 'vuex'
  export default {
    components: {
      pay: pay
    },
    props: {},
    data() {
      return {
        counttime: "00时29分59秒",
        show: false,
        paymoney: "0.00",//付款的钱
        order_number: "",//付款的订单号
        list: "",
        cancelid: "",
      }
    },
    watch: {},
    computed: {
      ...mapGetters(['userInfo'])
    },
    methods: {
      /**
       *删除订单
       */
      delorder(b_id) {
        this.$post('/api/orders/del_order',{id:b_id}).then(res=>{
          console.log(res);
          this.$toast(res.msg);
          if(res.code == 1){
            this.$store.commit('SENDpayment', 1);
            const that = this;
            setTimeout(function () {
              that.$router.go(-1);
            },1000)
          }
        })
      },
      /**
       * 确认收货
       * @param b_id 大订单号
       */
      confirmreceipt(b_id) {
        // 加载框子
        const toast = this.$toast({
          type:'loading',
          message:'正在提交...',
          mask:true,
        });
        this.$fetch('/api/orders/orders_arrive', {id: b_id}).then(res => {
          toast.clear();
          if (res.code == 1) {
            this.$store.commit('SENDpayment', 1);
            this.$router.go(-1);
          }
          this.$toast(res.msg);
        })
      },
      /**
       * 取消订单
       */
      cancelorder(b_id) {
        this.cancelid = b_id;
        this.show = true;
      },
      confirm() {
        // 加载框子
        const toast = this.$toast({
          type:'loading',
          message:'正在提交...',
          mask:true,
        });
        this.$fetch('/api/orders/cancel_orders', {id: this.cancelid}).then(res => {
          toast.clear();
          if (res.code == 1) {
            this.$store.commit('SENDpayment', 1);
            this.$router.go(-1);
          }
          this.$toast(res.msg);
        })
      },
      /**
       *去付款
       */
      gopay() {
        this.paymoney = this.list.orders_total_price;
        this.order_number = this.list.orders_num;
        this.$refs.pay.showpay();
      },
      /**
       *支付失败
       */
      goback() {

      },
      /**
       *支付成功 刷新页面并且设置  SENDpayment 为1 刷新我的订单列表
       *
       */
      paysuccess() {
        this.$store.commit('SENDpayment', 1);
        this.getdata();
      },
      /**
       * 退货退款
       * @param status 订单状态
       * @param s_id 小订单id
       */
      replacement(status, s_id) {
        this.$router.push({path: '/replacement', query: {status, s_id}})
      },
      /**
       * 待付款倒计时
       */
      countdownfunciton(orderCreateTime) {
        const that = this;
        this.countdown = setInterval(function () {
          let time = (new Date(orderCreateTime).getTime() + 60*30 * 1000) - new Date().getTime();// 30分钟时间付款
          if (time < 0) {
            that.counttime = '00时00分00秒';
            clearInterval(that.countdown);
            that.$store.commit('SENDpayment', 1);
          } else {
            let leftHours = parseInt(time / 1000 / 60 / 60, 10) < 10 ? '0' + parseInt(time / 1000 / 60 / 60, 10) : parseInt(time / 1000 / 60 / 60, 10);
            let leftMinutes = parseInt(time / 1000 / 60 % 60, 10) < 10 ? '0' + parseInt(time / 1000 / 60 % 60, 10) : parseInt(time / 1000 / 60 % 60, 10);
            let leftSeconds = parseInt(time / 1000 % 60, 10) < 10 ? '0' + parseInt(time / 1000 % 60, 10) : parseInt(time / 1000 % 60, 10);
            that.counttime = leftHours + '时' + leftMinutes + '分' + leftSeconds + '秒';
          }
        }, 1000)
      },
      getdata(){
        const that = this;
        this.$post('/api/orders/orders_detail', {id: this.$route.query.b_id}).then(res => {
          if (res.code == 1) {
            this.list = res.data;
            if (res.data.status == 0) {
              this.countdownfunciton(res.data.create_time)
            }
          } else {
            this.$toast(res.msg);
            setTimeout(function () {
              that.$router.go(-1)
            }, 1000)
          }
        })
      }
    },
    created() {
    },
    mounted() {
      document.title = '订单详情';
      this.getdata();
    },
    beforeRouteLeave(to, from, next) {
      clearInterval(this.countdown);
      next();
    },
  }
</script>
<style scoped lang='stylus'>
</style>
<style scoped lang='less'>
  .wrapper {
    width: 100%;
    min-height: 100vh;
    background: #F5F5F5;
  }

  .top {
    height: 1.28rem;
    padding: 0.28rem;
    line-height: 0.72rem;
    box-sizing: border-box;
    font-size: 0.32rem;
    color: #FFFFFF;
    background-image: linear-gradient(0deg, #FE6664 0%, #EF3342 100%);
  }

  .address {
    height: 1.5rem;
    padding: 0.28rem;
    box-sizing: border-box;
    background: #ffffff;
    .van-row {
      margin-left: 0.28rem;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      font-size: 0.32rem;
      color: #333333;
      span {
        font-size: 0.24rem;
        color: #999999;
      }
    }
    .van-row:after {
      display: none;
    }
  }

  .goodlistbox {
    background: #ffffff;
    padding: 0.28rem;
    box-sizing: border-box;
    margin-top: 0.2rem;
    .goodlist {
      margin-bottom: 0.28rem;
      img {
        width: 1.8rem;
        height: 1.8rem;
        border-radius: 0.12rem;
      }
      .goodinfo {
        flex: 1;
        width: 100%;
        margin-left: 0.24rem;
        font-size: 0.24rem;
        color: #999999;
        line-height: 0.34rem;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .name {
          color: #333333;
        }
        span {
          font-size: 0.32rem;
          color: #EF3342;
        }
      }
      .goodinfo:after {
        display: none;
      }
    }
  }

  .list {
    .van-cell {
      font-size: 0.28rem;
      padding: 0.2rem 0.3rem;
      box-sizing: border-box;
      line-height: 0.56rem;
      color: #333333;
    }
    .van-cell__title {
      flex: 1;
    }
    .van-cell__value {
      flex: 4;
    }
    .subtotal {
      height: 0.96rem;
      font-size: 0.24rem;
      padding: 0 0.28rem;
      color: #333333;
      text-align: right;
      line-height: 0.96rem;
      margin-bottom: 0.28rem;
      span {
        color: #EF3342;
        font-size: 0.32rem;
      }
    }
  }

  .orderbox {
    height: 1.28rem;
    padding: 0.2rem 0.28rem;
    box-sizing: border-box;
    font-size: 0.24rem;
    color: #999999;
    line-height: 0.34rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #ffffff;
  }

  .orderbox:after {
    display: none;
  }

  .bottom {
    margin-top: 0.2rem;
    padding: 0.2rem 0;
  }

  .btn {
    text-align: right;
    background: #ffffff;
    .default {
      width: 1.68rem;
      color: #666666;
      height: 0.6rem;
      font-size: 0.28rem;
      margin-right: 0.2rem;
    }
    .purple {
      margin-right: 0.2rem;
      width: 1.68rem;
      background: #5045D8;
      height: 0.6rem;
      color: #FFFFFF;
      font-size: 0.28rem;
    }
  }

  /*取消订单*/
  .van-popup {
    border-radius: 0.4rem;
  }

  .popup {
    width: 5.18rem;
    height: 2.56rem;
    .content {
      height: 1.6rem;
      text-align: center;
      font-size: 0.28rem;
      color: #999999;
    }
    .cancel {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #333333;
      border-right: 1px solid #ebebeb;
    }
    .confirm {
      height: 0.96rem;
      text-align: center;
      line-height: 0.96rem;
      width: 50%;
      background: #F5F5F5;
      font-size: 0.32rem;
      color: #EF3342;
    }
  }
</style>


